import styles from './styles.module.scss';
import SponsorshipEndedIcon from '../../../assets/images/sponsorsTextImages/Sponsorship-ended-icon.png';
// logos
import NikeLogo from '../../../assets/images/sponsorsTextImages/partners/nike.png';
import AdobeLogo from '../../../assets/images/sponsorsTextImages/partners/adobe.png';
import AlibabaLogo from '../../../assets/images/sponsorsTextImages/partners/alibaba.png';
import BombardierLogo from '../../../assets/images/sponsorsTextImages/partners/bombardier.png';
import BossLogo from '../../../assets/images/sponsorsTextImages/partners/boss.png';
import IBMLogo from '../../../assets/images/sponsorsTextImages/partners/ibm.png';
import MedtronicLogo from '../../../assets/images/sponsorsTextImages/partners/medtronic.png';
import NewbalanceLogo from '../../../assets/images/sponsorsTextImages/partners/newbalance.png';
import NokiaLogo from '../../../assets/images/sponsorsTextImages/partners/nokia.png';
import PrudentialLogo from '../../../assets/images/sponsorsTextImages/partners/prudential.png';
import SmileLogo from '../../../assets/images/sponsorsTextImages/partners/smile.png';
import SonyLogo from '../../../assets/images/sponsorsTextImages/partners/sony.png';

const partners = [
  { title: 'Nike', imagepath: NikeLogo },
  { title: 'Adobe', imagepath: AdobeLogo },
  { title: 'Sony', imagepath: SonyLogo },
  { title: 'NewBalance', imagepath: NewbalanceLogo },
  { title: 'Prudential', imagepath: PrudentialLogo },
  { title: 'Medtronic', imagepath: MedtronicLogo },
  { title: 'Nokia', imagepath: NokiaLogo },
  { title: 'Alibaba', imagepath: AlibabaLogo },
  { title: 'Bombardier', imagepath: BombardierLogo },
  { title: 'IBM', imagepath: IBMLogo },
  { title: 'Boss', imagepath: BossLogo },
  { title: 'Smile', imagepath: SmileLogo },
];

const BecomeSponsorEnded = () => {
  return (
    <div className={styles.bs_ended}>
      <div className={styles.bs_ended__wrap}>
        <div className={styles.bs_ended__header}>
          <div className={styles.bs_ended__header__text}>
            <h1 className={styles.bs_ended__header__text__title}>
              Our Pilot Sponsorship
              <br /> Program has now ended
            </h1>
            <p className={styles.bs_ended__header__text__description}>
              We look forward to working closely with all those who have shown
              their commitment to our vision.
            </p>
          </div>
          <div className={styles.bs_ended__header__image}>
            <img
              src={SponsorshipEndedIcon}
              alt="sponsorship program ended icon"
            />
          </div>
        </div>

        {/* our partners */}
        <div className={styles.bs_ended__partners}>
          <h2 className={styles.bs_ended__partners__title}>
            We thank all our world-class partners!
          </h2>
          <div className={styles.bs_ended__partners__brands}>
            {partners &&
              partners.map((item) => {
                return (
                  <div className={styles.bs_ended__partners__brands__item}>
                    <img src={item.imagepath} alt={item.title} />
                  </div>
                );
              })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default BecomeSponsorEnded;
