const selectActions = {
  REMOVE_ACCESS: 'REMOVE_ACCESS',
};

const options = [
  { value: selectActions.REMOVE_ACCESS, label: 'Remove access' },
];

const DropdownIndicator = (props) => {
  return (
    <components.DropdownIndicator {...props}>
      <img src={DropDownIcon} alt="dropdown icon" />
    </components.DropdownIndicator>
  );
};

const customStyles = {
  control: (base) => ({
    ...base,
    border: '1px solid #fff',
    backgroundColor: '#6098f5',
    fontFamily: 'Roboto',
    fontWeight: 400,
    fontSize: 14,
  }),
  placeholder: (defaultStyles) => {
    return {
      ...defaultStyles,
      color: '#fff',
    };
  },
  menu: (provided, state) => ({
    ...provided,
    width: '130%',
    backgroundColor: '#fff',
    color: '#707070',
    border: 'none',
    borderRadius: '1px',
  }),

  singleValue: (provided, state) => {
    const color = '#fff';

    return { ...provided, color };
  },
  option: (provided, state) => ({
    ...provided,
    fontFamily: 'Roboto',
    fontWeight: 400,
    fontSize: 14,
  }),
  indicatorSeparator: (styles) => ({ display: 'none' }),
};

const Members = ({ members = membersListFromDb }) => {
  const [isActiveSelectAllCheckbox, setIsActiveSelectAllCheckbox] =
    useState(false);
  const [membersList, setMembersList] = useState(
    members.map((el) => ({ ...el, checked: false }))
  );
  const [checkedMembersCount, setCheckedMembersCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const { params } = useParams();

  useEffect(() => {
    const page = (params && params.page) || 1;
    if (page !== 1 && page) {
      setCurrentPage(page);
      return;
    }
    setCurrentPage(page);
  }, [params]);

  const onSelectAllMembers = (e) => {
    const checkedStatus = e.target.checked;

    const newMembersList = members.map((el) => ({
      ...el,
      checked: checkedStatus,
    }));

    const newCheckedMembers = checkedStatus
      ? newMembersList.filter((el) => el.checked).length
      : 0;

    setIsActiveSelectAllCheckbox(checkedStatus);
    setMembersList(newMembersList);
    setCheckedMembersCount(newCheckedMembers);
  };

  const onSingleUserCheckox = (userId, checkStatus) => {
    const newMembersList = membersList.map((el) =>
      el.id === userId ? { ...el, checked: checkStatus } : { ...el }
    );
    const newCheckedMembers =
      newMembersList.filter((el) => el.checked === true).length || 0;

    setMembersList(newMembersList);
    setCheckedMembersCount(newCheckedMembers);
  };

  const cancelSelectHandler = () => {
    const newMembersList = membersList.map((el) => ({ ...el, checked: false }));
    setMembersList(newMembersList);
    setIsActiveSelectAllCheckbox(false);
    setCheckedMembersCount(0);
  };

  const onPaginationPage = (page) => {
    setCurrentPage(page);
  };

  return (
    <div className={styles.members}>
      <div className={styles.members__wrap}>
        <div className={styles.members__title}>Members</div>
      </div>
      <div className={styles.members__list}>
        <div className={styles.members__list__header}>
          <div className={styles.members__list__header__checkbox}>
            <div className={styles.members__list__header__checkbox__wrap}>
              <Checkbox
                name="selectAllMembers"
                checked={isActiveSelectAllCheckbox}
                onChangeHandler={onSelectAllMembers}
                backgroundColor="green"
              />
            </div>
          </div>
          {checkedMembersCount > 0 && (
            <div className={styles.members__list__header__selectedCount}>
              {checkedMembersCount} selected
            </div>
          )}
          <div className={styles.members__list__header__selectBar}>
            <Select
              isSearchable={false}
              placeholder="Actions"
              styles={customStyles}
              options={options}
              components={{ DropdownIndicator }}
            />
            {checkedMembersCount > 0 && (
              <>
                <span
                  className={`${styles.members__list__header__selectBar__borderLine} ${styles.left}`}
                ></span>
              </>
            )}
          </div>
          {checkedMembersCount > 0 && (
            <div
              className={styles.members__list__header__close}
              onClick={cancelSelectHandler}
            >
              <img src={CloseIcon} alt="close icon" />
              <span
                className={`${styles.members__list__header__selectBar__borderLine} ${styles.left}`}
              ></span>
            </div>
          )}
        </div>
        <div className={styles.members__list__body}>
          <ul className={styles.members__list__body__wrap}>
            {membersList
              ? membersList.map((member) => {
                  return (
                    <SingleMember
                      key={member.id + '__member'}
                      id={member.id}
                      firstName={member.firstName}
                      lastName={member.lastName}
                      department={member.department}
                      lastActivity={member.lastActivity}
                      registrationDate={member.registrationDate}
                      checked={member.checked}
                      checkBoxHandler={onSingleUserCheckox}
                    />
                  );
                })
              : 'members not found'}
          </ul>
        </div>
      </div>
      <div className={styles.members__pagination}>
        <div className={styles.members__pagination__wrap}>
          <div className={styles.members__pagination__controllers}>
            <div className={styles.members__pagination__controllers__button}>
              <button
                className={`${styles.members__pagination__controllers__button__btn} ${styles.previous}`}
                disabled
              >
                <img src={PaginationArrow} alt="pagination arrow" />
              </button>
            </div>
            <div className={styles.members__pagination__controllers__pages}>
              <Link
                className={`${
                  styles.members__pagination__controllers__pages__item
                } ${currentPage === 1 && styles.active}`}
                to={`/my-account/details/members/1`}
                onClick={() => onPaginationPage(1)}
              >
                1
              </Link>
              <Link
                className={`${
                  styles.members__pagination__controllers__pages__item
                } ${currentPage === 2 && styles.active}`}
                to={`/my-account/details/members/2`}
                onClick={() => onPaginationPage(2)}
              >
                2
              </Link>
              <Link
                className={`${
                  styles.members__pagination__controllers__pages__item
                } ${currentPage === 3 && styles.active}`}
                to={`/my-account/details/members/3`}
                onClick={() => onPaginationPage(3)}
              >
                3
              </Link>
              <Link
                className={`${
                  styles.members__pagination__controllers__pages__item
                } ${currentPage === 4 && styles.active}`}
                to={`/my-account/details/members/4`}
                onClick={() => onPaginationPage(4)}
              >
                4
              </Link>
              <Link
                className={`${
                  styles.members__pagination__controllers__pages__item
                } ${currentPage === 5 && styles.active}`}
                to={`/my-account/details/members/5`}
                onClick={() => onPaginationPage(5)}
              >
                5
              </Link>
              <Link
                className={`${
                  styles.members__pagination__controllers__pages__item
                } ${currentPage === 6 && styles.active}`}
                to={`/my-account/details/members/6`}
                onClick={() => onPaginationPage(6)}
              >
                6
              </Link>
              ...
              <Link
                className={`${
                  styles.members__pagination__controllers__pages__item
                } ${currentPage === 125 && styles.active}`}
                to={`/my-account/details/members/125`}
                onClick={() => onPaginationPage(125)}
              >
                125
              </Link>
            </div>
            <div className={styles.members__pagination__controllers__button}>
              <button
                className={`${styles.members__pagination__controllers__button__btn} ${styles.next}`}
              >
                <img src={PaginationArrow} alt="pagination arrow" />
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Members;
