import styles from './styles.module.scss';
import Button from '../../../components/atoms/Button';
import PersonalDetails from './PersonalDetails';
import ContactDetails from './ContactDetails';
import Members from './Members';
import BrandLogo from '../../../assets/images/supported2.png';
import SendInviteIcon from '../../../assets/images/ui-icons/send_invite_icon.png';
import SidebarMenu from './SidebarMenu';
import myDetailsImg from '../../../assets/images/MyAccount/my_details.png';
import sponsorshipTeamImg from '../../../assets/images/MyAccount/sponsorship_team.png';
import { Switch, Route, useRouteMatch, Redirect } from 'react-router-dom';
import PendingInvites from './PendingInvites';
import { connect } from 'react-redux';

const menuData = [
  {
    label: 'MY DETAILS',
    image: myDetailsImg,
    links: [
      { title: 'Personal info', url: 'personal-info' },
      { title: 'Contact details', url: 'contact-details' },
    ],
  },
  {
    label: 'sponsorship team',
    image: sponsorshipTeamImg,
    links: [
      { title: 'Members', url: 'members' },
      { title: 'Manage pending invites', url: 'manage-invites' },
    ],
  },
];

const AccountSettings = ({
  first_name,
  last_name,
  userInitials,
  email,
  brandLogo = BrandLogo,
  brandName = 'Volkswagen',
}) => {
  const { path } = useRouteMatch();
  return (
    <div className={styles.accountSettings}>
      <aside className={styles.accountSettings__sidebar}>
        <div className={styles.accountSettings__sidebar__userInfo}>
          <div className={styles.accountSettings__sidebar__userInfo__image}>
            {'' ? (
              <img src="/userImg" alt="userName here" />
            ) : (
              <div
                className={
                  styles.accountSettings__sidebar__userInfo__image__nameCircle
                }
              >
                {userInitials}
              </div>
            )}
          </div>
          <div className={styles.accountSettings__sidebar__userInfo__userData}>
            <p
              className={
                styles.accountSettings__sidebar__userInfo__userData__name
              }
            >
              {first_name}
            </p>
            <p
              className={
                styles.accountSettings__sidebar__userInfo__userData__surname
              }
            >
              {last_name}
            </p>
            <p
              className={
                styles.accountSettings__sidebar__userInfo__userData__email
              }
            >
              {email}
            </p>
          </div>
        </div>
        {/* brand logo & name */}
        <div className={styles.accountSettings__sidebar__brandInfo}>
          <div
            className={styles.accountSettings__sidebar__brandInfo__brandLogo}
          >
            <img src={brandLogo} alt={brandName} />
          </div>
          <div
            className={styles.accountSettings__sidebar__brandInfo__brandName}
          >
            {brandName}
          </div>
        </div>
        {/* Menu with Links */}
        <div className={styles.accountSettings__sidebar__menu}>
          <SidebarMenu menuData={menuData} />
        </div>
        {/* send invite button */}
        <div className={styles.accountSettings__sendInvite}>
          <div className={styles.accountSettings__sendInvite__wrap}>
            <Button
              text="Invite colleagues by Email"
              addClass="blueBG"
              image={SendInviteIcon}
              revert
            />
          </div>
        </div>
      </aside>
      <div className={styles.accountSettings__personalInfo}>
        <Switch>
          <Route exact path={`${path}`}>
            <Redirect to={`${path}/personal-info`} />
          </Route>
          <Route path={`${path}/personal-info`}>
            <PersonalDetails />
          </Route>
          <Route path={`${path}/contact-details`}>
            <ContactDetails />
          </Route>
          <Route path={`${path}/members`} exact>
            <Members />
          </Route>
          <Route exact path={`${path}/members/1`}>
            <Redirect to={`${path}/members`} />
          </Route>
          <Route path={`${path}/members/:page`}>
            <Members />
          </Route>
          <Route path={`${path}/manage-invites`}>
            <PendingInvites />
          </Route>
        </Switch>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    first_name: state.brand_id.first_name,
    last_name: state.brand_id.last_name,
    email: state.brand_id.email,
    userInitials: state.brand_id.userInitials,
    imagespath: state.brand_id.imagespath,
  };
};

export default connect(mapStateToProps, null)(AccountSettings);
