import React, { useEffect } from 'react';
import styles from './styles.module.scss';
import { connect } from 'react-redux';
import { getCategoriesData } from '../../redux/thunk/categoriesGrid';
import {
  setAtiveCommunity,
  resetActiveCommunity,
} from '../../redux/actions/gridPageActions';
import WrapLayout from '../Layouts/WrapLayout';
import Categories from '../Categories';
import CommunityWithRegions from '../CommunityWithRegions';
import SponsorsText from '../molecules/SponsorsText';
import Backdrop from '../molecules/Backdrop';
// b&w and colored category img

const Communities = ({
  communities,
  activeCommunity,
  isActiveCommunityOpen,
  currency_symbol,
  preferred_currency,
  loader,
  error,
  dispatchGetCategoriesData,
  dispatchSetAtiveCommunity,
  dispatchResetActiveCommunity,
}) => {
  useEffect(() => {
    dispatchGetCategoriesData();
  }, [dispatchGetCategoriesData]);

  const onCommunityClick = (categoryId) => {
    const currentCategoryData = communities?.find(
      (el) => el.community_id === categoryId
    );

    if (currentCategoryData) {
      dispatchSetAtiveCommunity({
        ...currentCategoryData,
        currency_symbol,
        preferred_currency,
      });
    }
  };

  const hidePayment = () => {
    dispatchResetActiveCommunity();
  };

  return (
    <div className={styles.communities}>
      <div className={styles.communities__wrap}>
        <Categories
          categories={[...communities].sort(
            (a, b) => Number(a.community_id) - Number(b.community_id)
          )}
          onClick={onCommunityClick}
        />

        {isActiveCommunityOpen && (
          <>
            <Backdrop show={isActiveCommunityOpen} clickhandler={hidePayment} />
            <CommunityWithRegions
              community_id={activeCommunity.community_id}
              community_title={activeCommunity.community_title}
              community_colored_icon={
                activeCommunity.large_community_icon_full_color
              }
              currency_symbol={activeCommunity.currency_symbol}
              preferred_currency={activeCommunity.preferred_currency}
              sponsorLogo={activeCommunity.sponsorLogo}
              categoryImage={activeCommunity.categoryImage}
              regions={activeCommunity.regions}
              hidePaymentHandler={hidePayment}
            />
          </>
        )}
      </div>
      {communities?.length <= 4 && (
        <WrapLayout>
          <SponsorsText />
        </WrapLayout>
      )}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    communities: state.categorygrid.communities,
    activeCommunity: state.categorygrid.activeCommunity,
    isActiveCommunityOpen: state.categorygrid.activeCommunity.isOpen,
    currency_symbol: state.categorygrid.currency_symbol,
    preferred_currency: state.categorygrid.preferred_currency,
    loader: state.categorygrid.loader,
    error: state.categorygrid.error,
  };
};

const mapDispatchToProps = {
  dispatchGetCategoriesData: getCategoriesData,
  dispatchSetAtiveCommunity: setAtiveCommunity,
  dispatchResetActiveCommunity: resetActiveCommunity,
};

export default connect(mapStateToProps, mapDispatchToProps)(Communities);
