import actionTypes from '../constants';

export const toggleSubmenu = (payload) => {
  return {
    type: actionTypes.TOGGLE_SUBMENU,
    payload,
  };
};

export const payForOfferedProducts = () => {
  return {
    type: actionTypes.PAY_FOR_OFFERED_PRODUCTS,
  };
};

export const setInvoiceReceive = (payload) => {
  return {
    type: actionTypes.SET_INVOICE_RECEIVE,
    payload,
  };
};

export const onCheckCategoryInCheckout = (payload) => {
  return {
    type: actionTypes.ON_CHECK_CATEGORY_IN_CHECKOUT,
    payload,
  };
};

export const onCheckRegionInCheckout = (payload) => {
  return {
    type: actionTypes.ON_CHECK_REGION_IN_CHECKOUT,
    payload,
  };
};

export const checkAllCategoriesAndRegions = (payload) => {
  return {
    type: actionTypes.CHECK_ALL_CATEGORIES_AND_REGIONS,
    payload,
  };
};

export const setPaymentStep = (payload) => {
  return {
    type: actionTypes.SET_PAYMENT_STEP,
    payload,
  };
};

export const setAppBackgroundColor = (payload) => {
  return {
    type: actionTypes.SET_APP_BACKGROUND_COLOR,
    payload,
  };
};
