import React, { useState } from 'react';
import styles from './styles.module.scss';
import { connect } from 'react-redux';
import Select from 'react-select';
import PolandIcon from '../../assets/images/countriesIcons/poland.jpg';
import { Link } from 'react-router-dom';
//icons
import MessageSentImg from '../../assets/images/Contacts/messageSent.jpg';
import validator from 'validator';

const options = [
  {
    value: 'PL',
    label: (
      <div className={styles.contactPage__rightBlock__selectImage}>
        <img src={PolandIcon} alt="country icon" />
      </div>
    ),
    code: '+48',
  },
  {
    value: 'QW',
    label: (
      <div className={styles.contactPage__rightBlock__selectImage}>
        <img src={PolandIcon} alt="country icon" />
      </div>
    ),
    code: '+23',
  },
  {
    value: 'ZA',
    label: (
      <div className={styles.contactPage__rightBlock__selectImage}>
        <img src={PolandIcon} alt="country icon" />
      </div>
    ),
    code: '+54',
  },
];

const customStyles = {
  control: (base) => ({
    ...base,
    height: '100%',
    border: 'none',
  }),
  indicatorSeparator: (styles) => ({ display: 'none' }),
};

const Contacts = () => {
  const [formData, setFormData] = useState({
    email: {
      value: '',
      errors: [],
    },
    countryCode: options[0].code,
    phone: {
      value: '',
      errors: [],
    },
    message: '',
  });

  const [step, setStep] = useState(1);

  const setFormDataHandler = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: {
        value: e.target.value,
        errors: [...formData[e.target.name].errors],
      },
    });
  };

  const selectHandler = (e) => {
    setFormData({ ...formData, countryCode: e.code });
  };

  const textAreaHandler = (e) => {
    setFormData({ ...formData, message: e.target.value });
  };

  const sendBtnHandler = () => {
    const email = formData.email.value;
    const phone = formData.phone.value;
    // const countryCode = formData.countryCode;

    if (validator.isEmpty(email) || !validator.isEmail(email)) {
      const newErrorsArr = [
        ...formData.email.errors,
        {
          message: 'Email must be formatted correctly',
        },
      ];
      return setFormData({
        ...formData,
        email: {
          ...formData.email,
          errors: newErrorsArr,
        },
        phone: {
          ...formData.phone,
          errors: [],
        },
      });
    }

    if (validator.isEmpty(phone) || !validator.isMobilePhone(phone)) {
      const newErrorsArr = [
        ...formData.phone.errors,
        {
          message: 'Must contain only numbers, + ()-, and x',
        },
      ];
      return setFormData({
        ...formData,
        email: {
          ...formData.email,
          errors: [],
        },
        phone: {
          ...formData.phone,
          errors: newErrorsArr,
        },
      });
    }
    //fetch to server and clear state
    setFormData({
      ...formData,
      email: {
        value: '',
        errors: [],
      },
      phone: {
        value: '',
        errors: [],
      },
    });
    setStep(2);
  };

  return (
    <div className={styles.contactPage}>
      <div className={styles.contactPage__leftBlock}>
        <div className={styles.contactPage__leftBlock__wrap}>
          <h1 className={styles.contactPage__leftBlock__title}>
            Schedule your conversation with Skillvox
          </h1>
          <p className={styles.contactPage__leftBlock__text}>
            Skillvox seeks to provide everyone a platform for their skills.
            <br />
            Sponsoring a Community not only provides unique awareness of your
            brand, it makes you the ultimate influencer and reaffirms your
            commitment to nurturing skills that matter.
          </p>
        </div>
      </div>
      <div className={styles.contactPage__rightBlock}>
        <div className={styles.contactPage__rightBlock__wrap}>
          {step === 1 ? (
            <>
              <div className={styles.contactPage__rightBlock__inputWrap}>
                <label
                  className={styles.contactPage__rightBlock__label}
                  htmlFor="emailInput"
                >
                  Work Email:
                  <span
                    className={styles.contactPage__rightBlock__labelRedColor}
                  >
                    *
                  </span>
                </label>
                <input
                  id="emailInput"
                  className={`${styles.contactPage__rightBlock__input} ${
                    formData.email.errors.length > 0 ? styles.error : ''
                  }`}
                  type="email"
                  name="email"
                  value={formData.email.value}
                  onChange={setFormDataHandler}
                  placeholder="John.Doe@xyz.com"
                />
                {formData.email.errors.length > 0 && (
                  <p className={styles.contactPage__rightBlock__errorText}>
                    {formData.email.errors[0].message}
                  </p>
                )}
              </div>
              <div className={styles.contactPage__rightBlock__inputWrap}>
                <label
                  className={styles.contactPage__rightBlock__label}
                  htmlFor="phoneInput"
                >
                  Phone:
                  <span
                    className={styles.contactPage__rightBlock__labelRedColor}
                  >
                    *
                  </span>
                </label>

                <div className={styles.contactPage__rightBlock__selectInput}>
                  <div
                    className={
                      styles.contactPage__rightBlock__selectInput__select
                    }
                  >
                    <Select
                      defaultValue={options[0]}
                      options={options}
                      hideSelectedOptions={false}
                      className={`${styles.contactPage__rightBlock__select} ${
                        formData.phone.errors.length > 0 ? styles.error : ''
                      }`}
                      onChange={selectHandler}
                      styles={customStyles}
                      isSearchable={false}
                    />
                  </div>
                  <div
                    className={`${
                      styles.contactPage__rightBlock__selectInput__countryCode
                    } ${formData.phone.errors.length > 0 ? styles.error : ''}`}
                  >
                    {formData.countryCode}
                  </div>
                  <div
                    className={
                      styles.contactPage__rightBlock__selectInput__input
                    }
                  >
                    <input
                      className={`${styles.contactPage__rightBlock__input} ${
                        styles.inputWithSelect
                      } ${
                        formData.phone.errors.length > 0 ? styles.error : ''
                      }`}
                      type="text"
                      name="phone"
                      value={formData.phone.value}
                      onChange={setFormDataHandler}
                      id="phoneInput"
                    />
                  </div>
                </div>
                {formData.phone.errors.length > 0 && (
                  <p className={styles.contactPage__rightBlock__errorText}>
                    {formData.phone.errors[0].message}
                  </p>
                )}
              </div>
              <div className={styles.contactPage__rightBlock__inputWrap}>
                <label
                  className={styles.contactPage__rightBlock__label}
                  htmlFor="textInput"
                >
                  Tell us how we can help:
                </label>
                <div className={styles.contactPage__rightBlock__textArea}>
                  <textarea
                    id="textInput"
                    name="message"
                    placeholder="Need help with sponsoring a Community."
                    onChange={textAreaHandler}
                    maxLength="300"
                  ></textarea>
                  <div
                    className={
                      styles.contactPage__rightBlock__textArea__symbolsCounter
                    }
                  >
                    {formData.message.length} / 300
                  </div>
                </div>
                <div className={styles.contactPage__rightBlock__confirmBtn}>
                  <div
                    className={styles.contactPage__rightBlock__confirmBtn__wrap}
                  >
                    <button onClick={sendBtnHandler}>LET'S TALK</button>
                  </div>
                </div>

                <div className={styles.contactPage__rightBlock__privacy}>
                  <p className={styles.contactPage__rightBlock__privacy__text}>
                    The information you provide will be used according to our{' '}
                    {` `}
                    <Link to="#">Privacy Policy</Link>
                  </p>
                </div>
              </div>
            </>
          ) : (
            <div className={styles.contactPage__rightBlock__messageSent}>
              <img src={MessageSentImg} alt="send message icon" />
              <div
                className={
                  styles.contactPage__rightBlock__messageSent__textBlock
                }
              >
                <h2
                  className={styles.contactPage__rightBlock__messageSent__title}
                >
                  Message sent
                </h2>
                <p
                  className={styles.contactPage__rightBlock__messageSent__text}
                >
                  Thanks for getting in touch! We will reply as soon as
                  possible.
                </p>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default connect(null, null)(Contacts);
