import styles from './styles.module.scss';
import Checkbox from '../../../../../components/atoms/Checkbox';
import VerticalDotsImg from '../../../../../assets/images/ui-icons/verticalDots.png';
import { useState } from 'react';

const SingleMember = ({
  id,
  firstName,
  lastName,
  department,
  lastActivity,
  registrationDate,
  checked,
  checkBoxHandler,
}) => {
  const [isMemberSettingsBarOpen, setIsMemberSettingsBarOped] = useState(false);
  const onCheckBox = (e) => {
    checkBoxHandler(id, e.target.checked);
  };
  const onMemberSettingsBar = () => {
    setIsMemberSettingsBarOped(!isMemberSettingsBarOpen);
  };
  return (
    <li
      className={styles.singleMember}
      key={id}
      onMouseLeave={() => setIsMemberSettingsBarOped(false)}
    >
      {/* single user checkbox */}
      <div className={styles.singleMember__checkbox}>
        <Checkbox
          //   label={`${firstName} ${lastName}`}
          name={id}
          checked={checked}
          onChangeHandler={onCheckBox}
          backgroundColor="green"
        />
      </div>

      {/* fullname */}
      <div className={styles.singleMember__fullname}>
        <span className={styles.singleMember__firstname}>
          {`${firstName} `}
        </span>
        <span className={styles.singleMember__lastname}>{lastName}</span>
      </div>

      {/* department/country */}
      <div className={styles.singleMember__department}>{department}</div>
      {/* last online activity */}
      <div className={styles.singleMember__lastOnlineDate}>{lastActivity}</div>
      {/* registration date */}
      <div className={styles.singleMember__registrationDate}>
        {registrationDate}
      </div>
      {/* member settings hover bar */}
      <div className={styles.singleMember__settings}>
        <div className={styles.singleMember__settings__wrap}>
          <div
            className={styles.singleMember__settings__icon}
            onClick={onMemberSettingsBar}
          >
            <img src={VerticalDotsImg} alt="member settings icon" />
          </div>
          <div
            className={`${styles.singleMember__settings__optionsBar} ${
              isMemberSettingsBarOpen ? styles.show : ''
            }`}
          >
            <div
              className={
                styles.singleMember__settings__optionsBar__singleOption
              }
            >
              Remove access
            </div>
          </div>
        </div>
      </div>
    </li>
  );
};

export default SingleMember;
