import styles from './styles.module.scss';
import TrashImg from '../../../assets/images/ui-icons/trash.png';
import { priceWithCommas } from '../../../helpers/index';
import Button from '../../../components/atoms/Button';
import { useState } from 'react';
import Backdrop from '../../../components/molecules/Backdrop';
import RegionsList from '../../../components/CommunityRegions/RegionsList';
import { regionsData } from '../../../components/CommunityRegions/Region';

const CheckoutProduct = ({
  title,
  id,
  image,
  regions,
  currency_symbol,
  campaign_title,
  loader,
  isPartialPaymentCommunity,
  symbol_placement,
  deleteRegionHandler,
  partialPaymentHandler,
}) => {
  const [showCountriesPopup, setShowCountriesPopup] = useState(false);
  const [popupCountries, setPopupCountries] = useState([]);
  const [regionTitle, setRegionTitle] = useState('');

  const showRegionCountriesClick = (regionId) => {
    const { region, countries } = regionsData.find(
      (region) => Number(region.id) === Number(regionId)
    );
    setPopupCountries(countries);
    setRegionTitle(region);
    setShowCountriesPopup(true);
  };

  const hideRegionCountriesClick = () => {
    setPopupCountries([]);
    setShowCountriesPopup(false);
    setRegionTitle('');
  };

  const onPartialPaymentClick = () => {
    partialPaymentHandler(id);
  };

  return (
    <div className={styles.community}>
      <div className={styles.community__wrap}>
        {/* header */}
        <div className={styles.community__header}>
          <div className={styles.community__header__image}>
            <img src={'https://voxdigital.by' + image} alt={title + ' icon'} />
          </div>
          <div className={styles.community__header__data}>
            <div className={styles.community__header__data__title}>{title}</div>
            <div className={styles.community__header__data__campaign}>
              {campaign_title || ''}
            </div>
          </div>
          {!isPartialPaymentCommunity && (
            <div className={styles.community__header__paymentButton}>
              <Button
                text={!loader ? 'Make a partial payment' : 'Loading'}
                addClass="whiteBG__blueBorder"
                clickHandler={onPartialPaymentClick}
              ></Button>
            </div>
          )}
        </div>
        {/* regions */}
        <div className={styles.community__regions}>
          {regions.map((region) => (
            <div
              className={styles.community__regions__single}
              key={region.region_id + '__' + region.name}
            >
              <div className={styles.community__regions__single__title}>
                {region.name}
              </div>
              <div className={styles.community__regions__single__countries}>
                <span
                  className={
                    styles.community__regions__single__countries__count
                  }
                  onClick={() => showRegionCountriesClick(region.region_id)}
                >
                  {region.countries_summary.country_tally} countries
                </span>{' '}
                <span
                  className={
                    styles.community__regions__single__countries__title
                  }
                >
                  {region.countries_summary.region_summary_text}
                </span>
              </div>
              <div className={styles.community__regions__single__price}>
                {symbol_placement === 'before' && (
                  <span
                    className={
                      styles.community__regions__single__price__currency
                    }
                  >
                    {currency_symbol}
                  </span>
                )}
                {priceWithCommas(Number(region.price))}
                {symbol_placement === 'after' && (
                  <span
                    className={
                      styles.community__regions__single__price__currency
                    }
                  >
                    {currency_symbol}
                  </span>
                )}
              </div>
              <div className={styles.community__regions__single__program}>
                <span
                  className={styles.community__regions__single__program__pilot}
                >
                  Pilot sponsorship program:
                </span>{' '}
                <span
                  className={
                    styles.community__regions__single__program__impressions
                  }
                >
                  3 million sponsor impressions
                </span>
              </div>
              {/* remove from cart */}
              <div
                className={styles.community__regions__single__remove}
                onClick={() => deleteRegionHandler(id, region.region_id)} //TODO
              >
                <img src={TrashImg} alt="remove from cart icon" />
              </div>
            </div>
          ))}
        </div>
      </div>
      <Backdrop
        show={showCountriesPopup}
        clickhandler={hideRegionCountriesClick}
        appearance="dark"
      />
      {showCountriesPopup && popupCountries?.length && (
        <RegionsList
          title={regionTitle}
          firstColumn={popupCountries[0]}
          secondColumn={popupCountries[1]}
          thirdColumn={popupCountries[2]}
          fourthColumn={popupCountries[3]}
        />
      )}
    </div>
  );
};

export default CheckoutProduct;
