import styles from './styles.module.scss';

const PaymentProgressBar = ({ step, textPoints }) => {
  return (
    <div className={styles.progressBar}>
      <div className={styles.progressBar__points}>
        {textPoints.map((text, idx) => {
          return (
            <div
              className={`${styles.progressBar__points__onePoint} ${
                step > idx + 1 ? styles.stepDone : ''
              }`}
            >
              <div
                className={`${styles.progressBar__points__onePoint__step} ${
                  step > idx + 1 ? styles.stepDone : ''
                } ${step === idx + 1 ? styles.currentStep : ''}`}
              ></div>
              <div
                key={text + '__' + idx}
                className={styles.progressBar__points__onePoint__text}
              >
                {text}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};
export default PaymentProgressBar;
