import { useState } from 'react';
import styles from './styles.module.scss';

const EditBillingAddress = ({
  address,
  secondAddress,
  city,
  postCode,
  country,
  state,
  setAddress,
  setSecondAddress,
  setCity,
  setPostCode,
  setCountry,
  setState,
  setShowEditField,
  onCancelClick,
  // onSaveDispatch
}) => {
  const [newAddress, setNewAddress] = useState(address);
  const [newSecondAddress, setNewSecondAddress] = useState(secondAddress);
  const [newCity, setNewCity] = useState(city);
  const [newPostCode, setNewPostCode] = useState(postCode);
  const [newCountry, setNewCountry] = useState(country);
  const [newState, setNewState] = useState(state);

  const setAddressHandler = (e) => {
    setNewAddress(e.target.value);
  };

  const setSecondAddressHandler = (e) => {
    setNewSecondAddress(e.target.value);
  };

  const setCityHandler = (e) => {
    setNewCity(e.target.value);
  };

  const setPostCodeHandler = (e) => {
    setNewPostCode(e.target.value);
  };

  const setCountryHandler = (e) => {
    setNewCountry(e.target.value);
  };

  const setStateHandler = (e) => {
    setNewState(e.target.value);
  };

  const saveBtnHandler = () => {
    setAddress(newAddress);
    setSecondAddress(newSecondAddress);
    setCity(newCity);
    setPostCode(newPostCode);
    setCountry(newCountry);
    setState(newState);
    setShowEditField(false);
    // onSaveDispatch(name, secondName, middleName)
  };

  return (
    <div className={styles.editBillingAddress}>
      <div className={styles.editBillingAddress__inputsField}>
        {/* address + 2nd address */}
        <div className={styles.editBillingAddress__input}>
          <label className={styles.editBillingAddress__label} htmlFor="address">
            Address{' '}
          </label>
          <input
            type="text"
            name="address"
            value={newAddress}
            id="address"
            onChange={setAddressHandler}
          />
        </div>
        <div className={styles.editBillingAddress__input}>
          <input
            type="text"
            name="secondAddress"
            value={newSecondAddress}
            id="secondAddress"
            onChange={setSecondAddressHandler}
          />
        </div>

        {/* city / town */}
        <div className={styles.editBillingAddress__input}>
          <label className={styles.editBillingAddress__label} htmlFor="city">
            City / town{' '}
          </label>
          <input
            type="text"
            name="city"
            value={newCity}
            id="city"
            onChange={setCityHandler}
          />
        </div>

        {/* state */}
        <div className={styles.editBillingAddress__input}>
          <label className={styles.editBillingAddress__label} htmlFor="state">
            State{' '}
          </label>
          <input
            type="text"
            name="state"
            value={newState}
            id="state"
            onChange={setStateHandler}
          />
        </div>

        {/*  postal code */}
        <div className={styles.editBillingAddress__input}>
          <label
            className={styles.editBillingAddress__label}
            htmlFor="postCode"
          >
            Postal code{' '}
          </label>
          <input
            type="text"
            name="postCode"
            value={newPostCode}
            id="postCode"
            onChange={setPostCodeHandler}
          />
        </div>

        {/* Country */}
        <div className={styles.editBillingAddress__input}>
          <label className={styles.editBillingAddress__label} htmlFor="country">
            Country{' '}
          </label>
          <input
            type="text"
            name="country"
            value={newCountry}
            id="country"
            onChange={setCountryHandler}
          />
        </div>
      </div>

      <div className={styles.editBillingAddress__buttons}>
        <button
          className={`${styles.editBillingAddress__buttons__btn} ${styles.editBillingAddress__buttons__btn__cancel}`}
          onClick={onCancelClick}
        >
          Cancel
        </button>
        <button
          className={`${styles.editBillingAddress__buttons__btn} ${styles.editBillingAddress__buttons__btn__save}`}
          onClick={saveBtnHandler}
        >
          Save changes
        </button>
      </div>
    </div>
  );
};

export default EditBillingAddress;
