import React, { useEffect } from 'react';
import styles from './styles.module.scss';
import { connect } from 'react-redux';
import { priceWithCommas } from '../../../helpers';
import FullScreenLayout from '../../Layouts/FullScreenLayout';
import Button from '../../atoms/Button';
import { setFixedHeaderPayment } from './action';
import { useHistory } from 'react-router-dom';

const HeaderPayment = ({
  isHeaderPaymentFixed,
  isMainMenuMiniSize,
  currency_symbol,
  isPayButtonDisabled,
  total_outstanding,
  symbol_placement,
  dispatchSetFixedHeaderPayment,
}) => {
  const history = useHistory();

  useEffect(() => {
    function scrollHandler() {
      if (window.scrollY >= 150) {
        dispatchSetFixedHeaderPayment(true);
      }
      if (window.scrollY < 50) {
        dispatchSetFixedHeaderPayment(false);
      }
    }

    if (window.screen.width > 860) {
      document.addEventListener('scroll', scrollHandler);
    }
    return () => {
      document.removeEventListener('scroll', scrollHandler);
      dispatchSetFixedHeaderPayment(false);
    };
  }, [dispatchSetFixedHeaderPayment]);

  const onPayHandler = () => {
    history.push('/checkout');
  };

  return (
    <FullScreenLayout
      addClass={
        isHeaderPaymentFixed && isMainMenuMiniSize ? 'payment__absolute' : ''
      }
    >
      <div
        className={`${styles['pay-check']} ${
          isHeaderPaymentFixed ? styles['fullWidth'] : ''
        }`}
      >
        <div
          className={`${styles['pay-check__wrap']} ${
            isHeaderPaymentFixed ? styles['wrapToContent'] : ''
          }`}
        >
          <p className={`${styles['pay-check__title']}`}>
            Explore the Communities below
          </p>
          <div className={`${styles['pay-check__result']}`}>
            <p className={`${styles['pay-check__right-title']}`}>
              Payment outstanding
            </p>
            <div className={`${styles['pay-check__summ']}`}>
              {symbol_placement === 'before' && currency_symbol + ' '}
              {total_outstanding && total_outstanding > 0
                ? priceWithCommas(total_outstanding)
                : '0, 000'}{' '}
              {symbol_placement === 'after' && currency_symbol}
            </div>
            <div className={styles['pay-check__button']}>
              <Button
                appearance="payment"
                text="Pay now"
                disabled={isPayButtonDisabled}
                clickHandler={onPayHandler}
              >
                Pay now
              </Button>
            </div>
          </div>
        </div>
      </div>
    </FullScreenLayout>
  );
};

const mapStateToProps = (state) => {
  const productsInBucket = state.cart.productsCount;
  const isPayButtonDisabled =
    productsInBucket && productsInBucket > 0 ? false : true;
  return {
    currency_symbol: state.categorygrid.currency_symbol,
    preferred_currency: state.categorygrid.preferred_currency,
    total_outstanding: state.cart.total_outstanding,
    isHeaderPaymentFixed: state.headerPayment.isHeaderPaymentFixed,
    isMainMenuMiniSize: state.headerMenu.mainMenu.isMiniSize,
    symbol_placement: state.cart.symbol_placement,
    isPayButtonDisabled,
  };
};

const mapDispatchToProps = {
  dispatchSetFixedHeaderPayment: setFixedHeaderPayment,
};

export default connect(mapStateToProps, mapDispatchToProps)(HeaderPayment);
