import React from "react";
import styles from "./styles.module.scss";

const FullScreenLayout = ({
  children,
  backgroundColor = "#ffffff",
  borderBottom = "none",
  addClass,
  fixed,
}) => {
  return (
    <div
      className={`${styles["fullScreenLayout"]} ${addClass ? styles[addClass] : ""} ${
        fixed ? styles["fixed"] : ""
      }`}
      style={{ background: backgroundColor, borderBottom: borderBottom }}
    >
      {children}
    </div>
  );
};

export default FullScreenLayout;
