import styles from './styles.module.scss';
import BlueLeftArrow from '../../../../assets/images/ui-icons/rightArrowBlue.png';
import { Link } from 'react-router-dom';
import Button from '../../../atoms/Button';
import WarningIcon from '../../../../assets/images/ui-icons/warningTransparentIcon.png';

const SwiftPaymentMethod = (props) => {
  const {
    step,
    setStep,
    totalPrice,
    currency,
    onCancelTransfer,
    error,
    appInfo: { app, bank },
  } = props;
  const stepBackHalder = () => {
    setStep(step - 1);
  };
  return (
    <div className={styles.swiftPayment}>
      <div className={styles.swiftPayment__title}>
        Our bank details for this transfer
      </div>
      <p className={styles.swiftPayment__text}>
        Youll need to do this next bit manually. Tell your bank to make a
        payment using the details below
      </p>

      <div className={styles.swiftPayment__handlingPaymentText}>
        <p className={styles.swiftPayment__text}>
          SWIFT payments are handled by intermediary banks before reaching
          Skillvox.
        </p>
        <p>
          <span
            className={`${styles.swiftPayment__text} ${styles.swiftPayment__boldText}`}
          >
            There will likely be extra fees, in addition to what your bank
            changes.{' '}
          </span>
          <span
            className={`${styles.swiftPayment__text} ${styles.swiftPayment__link}`}
          >
            Read more
          </span>
        </p>
      </div>

      <div
        className={`${styles.swiftPayment__error} ${error ? styles.show : ''}`}
      >
        <span className={styles.swiftPayment__error__text}>
          {error}
          <div className={styles.swiftPayment__error__icon}>
            <img src={WarningIcon} alt="Error icon" />
          </div>
        </span>
      </div>

      {/* table */}
      <div className={styles.swiftPayment__transferInstructions}>
        <div className={styles.swiftPayment__transferInstructions__wrap}>
          <div className={styles.swiftPayment__transferInstructions__header}>
            <p>SWIFT transfer instructions</p>
          </div>
          <div className={styles.swiftPayment__transferInstructions__table}>
            <div
              className={styles.swiftPayment__transferInstructions__table__row}
            >
              <div
                className={
                  styles.swiftPayment__transferInstructions__table__row__firstColumn
                }
              >
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__key
                  }
                >
                  Payee name
                </div>
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__value
                  }
                >
                  {app.name}
                </div>
              </div>
              <div
                className={
                  styles.swiftPayment__transferInstructions__table__row__secondColumn
                }
              >
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__key
                  }
                >
                  Use this reference
                </div>
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__value
                  }
                >
                  {bank.reference}
                </div>
              </div>
            </div>
            <div
              className={styles.swiftPayment__transferInstructions__table__row}
            >
              <div
                className={
                  styles.swiftPayment__transferInstructions__table__row__firstColumn
                }
              >
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__key
                  }
                >
                  IBAN:
                </div>
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__value
                  }
                >
                  {bank.IBAN}
                </div>
              </div>
              <div
                className={
                  styles.swiftPayment__transferInstructions__table__row__secondColumn
                }
              >
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__key
                  }
                >
                  Bank code (BIC/SWIFT):
                </div>
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__value
                  }
                >
                  {bank.code}
                </div>
              </div>
            </div>
            <div
              className={styles.swiftPayment__transferInstructions__table__row}
            >
              <div
                className={
                  styles.swiftPayment__transferInstructions__table__row__firstColumn
                }
              >
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__key
                  }
                >
                  Amount to send:
                </div>
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__value
                  }
                >
                  {totalPrice} {currency}
                </div>
              </div>
            </div>
            <div
              className={styles.swiftPayment__transferInstructions__table__row}
            >
              <div
                className={
                  styles.swiftPayment__transferInstructions__table__row__firstColumn
                }
              >
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__key
                  }
                >
                  Our bank address
                </div>
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__value
                  }
                >
                  <p>{bank.name}</p>
                  <p>{bank.entity}</p>
                  <p>{bank.address.street}</p>
                  <p>{bank.address.area}</p>
                  <p>{bank.address.city}</p>
                  <p>{bank.address.postcode}</p>
                  <p>{bank.country}</p>
                </div>
              </div>
              <div
                className={
                  styles.swiftPayment__transferInstructions__table__row__secondColumn
                }
              >
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__key
                  }
                >
                  {app.name} address:
                </div>
                <div
                  className={
                    styles.swiftPayment__transferInstructions__table__value
                  }
                >
                  <p>{app.address.floor}</p>
                  <p>{app.address.area}</p>
                  <p>{app.address.street}</p>
                  <p>{app.address.city}</p>
                  <p>{app.address.postcode}</p>
                  <p>{app.address.country}</p>
                </div>
              </div>
            </div>
          </div>
          <div
            className={
              styles.swiftPayment__transferInstructions__infoAfterTable
            }
          >
            You can use your banks online banking or mobile app to make your
            bank transfer to {app.name}.
          </div>
        </div>
      </div>
      <div className={styles.swiftPayment__buttons}>
        <Button clickHandler={onCancelTransfer} addClass="grayBG">
          Cancel this transfer
        </Button>
        <Button addClass="blueBG">I've made my SWIFT transfer</Button>
        <Link className={styles.swiftPayment__buttons__helpLink}>Help</Link>
      </div>
    </div>
  );
};

export default SwiftPaymentMethod;
