import styles from '../styles.module.scss';
import Button from '../../../components/atoms/Button';

const FinishRegistrationStep = ({ email, clickHandler }) => {
  return (
    <div className={styles.signUp__finishRegistration}>
      <div className={styles.signUp__finishRegistration__text}>
        <p>One last step. Activate your account.</p>
        <p>
          An activation mail has been sent to your email
          <span className="blueText">{' ' + email}.</span>
        </p>
        <p>Open the link in the email you recieve to complete verification.</p>
      </div>
      <div className={styles.signUp__finishRegistration__btn}>
        <Button
          text="Back"
          appearance="backButton"
          clickHandler={clickHandler}
        />
      </div>
    </div>
  );
};

export default FinishRegistrationStep;
