import React from 'react';
import styles from './styles.module.scss';
import Button from '../atoms/Button';
import { priceWithCommas } from '../../helpers';
import { useHistory } from 'react-router-dom';

const Payment = ({
  total_outstanding,
  total_pledged,
  productsCount,
  currency_symbol,
  symbol_placement,
  laterBtnOnclick,
}) => {
  const history = useHistory();

  const onPaymentClick = () => {
    history.push('/checkout');
  };
  return (
    <div className={`${styles['payment']}`}>
      <div className={`${styles['payment__window']}`}>
        <p className={`${styles['payment__window-promo-text']}`}>
          Upfront for 2021/22 sponsorship campaign
        </p>
        <div className={`${styles['payment__window-total-pledged']}`}>
          <div className={`${styles['payment__window-total-pledged-title']}`}>
            Total pledged
          </div>
          <div className={`${styles['payment__window-total-pledged-price']}`}>
            {symbol_placement === 'before' && currency_symbol}
            {total_pledged ? priceWithCommas(total_pledged) : '0,000'}{' '}
            {symbol_placement === 'after' && currency_symbol}
          </div>
        </div>
        <div className={`${styles['payment__window-total-price']}`}>
          <div className={`${styles['payment__window-total-price-title']}`}>
            Payment outstanding{' '}
          </div>
          <div
            className={`${styles['payment__window-total-price-final-price']}`}
          >
            {symbol_placement === 'before' && currency_symbol}
            {priceWithCommas(total_outstanding)}
            {symbol_placement === 'after' && currency_symbol}
          </div>
        </div>
        <div className={`${styles['payment__window-buttons']}`}>
          <Button
            clickHandler={laterBtnOnclick}
            text="Ill transfer my payment later"
            appearance="pre-payment"
          />

          <Button
            text="Continue to payment"
            appearance="payment"
            disabled={productsCount > 0 ? false : true}
            clickHandler={onPaymentClick}
          />
        </div>
      </div>
    </div>
  );
};

export default Payment;
