const Region = ({
  data,
  currency_symbol,
  symbol_placement,
  clickHander,
  showSponsorDetails,
  loader,
}) => {
  const { region_id, name, price, payment_status, chosen } = data;

  const onClick = () => {
    clickHander(data, !chosen);
  };
  return (
    <>
      <li className={`${styles['sponsor__table-element']}`}>
        <div className={`${styles['sponsor__table-element__wrapper']}`}>
          <div className={`${styles['sponsor__table-element-region']}`}>
            {name}
          </div>
          <div
            className={`${styles['sponsor__table-element-countriesCount']}`}
            // onMouseOver={()=> {setShowCountriesImg(showCountriesListImgHover)}}
            // onMouseLeave={()=> {setShowCountriesImg(showCountriesListImg)}}
            onClick={() =>
              showSponsorDetails(
                name,
                regionsData.find((el) => String(el.id) === String(region_id))
                  .countries
              )
            }
          >
            <span
              className={`${styles['sponsor__table-element-countriesCount-wrapper']}`}
            >
              {
                regionsData.find((el) => String(el.id) === String(region_id))
                  .countriesCount
              }{' '}
              countries{' '}
              <Link to="#">
                <img
                  className={`${styles['sponsor__table-element-img-link']}`}
                  src={showCountriesListImgHover}
                  alt="countries button"
                />
              </Link>
            </span>
          </div>
          <div className={`${styles['sponsor__table-element-description']}`}>
            {
              regionsData.find((el) => String(el.id) === String(region_id))
                .description
            }
          </div>
          <div className={`${styles['sponsor__table-element-price']}`}>
            {symbol_placement === 'before' && (
              <span className={`${styles['sponsor__table-element-currency']}`}>
                {currency_symbol}
              </span>
            )}
            {priceWithCommas(Number(price))}
            {symbol_placement === 'after' && (
              <span className={`${styles['sponsor__table-element-currency']}`}>
                {currency_symbol}
              </span>
            )}
          </div>
          <div className={`${styles['sponsor__table-element-sponsorsText']}`}>
            for{' '}
            <span
              className={`${styles['sponsor__table-element-sponsorsText-black']}`}
            >
              {
                regionsData.find((el) => String(el.id) === String(region_id))
                  .sponsorsText
              }
            </span>{' '}
            sponsor impressions
          </div>

          {payment_status === '0' && (
            <Button
              appearance="sponsor"
              clickHandler={onClick}
              clicked={chosen}
              disabled={loader}
            />
          )}
          {payment_status === '1' && (
            <div className={styles['sponsor__table-element-paid']}>
              Paid <img src={GreenTickImg} alt="Paid region" />
            </div>
          )}
        </div>
      </li>
    </>
  );
};

export default Region;
