import actionTypes from '../constants';

const initialState = {
  app: {
    name: 'Skillvox',
    address: {
      floor: '6th Floor',
      area: 'The Tea Building',
      street: '56 Shoreditch High Street',
      city: 'London',
      postcode: 'E1 6JJ',
      country: 'United Kingdom',
    },
    bgColor: '#ffffff',
    width: '100%',
  },
  bank: {
    name: 'Barclays',
    entity: 'BARCLAYS BANK PLC',
    IBAN: 'GB49BARC20923753650709',
    code: 'BARCGB22',
    reference: 'P1184257',
    address: {
      street: '1250 High Road',
      area: 'Whetstone',
      city: 'London',
      postcode: 'N20 OPB',
      country: 'United Kingdom',
    },
  },
};

export const appInfo = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case actionTypes.SET_APP_BACKGROUND_COLOR: {
      const { backgroundColor } = payload;
      return {
        ...state,
        app: {
          ...state.app,
          bgColor: backgroundColor,
        },
      };
    }
    default: {
      return state;
    }
  }
};
