import RoadmapImg from '../../assets/images/roadmap/Landscape.png';
import styles from './styles.module.scss';
import Cloud1 from '../../assets/images/roadmap/cloud1.png';
import Cloud2 from '../../assets/images/roadmap/Cloud2.png';
import Cloud3 from '../../assets/images/roadmap/Cloud3.png';
import AirBaloon from '../../assets/images/roadmap/ballon.png';
import WhiteTickImg from '../../assets/images/ui-icons/white-tick.png';

const Roadmap = () => {
  return (
    <div className={styles.roadmap}>
      <img
        className={styles.roadmap__mainImage}
        src={RoadmapImg}
        alt="Skillvox roadmap"
      />
      <div className={styles.roadmap__clouds}>
        {/* First Cloud */}
        <div
          className={`${styles.roadmap__clouds__singleCloud} ${styles.firstCloud}`}
        >
          <img src={Cloud1} alt="cloud #1" />
        </div>

        {/* Second Cloud */}
        <div
          className={`${styles.roadmap__clouds__singleCloud} ${styles.secondCloud}`}
        >
          <img src={Cloud2} alt="cloud #2" />
        </div>

        {/* Air baloon */}
        <div
          className={`${styles.roadmap__clouds__singleCloud} ${styles.airBaloon}`}
        >
          <img src={AirBaloon} alt="cloud #2"/>
        </div>

        {/* cloud near ballon */}
        <div
          className={`${styles.roadmap__clouds__singleCloud} ${styles.nearBallonCloud}`}
        >
          <img src={Cloud3} alt="cloud #3" />
        </div>
      </div>
      {/*  */}
      <div className={styles.roadmap__path}>
        {/* first point */}
        <div className={`${styles.roadmap__path__point} ${styles.firstPoint}`}>
          <div className={styles.roadmap__path__point__image}>
            <img src={WhiteTickImg} alt="cloud #2" />
          </div>
        </div>
        {/* second point */}
      </div>
    </div>
  );
};

export default Roadmap;
