import { useEffect } from 'react';
import './App.scss';
import Header from './components/molecules/Header';
import Footer from './components/molecules/Footer';
import Login from './pages/Login';
import MainPage from './pages/MainPage';
import BecomeSponsor from './pages/BecomeSponsor';
import BecomeSponsorEnded from './pages/BecomeSponsor/BecomeSponsorEnded';
import Roadmap from './pages/Roadmap';
import Contacts from './pages/Contacts';
import SignUp from './pages/SignUp';
import MyAccount from './pages/MyAccount';
import ScrollToTop from './components/molecules/ScrollToTop';
import Checkout from './pages/Checkout';
import { Switch, Route } from 'react-router-dom';
import { connect } from 'react-redux';
import PrivateRoute from './components/PrivateRoute';
import { checkAuth } from './redux/thunk/refreshToken';
import { getTokenData, deleteToken, deleteUser } from './http/localStorage';
import WhoWeAre from './pages/WhoWeAre';

function App({ backgroundColor, dispatchCheckAuth }) {
  useEffect(() => {
    const tokenData = getTokenData();
    if (tokenData) {
      dispatchCheckAuth();
    } else {
      deleteToken();
      deleteUser();
    }
  }, [dispatchCheckAuth]);

  return (
    <div style={{ background: backgroundColor, overflow: 'hidden' }}>
      <Header />
      <ScrollToTop />
      <Switch>
        <PrivateRoute exact path="/">
          <MainPage />
        </PrivateRoute>
        <PrivateRoute path="/become-sponsor">
          <BecomeSponsor />
        </PrivateRoute>
        <PrivateRoute path="/who-we-are">
          <WhoWeAre />
        </PrivateRoute>
        <PrivateRoute path="/become-sponsor-ended">
          <BecomeSponsorEnded />
        </PrivateRoute>
        <PrivateRoute path="/roadmap">
          <Roadmap />
        </PrivateRoute>
        <PrivateRoute path="/contacts">
          <Contacts />
        </PrivateRoute>
        <PrivateRoute path="/checkout">
          <Checkout />
        </PrivateRoute>

        {/* My Account  */}
        <PrivateRoute path="/my-account">
          <MyAccount />
        </PrivateRoute>

        <Route path="/login">
          <Login />
        </Route>
        <Route path="/sign-up">
          <SignUp />
        </Route>
      </Switch>

      <Footer />
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    backgroundColor: state.appInfo.app.bgColor,
  };
};

const mapDispatchToProps = {
  dispatchCheckAuth: checkAuth,
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
