import React, { useState, useEffect } from 'react';
import styles from './styles.module.scss';
import { connect } from 'react-redux';
import Logo from '../../assets/images/logoNew.png';
import FirstStep from './FirstStep';
import FinishRegistrationStep from './FinishRegistrationStep';
import {
  signUpInputEmail,
  signUpInputFirstName,
  signUpInputLastName,
  signUpSelectCountry,
  signUpSetStep,
  signUpSetPasswordErrors,
  signUpResetState,
} from '../../redux/actions/sign-up';
import validator from 'validator';
import { useHistory } from 'react-router-dom';
import { isStringHasNumber } from '../../helpers/index';
import { userActions } from '../../redux/actions/registrationActions';
import WarningIcon from '../../assets/images/ui-icons/warningIcon.png';
import RedCloseIcon from '../../assets/images/ui-icons/red-close.png';

const options = [
  { value: 'PL', label: 'Poland' },
  { value: 'BY', label: 'Belarus' },
  { value: 'SP', label: 'Spain' },
];

const SignUp = ({
  signUpData,
  register,
  dispatchSignUpInputEmail,
  dispatchSignUpInputFirstName,
  dispatchSignUpInputLastName,
  dispatchSignUpSelectCountry,
  //dispatchSignUpSetStep,
  dispatchSignUpSetPasswordErrors,
  dispatchSignUpResetState,
}) => {
  const [showPassword, setShowPassword] = useState(false);
  const [password, setPassword] = useState('');
  const [showConfirmedPassword, setShowConfirmedPassword] = useState(false);
  const [confirmedpassword, setConfirmedPassword] = useState('');
  const [isPasswordsMatches, setIsPasswordMatches] = useState(false);
  const [showError, setShowError] = useState(false);

  useEffect(() => {
    return () => {
      dispatchSignUpResetState();
    };
  }, [dispatchSignUpResetState]);

  useEffect(() => {
    if (confirmedpassword.length > 0 && confirmedpassword === password) {
      setIsPasswordMatches(true);
    } else {
      setIsPasswordMatches(false);
    }
  }, [confirmedpassword, password]);

  useEffect(() => {
    if (signUpData?.error?.message) {
      setShowError(true);
    }
  }, [signUpData?.error?.message]);

  const history = useHistory();

  const signUpEmailHandler = (e) => {
    const email = e.target.value;
    if (validator.isEmpty(email) || !validator.isEmail(email)) {
      return dispatchSignUpInputEmail({
        email,
        error: { message: 'Email must be formatted correctly' },
      });
    }
    dispatchSignUpInputEmail({ email, error: null });
  };

  const signUpFirstAndLastNameHandler = (e) => {
    const key = e.target.name;
    const value = validator.trim(e.target.value);

    if (key === 'firstName') {
      if (validator.isEmpty(value) || isStringHasNumber(value)) {
        return dispatchSignUpInputFirstName({
          firstName: value,
          error: {
            message: 'Should contain only letters',
          },
        });
      }
      if (value.length > 16) {
        return dispatchSignUpInputFirstName({
          firstName: value,
          error: {
            message: 'Use shortened form your name',
          },
        });
      }
      return dispatchSignUpInputFirstName({ firstName: value });
    }

    if (key === 'lastName') {
      if (validator.isEmpty(value) || isStringHasNumber(value)) {
        return dispatchSignUpInputLastName({
          lastName: value,
          error: {
            message: 'Should contain only letters',
          },
        });
      }

      return dispatchSignUpInputLastName({ lastName: value });
    }
  };

  const signUpCountryHandler = (e) => {
    dispatchSignUpSelectCountry({ value: e.value, label: e.label });
  };

  // const setSignUpStep = (step) => {
  //   dispatchSignUpSetStep({ step: step });
  // };

  const signUpInputPasswordHandler = (e) => {
    const password = validator.trim(e.target.value);

    const passwordErrors = {
      hasMin15Char: {
        status: true,
        message: '',
      },
      hasMin8Char: {
        status: true,
        message: '',
      },
      hasNumber: {
        status: true,
        message: '',
      },
      hasLowerCaseLetter: {
        status: true,
        message: '',
      },
    };

    if (password.length < 8) {
      passwordErrors.hasMin8Char.status = false;
      passwordErrors.hasMin8Char.message =
        'Password is too short(minimum is 8 characters)';
    }
    if (password.length < 15) {
      passwordErrors.hasMin15Char.status = false;
      passwordErrors.hasMin15Char.message =
        'Your password must be at least 15 characters';
    }
    // if (password.search(/[a-z]/i) < 0) {
    //   errors.push('Your password must contain at least one letter.');
    // }
    if (password.search(/[0-9]/) < 0) {
      passwordErrors.hasNumber.status = false;
      passwordErrors.hasNumber.message =
        'Your password must contain at least one digit';
    }
    if (password.search(/[a-z]/) < 0) {
      passwordErrors.hasLowerCaseLetter.status = false;
      passwordErrors.hasLowerCaseLetter.message =
        'Your password must contain at least one lowercase letter';
    }
    // if (password.search(/[A-Z]/) < 0) {
    //   errors.push('Your password must contain at least one uppercase letter.');
    // }
    setPassword(password);
    dispatchSignUpSetPasswordErrors({ errors: passwordErrors });
  };
  const toggleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const signUpInputConfirmedPasswordHandler = (e) => {
    setConfirmedPassword(e.target.value);
  };
  const toggleShowConfirmedPassword = () => {
    setShowConfirmedPassword(!showConfirmedPassword);
  };

  const thirdStepBtnClickHandler = () => {
    history.push('/login');
  };

  const createAccount = () => {
    const { firstName, lastName, email } = signUpData;
    register(firstName, lastName, email, password, confirmedpassword);
  };

  const onCloseErrorClick = () => {
    setShowError(false);
  };

  const signUpSteps = {
    1: (
      <FirstStep
        signUpData={signUpData}
        options={options}
        signUpEmailHandler={signUpEmailHandler}
        signUpFirstAndLastNameHandler={signUpFirstAndLastNameHandler}
        signUpCountryHandler={signUpCountryHandler}
        currentStep={signUpData.step}
        setSignUpStep={createAccount}
        password={password}
        setPassword={signUpInputPasswordHandler}
        confirmedPassword={confirmedpassword}
        setConfirmedPassword={signUpInputConfirmedPasswordHandler}
        showPassword={showPassword}
        setShowPassword={toggleShowPassword}
        showConfirmedPassword={showConfirmedPassword}
        setShowConfirmedPassword={toggleShowConfirmedPassword}
        isPasswordsMatches={isPasswordsMatches}
        loader={signUpData.loader}
      />
    ),
    2: (
      <FinishRegistrationStep
        email={signUpData.email}
        clickHandler={thirdStepBtnClickHandler}
      />
    ),
  };

  return (
    <div className={styles.signUp}>
      <div className={`${styles.signUp__error} ${showError && styles.show}`}>
        <div className={styles.signUp__error__message}>
          {signUpData?.error?.message}
          <div className={styles.signUp__error__message__icon}>
            <img
              src={WarningIcon}
              alt={`Error icon: ${signUpData?.error?.message}`}
            />
          </div>
          <div
            className={styles.signUp__error__message__close}
            onClick={onCloseErrorClick}
          >
            <img src={RedCloseIcon} alt="close icon" />
          </div>
        </div>
      </div>

      <div className={styles.signUp__wrap}>
        <div className={styles.signUp__logo}>
          <img src={Logo} alt="Skillvox logo" />
        </div>
        <div className={styles.signUp__title}>create skillvox account</div>

        {signUpSteps[signUpData.step]}
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  // TODO

  const signUpDataInitial = state.signUpData;

  const passwordValidationFailed = {
    status: false,
    message: '',
  };
  const { hasMin8Char, hasMin15Char, hasNumber, hasLowerCaseLetter } =
    signUpDataInitial.passwordErrors;

  if (
    !hasMin8Char.status ||
    // !hasMin15Char.status ||
    !hasNumber.status ||
    !hasLowerCaseLetter.status
  ) {
    passwordValidationFailed.status = true;
    if (!hasMin8Char.status) {
      passwordValidationFailed.message = hasMin8Char.message;
    } else if (!hasNumber.status) {
      passwordValidationFailed.message = hasNumber.message;
    } else if (!hasLowerCaseLetter.status) {
      passwordValidationFailed.message = hasLowerCaseLetter.message;
    } else {
      passwordValidationFailed.message =
        'Something went wrong. Please try again';
    }
  }
  const isSignUpFirstStepBtnDisabled =
    !signUpDataInitial.email ||
    signUpDataInitial.emailErrors.length > 0 ||
    !signUpDataInitial.firstName ||
    signUpDataInitial.firstNameErrors.length > 0 ||
    !signUpDataInitial.lastName ||
    signUpDataInitial.lastNameErrors.length > 0;

  const signUpData = {
    firstName: signUpDataInitial.firstName,
    lastName: signUpDataInitial.lastName,
    firstNameErrors: signUpDataInitial.firstNameErrors,
    firstNameValidationFailed:
      signUpDataInitial.firstName &&
      signUpDataInitial.firstNameErrors.length > 0,
    lastNameErrors: signUpDataInitial.lastNameErrors,
    lastNameValidationFailed:
      signUpDataInitial.lastName && signUpDataInitial.lastNameErrors.length > 0,
    email: signUpDataInitial.email,
    emailErrors: signUpDataInitial.emailErrors,
    emailValidationFailed:
      signUpDataInitial.email && signUpDataInitial.emailErrors.length > 0,
    isSignUpFirstStepBtnDisabled,
    country: {
      label: signUpDataInitial.country.label,
      value: signUpDataInitial.country.value,
    },
    passwordErrors: {
      hasMin8Char: hasMin8Char,
      hasMin15Char: hasMin15Char,
      hasNumber: hasNumber,
      hasLowerCaseLetter: hasLowerCaseLetter,
    },
    passwordValidationFailed,
    step: signUpDataInitial.step,
    loader: signUpDataInitial.loader,
    error: signUpDataInitial.error,
    isAuth: signUpDataInitial.isAuth,
  };
  return { signUpData };
};

const mapDispatchToProps = {
  register: userActions.register,
  dispatchSignUpInputEmail: signUpInputEmail,
  dispatchSignUpInputFirstName: signUpInputFirstName,
  dispatchSignUpInputLastName: signUpInputLastName,
  dispatchSignUpSelectCountry: signUpSelectCountry,
  dispatchSignUpSetStep: signUpSetStep,
  dispatchSignUpSetPasswordErrors: signUpSetPasswordErrors,
  dispatchSignUpResetState: signUpResetState,
};

export default connect(mapStateToProps, mapDispatchToProps)(SignUp);
