import React from 'react';
import styles from './style.module.scss';

const Checkbox = ({
  addClass,
  label,
  onChangeHandler,
  name,
  checked,
  value,
  labelTextColor,
  backgroundColor,
}) => (
  <label className={styles['checkbox']}>
    <input
      checked={checked}
      name={name}
      type="checkbox"
      value={value}
      className={`${styles['checkbox__input']} ${
        backgroundColor ? styles[`checkbox__input__${backgroundColor}`] : ''
      } ${checked ? styles['checkbox__input_checked'] : ''}`}
      onChange={onChangeHandler}
    />
    <span
      className={`${styles['checkbox__text']} ${
        labelTextColor ? styles['checkbox__text__' + labelTextColor] : ''
      }`}
      dangerouslySetInnerHTML={{ __html: label }}
    ></span>
  </label>
);

export default Checkbox;
