import actionTypes from '../constants';

export const setHeaderMainMenuMenuSize = (payload) => {
  return {
    type: actionTypes.SET_HEADER_MAIN_MENU_SIZE,
    payload,
  };
};

export const setHeaderMainMenuMenuType = (payload) => {
  return {
    type: actionTypes.SET_HEADER_MAIN_MENU_TYPE,
    payload,
  };
};
