import actionTypes from '../constants';

const initialState = {
  brand_name: null,
  brand_id: null,
  brand_logo: null,
  currency_symbol: null,
  preferred_currency: null,
  communities: [],
  activeCommunity: {
    isOpen: false,
    community_id: null,
    community_title: null,
    large_community_icon_full_color: '',
    categoryImage: null,
    sponsorLogo: null,
    regions: [],
  },
  loader: false,
  error: null,
};

export default function categorygridReducer(state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case actionTypes.LOAD_SPONSOR_COMMUNITIES_START:
      return {
        ...state,
        loader: true,
      };

    case actionTypes.LOAD_SPONSOR_COMMUNITIES_SUCCESS: {
      const {
        brand_id,
        brand_name,
        brand_logo,
        community,
        currency_symbol,
        preferred_currency,
      } = payload;
      return {
        ...state,
        brand_id,
        brand_name,
        brand_logo,
        currency_symbol,
        preferred_currency,
        communities: community,
        loader: false,
        error: null,
      };
    }

    case actionTypes.SET_ACTIVE_COMMUNITY: {
      const {
        community_id,
        community_title,
        large_community_icon_full_color,
        brand_logo,
        regions,
        categoryImage,
        sponsorLogo,
        currency_symbol,
        preferred_currency,
      } = payload;
      return {
        ...state,
        activeCommunity: {
          isOpen: true,
          community_id,
          community_title,
          brand_logo,
          currency_symbol,
          preferred_currency,
          large_community_icon_full_color,
          categoryImage,
          sponsorLogo,
          regions,
        },
      };
    }

    case actionTypes.UPDATE_COMMUNITIES: {
      const { brand_id, brand_name, community_id, community_title, regions } =
        payload;
      const changedCommunity = state.communities.find(
        (com) => Number(com.community_id) === Number(community_id)
      );
      const filteredCommunities = state.communities.filter(
        (el) => Number(el.community_id) !== Number(community_id)
      );
      const newCommunities = [
        ...filteredCommunities,
        { ...changedCommunity, regions },
      ];

      return {
        ...state,
        brand_id,
        brand_name,
        community_title,
        communities: newCommunities,
        activeCommunity: {
          ...state.activeCommunity,
          regions,
        },
        loader: false,
      };
    }

    case actionTypes.RESET_ACTIVE_COMMUNITY: {
      return {
        ...state,
        activeCommunity: {
          isOpen: false,
          community_id: null,
          community_title: null,
          categoryImage: null,
          brand_logo: null,
          sponsorLogo: null,
          regions: [],
        },
      };
    }
    case actionTypes.LOAD_SPONSOR_COMMUNITIES_ERROR: {
      const { error } = payload;
      return {
        ...state,
        loader: false,
        error,
      };
    }

    default:
      return state;
  }
}
