import React from 'react';
import styles from './styles.module.scss';



const RegionsList = ({title, firstColumn, secondColumn, thirdColumn, fourthColumn}) => {
    return (
        <div className={`${styles["countriesList"]} ${fourthColumn ? styles.fourthColumn : ""}`}>
        <div className={`${styles["countriesList__title"]}`}>
            {title}
        </div>
        <div className={`${styles["countriesList__list"]}`}>
            {firstColumn && <div className={`${styles["countriesList__column"]}`}>
                {firstColumn.map(el => {
                    return (
                        <div key={el.id} className={`${styles["countriesList__element"]}`}>
                            <div className={`${styles["countriesList__letter"]}`}>{el.letter}</div>
                            <ul className={`${styles["countriesList__countries"]}`}>                   
                                {el.countries.map((country, idx) => <li key={idx} className={`${styles["countriesList__singleCountry"]}`}>{country}</li>)}
                            </ul>
                        </div>
                    )    
                })}
            </div>
            }
           {secondColumn && <div className={`${styles["countriesList__column"]}`}>
                {secondColumn.map(el => {
                    return (
                        <div key={el.id} className={`${styles["countriesList__element"]}`}>
                            <div className={`${styles["countriesList__letter"]}`}>{el.letter}</div>
                            <ul className={`${styles["countriesList__countries"]}`}>                   
                                {el.countries.map((country, idx) => <li key={idx} className={`${styles["countriesList__singleCountry"]}`}>{country}</li>)}
                            </ul>
                        </div>
                    )    
                })}
            </div>
            }
           {thirdColumn && <div className={`${styles["countriesList__column"]}`}>
                { thirdColumn.map(el => {
                    return (
                        <div key={el.id} className={`${styles["countriesList__element"]}`}>
                            <div className={`${styles["countriesList__letter"]}`}>{el.letter}</div>
                            <ul className={`${styles["countriesList__countries"]}`}>                   
                                {el.countries.map((country, idx) => <li key={idx} className={`${styles["countriesList__singleCountry"]}`}>{country}</li>)}
                            </ul>
                        </div>
                    )    
                })}
            </div>}

            {fourthColumn && <div className={`${styles["countriesList__column"]}`}>
                {fourthColumn.map(el => {
                    return (
                        <div key={el.id} className={`${styles["countriesList__element"]}`}>
                            <div className={`${styles["countriesList__letter"]}`}>{el.letter}</div>
                            <ul className={`${styles["countriesList__countries"]}`}>                   
                                {el.countries.map((country, idx) => <li key={idx} className={`${styles["countriesList__singleCountry"]}`}>{country}</li>)}
                            </ul>
                        </div>
                    )    
                })}
            </div>}
            
        </div>
        </div>
    )
}
export default RegionsList