import React, { useState, useEffect } from 'react';
import Logo from '../../atoms/Logo';
import Nav from '../../atoms/Nav';
import { Link, useLocation, useHistory } from 'react-router-dom';
import SubmenuImg1 from '../../../assets/images/submenu1.png';
import SubmenuImg2 from '../../../assets/images/submenu2.png';
import SubmenuImg3 from '../../../assets/images/submenu3.png';
import SubmenuImg4 from '../../../assets/images/submenu4.png';
import MenuArrowDownBlack from '../../../assets/images/ui-icons/menu-arrow-down-black.png';
import BackArrowMainMenuImg from '../../../assets/images/ui-icons/back_arrow_mainMenu.png';
import styles from './styles.module.scss';
import Bucket from '../Bucket';
import UserMenu from '../UserMenu';
import { toggleSubmenu, setAppBackgroundColor } from '../../../redux/actions';
import {
  setHeaderMainMenuMenuSize,
  setHeaderMainMenuMenuType,
} from '../../../redux/actions/headerMenu'; //todo both import
import { connect } from 'react-redux';

const navLinks = [
  { title: 'Be a Sponsor', url: '/become-sponsor' },
  { title: 'Who we are', url: '/who-we-are' },
  {
    title: 'My Account',
    image: MenuArrowDownBlack,
    url: '/my-account',
    submenu: [
      {
        title: 'Communities',
        url: '/my-account/communities',
        description: 'Choose Communtiies you wish  to Sponsor.',
        image: SubmenuImg1,
      },
      {
        title: 'Account Summary',
        url: '/my-account/summary',
        description: 'View your invoicing and payment  history.',
        image: SubmenuImg2,
      },
      {
        title: 'Audience Analytics',
        url: '/my-account/audience',
        description:
          'Get Analytics on who your sponsored impressions are reaching.',
        image: SubmenuImg3,
      },
      {
        title: 'My Personal Details',
        url: '/my-account/details',
        description: "Manage any personal information you've provided to us.",
        image: SubmenuImg4,
      },
    ],
  },
  { title: 'Roadmap', url: '/roadmap' },
  { title: 'Blog', url: '/blog' },
];

const headerStyle = {
  DEFAULT: 'DEFAULT',
  MY_ACCOUNT: 'MY_ACCOUNT',
};

const Header = ({
  mainMenuType,
  isMainMenuMiniSize,
  isHeaderPaymentFixed,
  isAuth,
  userData,
  dispatchToggleSubmenu,
  dispatchSetHeaderMainMenuMenuSize,
  dispatchSetAppBackgroundColor,
  dispatchSetHeaderMainMenuMenuType,
}) => {
  const [showSubmenu, setShowSubmenu] = useState(null);
  const [activeMenuPoint, setActiveMenuPoint] = useState(null);

  const { pathname } = useLocation();
  const history = useHistory();

  useEffect(() => {
    document.addEventListener('scroll', setHeaderMenuHeight);
    return () => document.removeEventListener('scroll', setHeaderMenuHeight);
  }, []);

  useEffect(() => {
    if (pathname) {
      const isAdminLocation = pathname.split('/').includes('my-account');
      if (isAdminLocation) {
        dispatchSetAppBackgroundColor({ backgroundColor: '#ebebeb' });
        dispatchSetHeaderMainMenuMenuType({ type: headerStyle.MY_ACCOUNT });
      } else {
        dispatchSetAppBackgroundColor({ backgroundColor: '#ffffff' });
        dispatchSetHeaderMainMenuMenuType({ type: headerStyle.DEFAULT });
      }
    }
  }, [
    dispatchSetAppBackgroundColor,
    dispatchSetHeaderMainMenuMenuType,
    pathname,
  ]);

  function setHeaderMenuHeight() {
    const scrollFromTop = window.scrollY;   
    return scrollFromTop >= 1 && !isMainMenuMiniSize
      ? dispatchSetHeaderMainMenuMenuSize(true)
      : dispatchSetHeaderMainMenuMenuSize(false);
  }

  const subMenuHandler = (menu, menuActivePoint) => {
    setActiveMenuPoint(menuActivePoint);
    if (showSubmenu) {
      dispatchToggleSubmenu({ data: false });
      setShowSubmenu(null);
      return;
    }
    dispatchToggleSubmenu({ data: true });
    setShowSubmenu(menu || null);
    return;
  };

  const onSubmenuPointClick = () => {
    setShowSubmenu(false);
  };

  const onBackButtonClick = () => {
    history.push('/');
  };

  return (
    <div
      onMouseLeave={() => setShowSubmenu(null)}
      className={`${styles.header} ${
        isMainMenuMiniSize ? styles.miniHeader : ''
      } ${
        mainMenuType === headerStyle.DEFAULT
          ? ''
          : headerStyle.MY_ACCOUNT
          ? styles.myAccount
          : ''
      }`}
    >
      <div
        className={`${styles.header__mainMenu} ${
          isMainMenuMiniSize && !isHeaderPaymentFixed ? styles.withShadow : ''
        } ${
          mainMenuType === headerStyle.DEFAULT
            ? ''
            : headerStyle.MY_ACCOUNT
            ? styles.myAccount
            : ''
        }`}
      >
        <header
          className={`${styles.header__wrap} ${
            mainMenuType === headerStyle.MY_ACCOUNT ? styles.myAccount : ''
          }`}
        >
          <div
            className={`${styles['header__logo']} ${
              mainMenuType === headerStyle.MY_ACCOUNT ? styles.myAccount : ''
            }`}
          >
            <Logo />
          </div>
          {!isAuth && pathname !== '/sign-up' && (
            <div className={styles['header__signUp']}>
              <Link className={styles.header__signUp__link} to="/sign-up">
                Sign up
              </Link>
            </div>
          )}
          {isAuth && (
            <>
              <div
                className={`${styles['header__nav']} ${
                  mainMenuType === headerStyle.MY_ACCOUNT
                    ? styles.myAccount
                    : ''
                }`}
              >
                <Nav
                  links={navLinks}
                  clickHandler={subMenuHandler}
                  activeMenuPoint={activeMenuPoint}
                  menuType={mainMenuType}
                />
              </div>
             
              {mainMenuType === headerStyle.MY_ACCOUNT && (
                <div
                  className={styles['header__backButton']}
                  onClick={onBackButtonClick}
                >
                  <div className={styles['header__backButton__wrap']}>
                    <img
                      src={BackArrowMainMenuImg}
                      alt="back to main page arrow"
                    />
                  </div>
                 
                </div>
              )}
             
              <div
                className={`${styles['header__bucket']} ${
                  mainMenuType === headerStyle.MY_ACCOUNT
                    ? styles.myAccount
                    : ''
                }`}
              >
                { mainMenuType === headerStyle.MY_ACCOUNT && <span className={styles['header__myTitleAccount']}>My Account</span>}
                <Bucket />
              </div>
             
              <div
                className={`${styles['header__userMenu']} ${
                  mainMenuType === headerStyle.MY_ACCOUNT
                    ? styles.myAccount
                    : ''
                }`}
              >
                <UserMenu
                  userInitials={userData.userInitials}
                  size={
                    mainMenuType === headerStyle.MY_ACCOUNT ? 'mini' : 'default'
                  }
                />
              </div>

              <div
                className={`${styles['header__submenu']} ${
                  styles[
                    showSubmenu && showSubmenu.length ? 'show-submenu' : ' '
                  ]
                }`}
              >
                <ul>
                  {showSubmenu &&
                    showSubmenu.length &&
                    showSubmenu.map((el, idx) => {
                      return (
                        <li
                          key={idx}
                          className={styles['header__submenu-item']}
                        >
                          <Link to={el.url} onClick={onSubmenuPointClick}>
                            <div
                              className={styles['header__submenu-item-wrap']}
                            >
                              <div
                                className={
                                  styles['header__submenu-image-wrapper']
                                }
                              >
                                <img src={el.image} alt={el.title} />
                              </div>
                              <div className={styles['header__submenu-title']}>
                                {el.title}
                              </div>
                              <div
                                className={
                                  styles['header__submenu-description']
                                }
                              >
                                {el.description}
                              </div>
                            </div>
                          </Link>
                        </li>
                      );
                    })}
                </ul>
              </div>
            </>
          )}
        </header>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    mainMenuType: state.headerMenu.mainMenu.type,
    isMainMenuMiniSize: state.headerMenu.mainMenu.isMiniSize,
    isHeaderPaymentFixed: state.headerPayment.isHeaderPaymentFixed,
    isAuth: state.session.isAuth,
    userData: state.session.user,
  };
};

const mapDispatchToProps = {
  dispatchToggleSubmenu: toggleSubmenu,
  dispatchSetHeaderMainMenuMenuSize: setHeaderMainMenuMenuSize,
  dispatchSetAppBackgroundColor: setAppBackgroundColor,
  dispatchSetHeaderMainMenuMenuType: setHeaderMainMenuMenuType,
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);
