import { loginStart, loginSuccess, loginError } from '../../actions/login';
import { saveToken, saveUser } from '../../../http/localStorage';

export const logInUser = (email, password) => async (dispatch, getState) => {
  const requestOptions = {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    credentials: 'include',
    body: JSON.stringify({ email, password }),
  };
  try {
    dispatch(loginStart());
    const response = await fetch(
      `https://voxdigital.by:4000/login`,
      requestOptions
    );
    if (!response || response.status === 500) {
      dispatch(
        loginError({
          message: "Please re-check the credentials you've provided",
          statusCode: 401,
        })
      );
      return;
    }
    if (response.status === 401) {
      dispatch(
        loginError({
          message: "Please re-check the credentials you've provided",
          statusCode: 401,
        })
      );
      return;
    }
    const responseData = await response.json();
    if (!responseData.isAuth) {
      dispatch(
        loginError({
          message: "Please re-check the credentials you've provided",
          statusCode: 401,
        })
      );
      return;
    }
    const {
      user: { first_name, last_name },
      accessToken,
      isAuth,
      email: userEmail,
      brand_id,
    } = responseData;

    const currentDate = new Date();
    const expiresIn = new Date(currentDate.getTime() + 35000).getTime();

    saveToken(accessToken, expiresIn);

    saveUser({ first_name, last_name, email: userEmail, brand_id });

    dispatch(
      loginSuccess({
        first_name,
        last_name,
        email: userEmail,
        brand_id,
        isAuth,
      })
    );
  } catch (error) {
    dispatch(loginError({ message: error.message, statusCode: error.status }));
    throw error;
  }
};
