import actionTypes from '../constants';

export const signUpInputEmail = (payload) => {
  return {
    type: actionTypes.SIGN_UP_INPUT_EMAIL,
    payload,
  };
};

export const signUpInputFirstName = (payload) => {
  return {
    type: actionTypes.SIGN_UP_INPUT_FIRST_NAME,
    payload,
  };
};

export const signUpInputLastName = (payload) => {
  return {
    type: actionTypes.SIGN_UP_INPUT_LAST_NAME,
    payload,
  };
};

export const signUpSelectCountry = (payload) => {
  return {
    type: actionTypes.SIGN_UP_SELECT_COUNTRY,
    payload,
  };
};

export const signUpSetStep = (payload) => {
  return {
    type: actionTypes.SIGN_UP_SET_STEP,
    payload,
  };
};

export const signUpSetPasswordErrors = (payload) => {
  return {
    type: actionTypes.SIGN_UP_SET_PASSWORD_ERRORS,
    payload,
  };
};

export const signUpResetState = () => {
  return {
    type: actionTypes.SIGN_UP_RESET_STATE,
  };
};

export const signUpStart = () => {
  return { type: actionTypes.SIGN_UP_START };
};
export const signUpSuccess = (user) => {
  return { type: actionTypes.SIGN_UP_SUCCESS, user };
};
export const signUpError = (payload) => {
  return { type: actionTypes.SIGN_UP_ERROR, payload };
};
