import { combineReducers } from 'redux';
import { cart } from './cart';
import { headerMenu } from './headerMenu';
import { appInfo } from './appInfo';
import { headerPayment } from '../../components/molecules/HeaderPayment/reducer';
import { signUpData } from './sign-up';
import session from './sessionreducer';
import brand_id from './homepagereducer';
import categorygrid from './categorygridreducer';

export default combineReducers({
  appInfo,
  signUpData,
  cart,
  headerMenu,
  headerPayment,
  session,
  brand_id,
  categorygrid,
});
