import styles from './styles.module.scss';

const Backdrop = ({
  show,
  clickhandler,
  transparent = true,
  appearance = 'default',
}) => {
  return (
    <div
      className={`${styles.backdrop} ${show ? styles.show : ''} ${
        styles[appearance]
      }`}
      onClick={clickhandler}
    ></div>
  );
};

export default Backdrop;
