import { useState, useRef, useEffect } from 'react';
import styles from './styles.module.scss';
import videoPreviewImg from '../../assets/images/videoControllers/videoPreview.jpg';
import skillvoxVideo from '../../assets/videos/skillvox_mainPage.mp4';
import videoPlayBtn from '../../assets/images/videoControllers/playBtn.png';
import videoPauseBtn from '../../assets/images/videoControllers/pauseBtn.svg';
import showVideoFullSize from '../../assets/images/videoControllers/showFullSize.png';
import { useHistory } from 'react-router-dom';
import { connect } from 'react-redux';





const WhoWeAre = () => {
    const [isVideoPlaying, setIsVideoPlaying] = useState(false);
    const videoRef = useRef(null);
    const [position, setPosition] = useState(0);
    const history = useHistory();
    const [move, setMove] = useState(0);
    const [screenWidth, setscreenWidth] = useState(1);

  
    useEffect(()=>{
        const clientScreenWidth =
            window.innerWidth ||
            document.documentElement.clientWidth ||
            document.body.clientWidth;
         if(clientScreenWidth < 768) return setscreenWidth(6);      
    },[])

    useEffect(() => {        
        setTimeout(() => setMove(-1),500);
        document.addEventListener('scroll', shouldShowBlock);    
          return () => {
            document.removeEventListener('scroll', shouldShowBlock);     
          };
    },[]);

    function shouldShowBlock(e) {    
        const scrollFromTop = window.scrollY; 
        console.log(scrollFromTop);    
        setPosition(scrollFromTop);              
    };        

    const playVideoHandler = () => {
        if (videoRef.current.paused === true) {
          // Play the video
          videoRef.current.play();
          setIsVideoPlaying(true);
        } else {
          // Pause the video
          videoRef.current.pause();
          setIsVideoPlaying(false);
        }
    };
    const showVideoFullScreenHandler = () => {
        if (videoRef.current.requestFullscreen) {
          videoRef.current.requestFullscreen();
        } else if (videoRef.current.mozRequestFullScreen) {
          videoRef.current.mozRequestFullScreen(); // Firefox
        } else if (videoRef.current.webkitRequestFullscreen) {
          videoRef.current.webkitRequestFullscreen(); // Chrome and Safari
        }
    };
    const one = position  >  move  && position < 1000 - 120*(screenWidth - 1);
    const three = position > 900 - 300*(screenWidth - 1) && position < 1800 - 180*(screenWidth - 1);
    const four = position > 1800/screenWidth ;
    return (
        <div className={styles.whoweare}>
            <div className={styles.whoweare__firstblock}>               
                <div className={styles.whoweare__firstblock__one}>
                    <p>Your <b>Skills</b></p>
                </div>
                <div className={styles.whoweare__firstblock__two}>
                    <p>Your <b>Narrative</b></p>
                </div>
                <div className={styles.whoweare__firstblock__title}>
                    <h1>
                        We're a new <br/> 
                        <span>social network</span> <br/> 
                        seeking to give <br/>
                        your Skills a Voice.
                    </h1>
                    <p>
                        We've designed Skillvox in the belief that our <br/>
                        Skills are a part of our identity and we have a <br/>
                        right to control that narrative

                    </p>
                    <button onClick={()=>history.push("/become-sponsor")}>GET STARTED</button>
                </div>
                <div className={styles.whoweare__firstblock__image}></div>
            </div>
            <div className={styles.whoweare__secondblock}>
                <div className={styles.whoweare__firstblock__videoBlock}>
                    <div className={styles.whoweare__firstblock__videoBlock__wrap}>
                        <div className={styles.whoweare__firstblock__videoBlock__videoMenu}>
                            <img
                                src={showVideoFullSize}
                                className={styles.whoweare__firstblock__videoBlock__full}
                                onClick={showVideoFullScreenHandler}
                                alt="paused video"
                            />
                            <span
                                className={styles.whoweare__firstblock__videoBlock__close}
                            />
                        </div>
                        <div className={styles.whoweare__firstblock__videoBlock__videoWrap}>
                            <video
                                poster={videoPreviewImg}
                                preload="auto"
                                ref={videoRef}
                                // controls={isVideoPlaying ? true : false}
                            >
                                <source src={skillvoxVideo} type="video/mp4"></source>
                                <p>
                                Your browser doesn't support HTML5 video.
                                <a href={skillvoxVideo}>Download</a> the video instead.
                                </p>
                            </video>
                            <div
                                className={`${
                                styles.whoweare__firstblock__videoBlock__controllers
                                } ${isVideoPlaying ? styles.showPauseBtn : ''}`}
                                onClick={playVideoHandler}
                            >
                                <button
                                className={
                                    isVideoPlaying
                                    ? styles.whoweare__firstblock__videoBlock__controllers__stopVideoBtn
                                    : styles.whoweare__firstblock__videoBlock__controllers__playVideoBtn
                                }
                                >
                                    <img
                                        src={isVideoPlaying ? videoPauseBtn : videoPlayBtn}
                                        alt="play/pause video"
                                    />
                                </button>
                            </div>
                        </div>
                    </div>    
                </div>          
                <h1 
                    className={styles.whoweare__secondblock__h}
                    style={{left: one ? (screenWidth === 6 ? '3vw' : '51%'): '90%',
                    opacity: one ? 1 : 0}}
                >
                    <span>Manage </span>your<span> Professional</span><br/>
                    Identity
                </h1>
                <p 
                   className={styles.whoweare__secondblock__p}
                   style={{left: one ? (screenWidth === 6 ? '3%' : '14%') : '-34%',
                   opacity: one ? 1 : 0}}
                >
                    <b>Take control</b> of your Professional presence.<br/>
                    <b>Share what</b> you're passionate about.<br/>
                    <b>Build</b> relationships.
                </p>    
                <button
                    onClick={()=>history.push("/become-sponsor")} 
                    className={styles.whoweare__secondblock__button}
                    style={{top: one ? '38vw': '48vw',opacity: one ? 1 : 0}}
                >
                    GET STARTED
                </button>
                <div 
                    className={styles.whoweare__secondblock__image}
                    style={{ backgroundPosition: one ? '0% 13vw': '0 150%',opacity: one ? 1 : 0}}
                />                
            </div>
            <div className={styles.whoweare__threeblock}>
                <div 
                    className={styles.whoweare__threeblock__image}
                    style={{backgroundPosition: three ? '90% 3vw': '80% 150%',opacity: three ? 1 : 0}}    
                />
                <h1 
                    className={styles.whoweare__threeblock__h}
                    style={{top: three ? (screenWidth === 6 ? '5%':'22%'): '90%',opacity: three ? 1 : 0}}            
                >
                    <span>Join</span> your colleagues,<br/> 
                    classmates and friends<br/> 
                    on Skillvox
                </h1>                
                <button 
                    onClick={()=>history.push("/become-sponsor")}
                    className={styles.whoweare__threeblock__button}
                    style={{top: three ? (screenWidth === 6 ? '30vw' : '47%'): 0 ,opacity: three ? 1 : 0}}    
                >GET STARTED</button>
                
            </div>
            <div className={styles.whoweare__fourblock}>
                <h2 
                    className={styles.whoweare__fourblock__head}
                    style={{transform: four ? (screenWidth === 6 ? 'translateY(0px)': 'translateY(0px)'): 'translateY(-40px)',opacity: four ? 1 : 0}}
                
                >
                    Looking to make your next <b>career move?</b>
                </h2>
                <h1 
                    className={styles.whoweare__fourblock__h}
                    style={{left: four ? (screenWidth === 6 ? '3%' : '14%'): '-24%',opacity: four ? 1 : 0}}
                >
                    Find the right <span>job</span><br/> 
                    or <span>internship</span> for you.
                </h1>
                <p 
                    className={styles.whoweare__fourblock__p}
                    style={{left: four ?  (screenWidth === 6 ? '3%' : '14%'): '-24%',opacity: four ? 1 : 0}}
                >
                    Utilise your skills and fulfill your potential.<br/>
                    Make your next career move now. Connect with <br/>
                    thousands of  top employers.
                </p>
                <button 
                    onClick={()=>history.push("/become-sponsor")}
                    className={styles.whoweare__fourblock__button}
                    style={{left: four ? (screenWidth === 6 ? '3%' : '14%' ): 0, opacity: four ? 1 : 0}}
                >GET STARTED</button>
                <div 
                    className={styles.whoweare__fourblock__image}
                    style={{backgroundPosition: four ? (screenWidth === 6 ? 'right center': 'left center') : 'left 520%',opacity: four ? 1 : 0}}            
                />

            </div>
        </div>

    )
}
export default connect()(WhoWeAre);