import { fetchWithAuth } from '../../../http/fetchWithAuth';
import {
  fetchCheckoutPageStart,
  fetchCheckoutPageSuccess,
  fetchCheckoutPageError,
  deleteRegionCheckoutPageStart,
  deleteRegionCheckoutPageSuccess,
  deleteRegionCheckoutPageError,
  updateCheckoutPageClientAddressStart,
  updateCheckoutPageClientAddressSuccess,
  updateCheckoutPageClientAddressError,
  setPartialPaymentCommunityStart,
  setPartialPaymentCommunitySuccess,
  setPartialPaymentCommunityError,
  undoPartialPaymentCommunityStart,
  undoPartialPaymentCommunitySuccess,
  undoPartialPaymentCommunityError,
} from '../../actions/bucket';
import { logoutUser } from '../logout';

export const getCheckoutPageData = () => async (dispatch) => {
  try {
    dispatch(fetchCheckoutPageStart());
    const response = await fetchWithAuth(
      'https://voxdigital.by:4000/checkout',
      { method: 'GET' }
    );
    if (!response || response.status === 500) {
      fetchCheckoutPageError({
        message: 'Something went wrong. Please try again',
        statusCode: 500,
      });
      dispatch(logoutUser());
      return;
    }
    const responseData = await response.json();
    dispatch(fetchCheckoutPageSuccess(responseData.payload));
  } catch (err) {
    dispatch(fetchCheckoutPageError({ message: err.message }));
    console.log(err);
    throw err;
  }
};

export const deleteRegionFromCart =
  (communityId, regionId) => async (dispatch) => {
    try {
      dispatch(deleteRegionCheckoutPageStart());
      const graphqlQuery = {
        query: `
        {
          getPayNowCart(regionId: ${regionId}, communityId: ${communityId}, chosen: false) {
            brand_name
            brand_id
            campaign_title
            total_outstanding
            communities{
              community_title
              community_id
              basic_community_icon_hover
              regions{
                name
                region_id
                price
                payment_status
                chosen
                countries_summary  {
                  country_tally
                  region_summary_text
                }
              }
            }
            billing_address {
              address_line1
              address_line2
              city_town
              region
              postal_code
              country
            }
          }
        }`,
      };
      const response = await fetchWithAuth('https://voxdigital.by:4000/query', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(graphqlQuery),
      });
      if (!response || response.status === 500) {
        deleteRegionCheckoutPageError({
          message: 'Something went wrong. Please try again',
          statusCode: 500,
        });
        dispatch(logoutUser());
        return;
      }
      const responseData = await response.json();
      dispatch(
        deleteRegionCheckoutPageSuccess(responseData?.data?.getPayNowCart)
      );
    } catch (err) {
      dispatch(deleteRegionCheckoutPageError({ message: err.message }));
      console.log(err);
      dispatch(logoutUser());
      throw err;
    }
  };

export const updateClientCheckoutAddress = (address) => async (dispatch) => {
  try {
    dispatch(updateCheckoutPageClientAddressStart());
    const graphqlQuery = {
      query: `
      mutation {
        UpdateInvoicingAddress(
          telephone: "${address.telephone || ''}"
          address_line1: "${address.address_line1 || ''}"
          address_line2: "${address.address_line2 || ''}"
          city_town: "${address.city_town || ''}"
          region: "${address.region || ''}"
          postal_code: "${address.postal_code || ''}"
          country: "${address.country || ''}"
        )
         {
          brand_name
          brand_id
          campaign_title
          total_outstanding
          communities{
            community_title
            community_id
            basic_community_icon_hover
            regions{
              name
              region_id
              price
              payment_status
              chosen
              countries_summary  {
                country_tally
                region_summary_text
              }
            }
          }
          billing_address {
            address_line1
            address_line2
            city_town
            region
            postal_code
            country
          }
        }
      }`,
    };

    const response = await fetchWithAuth('https://voxdigital.by:4000/query', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(graphqlQuery),
    });
    if (!response || response.status === 500) {
      dispatch(
        updateCheckoutPageClientAddressError({
          message: 'Something wend wrong, please try againg',
          statusCode: response.status || 500,
        })
      );
      dispatch(logoutUser());

      return;
    }
    if (response.status === 401) {
      dispatch(
        updateCheckoutPageClientAddressError({
          message:
            'You are not authorized. Please enter in your account again.',
          statusCode: 401,
        })
      );
      dispatch(logoutUser());

      return;
    }
    const responseData = await response.json();
    dispatch(
      updateCheckoutPageClientAddressSuccess(
        responseData?.data?.UpdateInvoicingAddress
      )
    );
    return responseData?.data?.UpdateInvoicingAddress;
  } catch (err) {
    dispatch(
      updateCheckoutPageClientAddressError({
        message: err.message,
        statusCode: 500,
      })
    );
    return;
  }
};

