import actionTypes from '../constants';

const initialState = {
  firstName: '',
  lastName: '',
  email: '',
  country: {
    label: 'Poland',
    value: 'PL',
  },
  step: 1,
  isAuth: false,
  loader: false,
  error: null,
  firstNameErrors: [],
  lastNameErrors: [],
  emailErrors: [],
  passwordErrors: {
    hasMin15Char: {
      status: false,
      message: '',
    },
    hasMin8Char: {
      status: false,
      message: '',
    },
    hasNumber: {
      status: false,
      message: '',
    },
    hasLowerCaseLetter: {
      status: false,
      message: '',
    },
  },
};

export const signUpData = (state = initialState, action) => {
  const { type, payload } = action;
  switch (type) {
    case actionTypes.SIGN_UP_INPUT_FIRST_NAME: {
      const { firstName, error } = payload;
      if (error) {
        return {
          ...state,
          firstName: firstName,
          firstNameErrors: [error],
        };
      }
      return {
        ...state,
        firstName: firstName,
        firstNameErrors: [],
      };
    }

    case actionTypes.SIGN_UP_INPUT_LAST_NAME: {
      const { lastName, error } = payload;
      if (error) {
        return {
          ...state,
          lastName: lastName,
          lastNameErrors: [error],
        };
      }
      return {
        ...state,
        lastName: lastName,
        lastNameErrors: [],
      };
    }

    case actionTypes.SIGN_UP_INPUT_EMAIL: {
      const { email, error } = payload;
      if (error) {
        return {
          ...state,
          email: email,
          emailErrors: [error],
        };
      }
      return {
        ...state,
        email: email,
        emailErrors: [],
      };
    }

    case actionTypes.SIGN_UP_SELECT_COUNTRY: {
      const { label, value } = payload;
      return {
        ...state,
        country: {
          label,
          value,
        },
      };
    }

    case actionTypes.SIGN_UP_SET_PASSWORD_ERRORS: {
      const { errors } = payload;
      return {
        ...state,
        passwordErrors: errors,
      };
    }

    case actionTypes.SIGN_UP_SET_STEP: {
      const { step } = payload;
      return {
        ...state,
        step: step,
      };
    }

    case actionTypes.SIGN_UP_START: {
      return { ...state, loader: true };
    }

    case actionTypes.SIGN_UP_SUCCESS: {
      return { ...state, loader: false, isAuth: true };
    }

    case actionTypes.SIGN_UP_ERROR: {
      const { message, status } = payload;
      return { ...state, loader: false, error: { message, status } };
    }

    case actionTypes.SIGN_UP_RESET_STATE: {
      return {
        ...initialState,
      };
    }

    default:
      return state;
  }
};
