import actionTypes from '../constants';

const initialState = {
  loader: false,
  products: [],
  partialPayment: {
    isActive: false,
    community: null,
    total_outstanding: 0,
    loader: false,
    error: null,
  },
  productsCount: 0,
  total_paid: 0,
  total_pledged: 0,
  total_outstanding: 0,
  currency_symbol: '',
  preferred_currency: '',
  symbol_placement: '',
  invoiceReceive: false,
  paymentDetails: {
    paymentStep: 1,
    billing_address: {
      address_line1: '',
      address_line2: '',
      city_town: '',
      region: '',
      postal_code: '',
      country: '',
      email_id: '',
      loader: false,
      error: false,
    },
    campaign_title: '',
    telephone: '',
  },
};

export const cart = (state = initialState, { type, payload }) => {
  switch (type) {
    case actionTypes.ADD_TO_BUCKET_START:
    case actionTypes.FETCH_CHECKOUT_PAGE_START:
    case actionTypes.REMOVE_FROM_BUCKET_START:
    case actionTypes.DELETE_REGION_CHECKOUT_PAGE_START: {
      return {
        ...state,
        loader: true,
      };
    }

    case actionTypes.SET_PARTIAL_PAYMENT_COMMUNITY_START:
    case actionTypes.UNDO_PARTIAL_PAYMENT_COMMUNITY_START: {
      return {
        ...state,
        partialPayment: {
          ...state.partialPayment,
          loader: true,
        },
      };
    }

    case actionTypes.UPDATE_BUCKET_PRODUCTS: {
      const {
        products,
        total_paid,
        total_pledged,
        total_outstanding,
        productsCount,
        symbol_placement,
      } = payload;

      return {
        ...state,
        products,
        productsCount,
        symbol_placement,
        total_outstanding: Number(total_outstanding),
        total_paid: Number(total_paid),
        total_pledged: Number(total_pledged),
        loader: false,
      };
    }

    case actionTypes.ADD_TO_BUCKET_SUCCESS: {
      const {
        community_id,
        regionData: { region_id, name, price, payment_status },
      } = payload;

      const communityInBuket = state.products.find(
        (el) => el.community_id === community_id
      );
      if (communityInBuket) {
        const newRegionsInCommunity = [
          ...communityInBuket.regions,
          { name, region_id, price, payment_status, chosen: true },
        ];
        const inBucketWithoutCurrentCommunity = state.products.filter(
          (el) => el.community_id !== community_id
        );
        const total_outstanding = newRegionsInCommunity.reduce((acc, el) => {
          return acc + el.price;
        }, 0);
        return {
          ...state,
          loader: false,
          products: [
            ...inBucketWithoutCurrentCommunity,
            { ...communityInBuket, regions: newRegionsInCommunity },
          ],
          productsCount: state.productsCount + 1,
          total_outstanding,
        };
      }

      return {
        ...state,
        loader: false,
      };
    }

    case actionTypes.REMOVE_FROM_BUCKET_SUCCESS: {
      const { community_id, regionId } = payload;

      const communityInBuket = state.products.find(
        (el) => el.community_id === community_id
      );
      if (communityInBuket) {
        const newRegionsInCommunity = communityInBuket.regions.filter(
          (el) => el.region_id !== regionId
        );
        const inBucketWithoutCurrentCommunity = state.products.filter(
          (el) => el.community_id !== community_id
        );
        const total_outstanding = newRegionsInCommunity.reduce((acc, el) => {
          return acc + Number(el.price);
        }, 0);
        return {
          ...state,
          loader: false,
          products: [
            ...inBucketWithoutCurrentCommunity,
            { ...communityInBuket, regions: newRegionsInCommunity },
          ],
          productsCount: state.productsCount - 1,
          total_outstanding,
        };
      }
      return {
        ...state,
        loader: false,
      };
    }

    case actionTypes.FETCH_CHECKOUT_PAGE_SUCCESS: {
      const {
        billing_address,
        campaign_title,
        community,
        currency_symbol,
        preferred_currency,
        symbol_placement,
        telephone,
        total_outstanding,
        total_paid,
        total_pledged,
      } = payload;

      let productsCount = 0;
      let newCommunities = [];
      if (community?.length) {
        community.map((el) =>
          el.regions.map((reg) => reg.chosen === true && productsCount++)
        );
        newCommunities = [...community].sort((a, b) =>
          a.community_title.localeCompare(b.community_title)
        );
      }

      return {
        ...state,
        loader: false,
        currency_symbol,
        preferred_currency,
        symbol_placement,
        total_outstanding,
        total_paid,
        total_pledged,
        paymentDetails: {
          ...state.paymentDetails,
          billing_address,
          campaign_title,
          telephone,
        },
        products: newCommunities,
        productsCount: productsCount,
      };
    }

    case actionTypes.FETCH_CHECKOUT_PAGE_ERROR: {
      const { message, statusCode } = payload;
      return {
        ...state,
        loader: false,
        error: {
          message,
          statusCode,
        },
      };
    }

    case actionTypes.DELETE_REGION_CHECKOUT_PAGE_SUCCESS: {
      const {
        billing_address,
        campaign_title,
        communities,
        currency_symbol,
        preferred_currency,
        symbol_placement,
        total_outstanding,
        total_paid,
        total_pledged,
      } = payload;

      let productsCount = 0;
      let newCommunities = [];
      if (communities?.length) {
        communities.map((el) =>
          el.regions.map((reg) => reg.chosen === true && productsCount++)
        );
        newCommunities = [...communities].sort((a, b) =>
          a.community_title.localeCompare(b.community_title)
        );
      }

      return {
        ...state,
        loader: false,
        currency_symbol,
        preferred_currency,
        symbol_placement,
        total_outstanding,
        total_paid,
        total_pledged,
        paymentDetails: {
          ...state.paymentDetails,
          billing_address,
          campaign_title,
        },
        products: newCommunities,
        productsCount: productsCount,
      };
    }

    case actionTypes.DELETE_REGION_CHECKOUT_PAGE_ERROR: {
      const { message, statusCode } = payload;
      return {
        ...state,
        loader: false,
        error: {
          message,
          statusCode,
        },
      };
    }

    // checkout page changing address

    case actionTypes.UPDATE_CHECKOUT_PAGE_CLIENT_ADDRESS_START: {
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          billing_address: {
            ...state.billing_address,
            loader: true,
          },
        },
      };
    }

    case actionTypes.UPDATE_CHECKOUT_PAGE_CLIENT_ADDRESS_SUCCESS: {
      const { billing_address, communities, total_outstanding } = payload;
      return {
        ...state,
        products: [...communities].sort((a, b) =>
          a.community_title.localeCompare(b.community_title)
        ),
        total_outstanding,
        paymentDetails: {
          ...state.paymentDetails,
          billing_address,
          loader: false,
          error: null,
        },
      };
    }

    case actionTypes.UPDATE_CHECKOUT_PAGE_CLIENT_ADDRESS_ERROR: {
      const { message, statusCode } = payload;
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          loader: false,
          error: {
            message,
            statusCode,
          },
        },
      };
    }

    // set partial payment community

    case actionTypes.SET_PARTIAL_PAYMENT_COMMUNITY_SUCCESS: {
      const { total_outstanding, communities } = payload;
      return {
        ...state,
        partialPayment: {
          ...state.partialPayment,
          isActive: true,
          community: communities[0],
          total_outstanding,
          loader: false,
          error: null,
        },
      };
    }

    case actionTypes.UNDO_PARTIAL_PAYMENT_COMMUNITY_SUCCESS: {
      return {
        ...state,
        partialPayment: {
          ...initialState.partialPayment,
        },
      };
    }

    case actionTypes.SET_PARTIAL_PAYMENT_COMMUNITY_ERROR:
    case actionTypes.UNDO_PARTIAL_PAYMENT_COMMUNITY_ERROR: {
      const { message, statusCode = 500 } = payload;
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          isActive: false,
          loader: false,
          error: {
            message,
            statusCode,
          },
        },
      };
    }

    // checkout page details

    case actionTypes.SET_INVOICE_RECEIVE: {
      return {
        ...state,
        invoiceReceive: payload,
      };
    }

    case actionTypes.SET_PAYMENT_STEP: {
      return {
        ...state,
        paymentDetails: {
          ...state.paymentDetails,
          paymentStep: payload,
        },
      };
    }

    default:
      return state;
  }
};
