import React, { useState } from 'react';
import styles from './styles.module.scss';
import { Link, useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import OutsideCloseModal from '../../OutsideCloseModal';
import Button from '../../atoms/Button';
import AddUserImg from '../../../assets/images/ui-icons/addAnotherUser.svg';
import { logoutUser } from '../../../redux/thunk/logout';

const UserMenu = ({
  userInitials,
  isAuth,
  first_name,
  last_name,
  email,
  size = 'default',
  dispatchLogoutUser,
}) => {
  const history = useHistory();

  const [isUserMenuOpen, setIsUserMenuOpen] = useState(false);

  const signoutHandler = () => {
    dispatchLogoutUser();
  };

  const goToAccountSettings = () => {
    history.push('/my-account/details/personal-info');
    setIsUserMenuOpen(false);
  };

  const toggleShowUserMenu = () => {
    setIsUserMenuOpen(!isUserMenuOpen);
  };

  return (
    <>
      {isAuth && (
        <OutsideCloseModal state={isUserMenuOpen} setState={setIsUserMenuOpen}>
          <div
            className={`${styles['userMenu']} ${
              size === 'mini' && styles.mini
            }`}
          >
            <Link
              className={`${styles['userMenu__name']} ${
                size === 'mini' && styles.mini
              }`}
              to="#"
              onClick={toggleShowUserMenu}
            >
              {userInitials}
            </Link>
            <div
              className={styles['userMenu__submenu']}
              style={{
                opacity: isUserMenuOpen ? 1 : 0,
                visibility: isUserMenuOpen ? 'visible' : 'hidden',
              }}
            >
              <div className={styles['userMenu__submenu-wrap']}>
                <div className={styles['userMenu__submenu-circle']}>
                  {userInitials}
                </div>
                <div className={styles['userMenu__submenu-name']}>
                  {first_name} {last_name}
                </div>
                <div className={styles['userMenu__submenu-mail']}>{email}</div>

                <div className={styles['userMenu__submenu-settingsBtn']}>
                  <Button
                    text="Account settings"
                    clickHandler={goToAccountSettings}
                  />
                </div>
              </div>
              <div className={styles['userMenu__submenu-addAnotherAcc']}>
                <img src={AddUserImg} alt="add new user" />
                <p>Add another account User</p>
              </div>
              <div className={styles['userMenu__submenu-signout']}>
                <Button text="Sign out" clickHandler={signoutHandler} />
              </div>
            </div>
          </div>
        </OutsideCloseModal>
      )}
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    isAuth: state.session.isAuth,
    email: state.session.user.email,
    first_name: state.session.user.first_name,
    last_name: state.session.user.last_name,
  };
};

const mapDispatchToProps = {
  dispatchLogoutUser: logoutUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(UserMenu);
