import actionTypes from '../constants';

const initialState = {
  isAuth: !!window.localStorage.getItem('token'),
  user: {
    first_name: '',
    last_name: '',
    email: '',
    userInitials: '',
  },
  error: null,
  loader: false,
};

export default function sessionReducer(state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case actionTypes.LOG_IN_START: {
      return {
        ...state,
        loader: true,
      };
    }

    case actionTypes.LOG_IN_SUCCESS: {
      const { first_name, last_name, email, brand_id, isAuth } = payload;
      const userInitials = `${first_name ? first_name[0] : ''}${
        last_name ? last_name[0] : ''
      }`;
      return {
        ...state,
        isAuth,
        user: {
          ...state.user,
          first_name,
          last_name,
          email,
          userInitials,
          brand_id,
        },
        error: null,
        loader: false,
      };
    }

    case actionTypes.LOG_IN_ERROR: {
      const { message } = action.payload;
      return {
        ...state,
        isAuth: false,
        error: message,
        loader: false,
      };
    }

    case actionTypes.LOG_IN_RESET_ERROR_MESSAGE: {
      return {
        ...state,
        error: null,
      };
    }

    case actionTypes.LOG_OUT_START: {
      return { ...state, loader: true };
    }

    case actionTypes.LOG_OUT_SUCCESS: {
      return { ...state, isAuth: false, loader: false };
    }

    case actionTypes.LOG_OUT_ERROR: {
      const { error } = payload;
      return { ...state, loader: false, error: error };
    }

    case actionTypes.REFRESH_TOKEN_START: {
      return {
        ...state,
        loader: true,
      };
    }

    case actionTypes.REFRESH_TOKEN_SUCCESS: {
      const { first_name, last_name, email, brand_id, isAuth } = payload;
      const userInitials = `${first_name ? first_name[0] : ''}${
        last_name ? last_name[0] : ''
      }`;
      return {
        ...state,
        isAuth,
        user: {
          ...state.user,
          first_name,
          last_name,
          email,
          userInitials,
          brand_id,
        },
        error: null,
        loader: false,
      };
    }

    case actionTypes.REFRESH_TOKEN_ERROR: {
      const { error } = payload;
      return {
        ...state,
        isAuth: false,
        loader: false,
        error,
      };
    }

    default:
      return state;
  }
}
