import React from 'react';
import styles from './styles.module.scss';
import SponsorsBenefit from './SponsorBenefits';
import SponsorImg1 from '../../../assets/images/sponsorsTextImages/1.png';
import SponsorImg2 from '../../../assets/images/sponsorsTextImages/2.png';
import SponsorImg3 from '../../../assets/images/sponsorsTextImages/3.png';

const benefitsBlocks = [
  {
    title: 'Partner in our social mission',
    image: SponsorImg1,
    text: 'Join us in helping people enculturate themselves in the knowledge and skills that are fundamental to innovation-led growth.',
  },
  {
    title: 'Real metrics',
    image: SponsorImg2,
    text: 'We provide our Sponsors with fully verifiable metrics free of the fake clicks that cost companies tens of billions a year.',
  },
  {
    title: 'No long terms contracts',
    image: SponsorImg3,
    text: 'Our Direct Programmatic Guarantee offers lower costs and better audience reach with no fee to influencers or agencies.',
  },
];

const SponsorsText = ({ benefits = benefitsBlocks, backgroundColor }) => {
  return (
    <div
      className={`${styles['reasonsForSponsoring']} ${
        backgroundColor ? styles[backgroundColor] : ''
      }`}
    >
      <div className={styles['reasonsForSponsoring__wrap']}>
        <h2 className={styles['reasonsForSponsoring__title']}>
          Why Sponsor a Community?
        </h2>
        <p className={styles['reasonsForSponsoring__description']}>
          Sponsorship elevates your brand to prominence amongst a highly-skilled
          pool of talent and to relevant businesses.
        </p>
      </div>
      <ul className={styles['reasonsForSponsoring__ul']}>
        {benefitsBlocks.map(({ title, image, text }, idx) => {
          return (
            <SponsorsBenefit
              key={idx}
              image={image}
              title={title}
              text={text}
            />
          );
        })}
      </ul>
    </div>
  );
};

export default SponsorsText;
