import styles from './styles.module.scss';
import AccountSettings from './AccountSettings';
import selectors from '../../redux/selectors';
import { connect } from 'react-redux';
import {
  Switch,
  Route,
  useLocation,
  Link,
  useRouteMatch,
  Redirect,
} from 'react-router-dom';
import Summary from './Summary';
import Summary_Owner from './Summary_Owner';

export const locationUrl = {
  ACCOUNT_SETTINGS: {
    id: 'DETAILS',
    label: 'Account Settings',
    url: 'details',
    layout: <AccountSettings />,
  },
  ORDER_SUMMARY: {
    id: 'SUMMARY',
    label: 'Order Summary',
    url: 'summary_owner',
    layout: <Summary_Owner/>,
  },
  AUDIENCE_ANALYTICS: {
    id: 'AUDIENCE',
    label: 'Audience Analytics',
    url: 'audience',
    layout: <div>AUDIENCE page</div>,
  },
};
const MyAccount = () => {
  const location = useLocation();
  const currentUrl = location.pathname.split('/')[2];
  const { path, url } = useRouteMatch();

  return (
    <div className={styles.myAccount}>
      <div>
        <div className={styles.myAccount__tabMenu}>
          <ul className={styles.myAccount__tabMenu__list}>
            {Object.values(locationUrl).map((el, id) => {
              return (
                <li
                  key={el + '_' + id}
                  className={styles.myAccount__tabMenu__menuItem}
                >
                  <Link
                    className={`${styles.myAccount__tabMenu__menuItem__link} ${
                      currentUrl === el.url ? styles.active : ''
                    }`}
                    to={`${url}/${el.url}`}
                  >
                    {el.label}
                  </Link>
                </li>
              );
            })}
          </ul>
        </div>

        <div className={styles.myAccount__content}>
          <Switch>
            <Route>
              <Route exact path={`${path}`}>
                <Redirect to={`${path}/details`} />
              </Route>
              <Route path={`${path}/details`}>
                <AccountSettings />
              </Route>
              <Route path={`${path}/summary_owner`}>
                <Summary_Owner />
              </Route>
              <Route path={`${path}/summary`}>
                <Summary />
              </Route>
              <Route path={`${path}/audience`}>
                <div>audience page</div>
              </Route>
            </Route>
          </Switch>
        </div>
      </div>
    </div>
  );
};
export default connect(selectors, null)(MyAccount);
