import { useState } from 'react';

import styles from './styles.module.scss';
import UserInfo from '../../../../components/molecules/UserInfo';

const personData = {
  firstName: 'Abdurahmangaji',
  middleName: '',
  secondName: 'Keihanaikukauakahihuli',
  job: 'Marketing analyst',
  department: 'Marketing',
  passwordLastUpdateDate: 'Last changed July 1, 2019',
  verified: true,
  verifiedDate: 'Mar 1, 2018',
};

const PersonalDetails = (props) => {
  const [userFirstName, setUserFirstName] = useState(personData.firstName);
  const [userSecondName, setUserSecondName] = useState(personData.secondName);
  const [userMiddleName, setUserMiddleName] = useState(personData.middleName);
  const [userJob, setUserJob] = useState(personData.job);
  const [userDepartment, setUserDepartment] = useState(personData.department);

  const [userPassword, setUserPassword] = useState('');
  // controllers of edit fields
  const [showFullNameEditField, setShowFullNameEditField] = useState(false);
  const [showJobEditField, setShowJobEditField] = useState(false);
  const [showDepartmentEditField, setShowDepartmentEditField] = useState(false);
  const [showPasswordEditField, setShowPasswordEditField] = useState(false);

  const resetFullName = () => {
    setUserFirstName(userFirstName);
    setUserSecondName(userSecondName);
    setUserMiddleName(userMiddleName);
    setShowFullNameEditField(false);
  };

  const resetJob = () => {
    setUserJob(userJob);
    setShowJobEditField(false);
  };

  const resetDepartment = () => {
    setUserDepartment(userDepartment);
    setShowDepartmentEditField(false);
  };

  const resetPassword = () => {
    setUserPassword('');
    setShowPasswordEditField(false);
  };

  return (
    <div className={styles.details}>
      <div className={styles.details__wrap}>
        <div className={styles.details__title}>Personal info</div>
        <div className={styles.details__userData}>
          <div className={styles.details__userData__editFields}>
            <UserInfo
              option="fullname"
              firstName={userFirstName}
              setFirstName={setUserFirstName}
              secondName={userSecondName}
              setSecondName={setUserSecondName}
              middleName={userMiddleName}
              setMiddleName={setUserMiddleName}
              showEditField={showFullNameEditField}
              setShowEditField={setShowFullNameEditField}
              closeButtonHandler={resetFullName}
              verified={personData.verified}
              verifiedDate={personData.verifiedDate}
            />
            <UserInfo
              option="job"
              job={userJob}
              setJob={setUserJob}
              showEditField={showJobEditField}
              setShowEditField={setShowJobEditField}
              closeButtonHandler={resetJob}
            />
            <UserInfo
              option="department"
              department={userDepartment}
              setDepartment={setUserDepartment}
              showEditField={showDepartmentEditField}
              setShowEditField={setShowDepartmentEditField}
              closeButtonHandler={resetDepartment}
            />
          </div>
          <div className={styles.details__userData__changePass}>
            <UserInfo
              option="changePassword"
              passwordLastUpdatedDate={personData.passwordLastUpdateDate}
              password={userPassword}
              setPassword={setUserPassword}
              showEditField={showPasswordEditField}
              setShowEditField={setShowPasswordEditField}
              closeButtonHandler={resetPassword}
            />
          </div>
          <div className={styles.details__userData__deleteField}>
            <UserInfo option="deleteAccount" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default PersonalDetails;
