import actionTypes from '../constants';

export function loadHomePageDataStart() {
  return {
    type: actionTypes.LOAD_HOMEPAGE_IMAGES_START,
  };
}

export function loadHomePageDataSuccess(payload) {
  return {
    type: actionTypes.LOAD_HOMEPAGE_IMAGES_SUCCESS,
    payload,
  };
}

export function loadHomePageDataError(payload) {
  return {
    type: actionTypes.LOAD_HOMEPAGE_IMAGES_ERROR,
    payload,
  };
}
