import React from "react";
import styles from "./styles.module.scss";

const WrapLayout = ({
  backgroundColor = "#ffffff",
  width = "75%",
  children,
  addCss,
}) => {
  return (
    <div
      className={styles["wrapLayout"]}
      style={{ background: backgroundColor, width: width, addCss }}
    >
      {children}
    </div>
  );
};

export default WrapLayout;
