import { useState } from 'react';
import styles from './styles.module.scss';

const EditEmail = ({
  email,
  setEmail,
  setShowEditField,
  onCancelClick,
  // onSaveDispatch
}) => {
  const [newEmail, setNewEmail] = useState(email);

  const setEmailHandler = (e) => {
    setNewEmail(e.target.value);
  };

  const saveBtnHandler = () => {
    setEmail(newEmail);
    setShowEditField(false);
    // onSaveDispatch(name, secondName, middleName)
  };

  return (
    <div className={styles.editEmail}>
      <div className={styles.editEmail__inputsField}>
        <div className={styles.editEmail__input}>
          <label className={styles.editEmail__label} htmlFor="email">
            Your Email Address{' '}
            <span className={styles.editEmail__label__required}>*</span>
          </label>
          <input
            type="text"
            name="email"
            value={newEmail}
            id="email"
            onChange={setEmailHandler}
          />
        </div>
      </div>

      <div className={styles.editEmail__buttons}>
        <button
          className={`${styles.editEmail__buttons__btn} ${styles.editEmail__buttons__btn__cancel}`}
          onClick={onCancelClick}
        >
          Cancel
        </button>
        <button
          className={`${styles.editEmail__buttons__btn} ${styles.editEmail__buttons__btn__save}`}
          onClick={saveBtnHandler}
        >
          Save changes
        </button>
      </div>
    </div>
  );
};

export default EditEmail;
