import { useState, useEffect } from 'react';
import styles from './styles.module.scss';
import EditFullName from './EditFullName';
import EditJob from './EditJob';
import EditDepartment from './EditDepartment';
import EditBillingAddress from './EditBillingAddress';
import EditEmail from './EditEmail';
import EditPhone from './EditPhone';
import Button from '../../atoms/Button';
import UserIcon from '../../../assets/images/ui-icons/userIcon_blue.png';
import JobTitle from '../../../assets/images/ui-icons/jobTitle_blue.png';
import DepartmentIcon from '../../../assets/images/ui-icons/departmentIcon_blue.png';
import ChangePasswordIcon from '../../../assets/images/ui-icons/changePasswordIcon_blue.png';
import DeleteAccountIcon from '../../../assets/images/ui-icons/deleteAccountIcon_blue.png';
import VerifiedUserIcon from '../../../assets/images/ui-icons/verifiedIcon_bw.png';
import LocationIcon from '../../../assets/images/ui-icons/locationIcon_blue.png';
import EmailIcon from '../../../assets/images/ui-icons/emailIcon_blue.png';
import PhoneIcon from '../../../assets/images/ui-icons/phoneIcon_blue.png';

import EditPassword from './EditPassword';

const UserInfo = ({
  option,
  firstName,
  secondName,
  middleName,
  job,
  department,
  passwordLastUpdatedDate,
  verified,
  verifiedDate,
  email,
  address,
  secondAddress,
  city,
  postCode,
  country,
  state,
  phoneNumber,
  countryCode,
  operatorCode,
  operatorCodeOptions,
  countryIcon,
  countryId,
  showEditField,
  setShowEditField,
  setFirstName,
  setSecondName,
  setMiddleName,
  setJob,
  setDepartment,
  setPassword,
  setAddress,
  setSecondAddress,
  setCity,
  setPostCode,
  setCountry,
  setState,
  setEmail,
  setPhoneNumber,
  setCountryCode,
  setOperatorCode,
  setCountryIcon,
  setCountryId,
  setOperatorCodeOptions,
  closeButtonHandler,
}) => {
  const [icon, setIcon] = useState(null);
  const [titleField, setTitleField] = useState('');

  const onCancel = () => {
    setShowEditField(false);
  };

  const showEditBlockHandler = () => {
    setShowEditField(true);
  };

  const closeBtnHandler = () => {
    closeButtonHandler();
  };

  useEffect(() => {
    switch (option) {
      case 'fullname': {
        setIcon(UserIcon);
        setTitleField('Name');
        break;
      }
      case 'job': {
        setIcon(JobTitle);
        setTitleField('Job Title');
        break;
      }
      case 'department': {
        setIcon(DepartmentIcon);
        setTitleField('Department');
        break;
      }
      case 'changePassword': {
        setIcon(ChangePasswordIcon);
        setTitleField('Change Password');
        break;
      }
      case 'deleteAccount': {
        setIcon(DeleteAccountIcon);
        setTitleField('Delete Sponsor Account and Data');
        break;
      }
      case 'billingAddress': {
        setIcon(LocationIcon);
        setTitleField('Billing Address');
        break;
      }
      case 'email': {
        setIcon(EmailIcon);
        setTitleField('Email');
        break;
      }
      case 'phone': {
        setIcon(PhoneIcon);
        setTitleField('Phone');
        break;
      }
      default: {
        return;
      }
    }
  }, [option]);

  return (
    <div className={styles.userInfo}>
      <div className={styles.userInfo__wrap}>
        <div className={styles.userInfo__image}>
          <img src={icon} alt={titleField} />
        </div>
        <div className={styles.userInfo__title}>
          {titleField}
          {option === 'changePassword' && (
            <p className={styles.userInfo__subTitle}>
              Its a good idea to use a strong password that you dont use
              elsewhere
            </p>
          )}
        </div>
        <div className={styles.userInfo__value}>
          {option === 'fullname' && (
            <div>
              <p>{`${firstName} ${middleName} ${secondName}`}</p>
              {verified && verifiedDate && showEditField && (
                <div className={styles.userInfo__verified}>
                  <img
                    className={styles.userInfo__verified__icon}
                    src={VerifiedUserIcon}
                    alt="verified icon"
                  />
                  <p>Verified since {verifiedDate}</p>
                </div>
              )}
            </div>
          )}
          {option === 'job' && job}
          {option === 'department' && department}
          {option === 'changePassword' && passwordLastUpdatedDate}
          {option === 'phone' && (
            <span>{`${countryCode} ${operatorCode} ${phoneNumber}`}</span>
          )}
          {option === 'billingAddress' && (
            <div className={styles.userInfo__billingAddress}>
              <p>{address}</p>
              <p>{city}</p>
              <p>{postCode}</p>
            </div>
          )}
          {option === 'email' && (
            <div className={styles.userInfo__email}>{email}</div>
          )}
        </div>
        <div className={styles.userInfo__editButton}>
          {option !== 'deleteAccount' && (
            <Button
              text={showEditField ? 'Close' : 'Edit'}
              addClass="grayBG"
              clickHandler={
                showEditField ? closeBtnHandler : showEditBlockHandler
              }
            />
          )}
          {option === 'deleteAccount' && (
            <Button
              text="Delete"
              addClass="blueBG"
              // clickHandler={}
            />
          )}
        </div>
      </div>

      <div
        className={`${styles.userInfo__editBlock} ${
          showEditField ? styles.show : ''
        }`}
      >
        <div className={styles.userInfo__editBlock__wrap}>
          {option === 'fullname' && showEditField && (
            <EditFullName
              firstName={firstName}
              setFirstName={setFirstName}
              secondName={secondName}
              setSecondName={setSecondName}
              middleName={middleName}
              setMiddleName={setMiddleName}
              onCancelClick={onCancel}
              showEditField={showEditField}
              setShowEditField={setShowEditField}
            />
          )}

          {option === 'job' && showEditField && (
            <EditJob
              job={job}
              setJob={setJob}
              onCancelClick={onCancel}
              showEditField={showEditField}
              setShowEditField={setShowEditField}
            />
          )}

          {option === 'department' && showEditField && (
            <EditDepartment
              department={department}
              setDepartment={setDepartment}
              onCancelClick={onCancel}
              showEditField={showEditField}
              setShowEditField={setShowEditField}
            />
          )}

          {option === 'changePassword' && showEditField && (
            <EditPassword
              setPassword={setPassword}
              onCancelClick={onCancel}
              showEditField={showEditField}
              setShowEditField={setShowEditField}
            />
          )}

          {option === 'billingAddress' && showEditField && (
            <EditBillingAddress
              address={address}
              setAddress={setAddress}
              secondAddress={secondAddress}
              setSecondAddress={setSecondAddress}
              city={city}
              setCity={setCity}
              country={country}
              setCountry={setCountry}
              state={state}
              setState={setState}
              postCode={postCode}
              setPostCode={setPostCode}
              onCancelClick={onCancel}
              showEditField={showEditField}
              setShowEditField={setShowEditField}
            />
          )}

          {option === 'email' && showEditField && (
            <EditEmail
              email={email}
              setEmail={setEmail}
              onCancelClick={onCancel}
              showEditField={showEditField}
              setShowEditField={setShowEditField}
            />
          )}

          {option === 'phone' && showEditField && (
            <EditPhone
              phoneNumber={phoneNumber}
              setPhoneNumber={setPhoneNumber}
              countryCode={countryCode}
              setCountryCode={setCountryCode}
              operatorCode={operatorCode}
              setOperatorCode={setOperatorCode}
              operatorCodeOptions={operatorCodeOptions}
              setOperatorCodeOptions={setOperatorCodeOptions}
              countryIcon={countryIcon}
              setCountryIcon={setCountryIcon}
              countryId={countryId}
              setCountryId={setCountryId}
              onCancelClick={onCancel}
              showEditField={showEditField}
              setShowEditField={setShowEditField}
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default UserInfo;
