import React, { useEffect } from 'react';
import CommunityHeader from '../molecules/CommunityHeader';
import CommunityRegions from '../CommunityRegions';
import Payment from '../Payment';
import styles from './styles.module.scss';
import { connect } from 'react-redux';

const CommunityWithRegions = ({
  community_id,
  community_title,
  community_colored_icon,
  brand_logo,
  currency_symbol,
  prefered_currency,
  regions,
  total_outstanding,
  total_pledged,
  symbol_placement,
  productsCount,
  hidePaymentHandler,
  isHeaderPaymentFixed,
  isMainMenuMiniSize,
}) => {
  useEffect(() => {
    // this wil fixed soon FIX
    let body = document.querySelector('body');
    body.style.overflow = 'hidden';
    return () => {
      body.style.overflow = 'auto';
    };
  }, []);

  const getTopOffset = () => {
    if (isHeaderPaymentFixed && isMainMenuMiniSize) {
      return styles.menuMiniPaymentFixed; //150
    }

    if (!isHeaderPaymentFixed && isMainMenuMiniSize) {
      return styles.menuMini; //80
    }
    return styles.menuUsual;
  };

  return (
    <div className={`${styles.communityWithRegions} ${getTopOffset()}`}>
      <div
        className={`${styles.communityWithRegions__wrap} ${getTopOffset()} `}
      >
        <CommunityHeader
          title={community_title}
          community_icon={community_colored_icon}
          brand_logo={brand_logo}
        />
        <CommunityRegions
          community_id={community_id}
          regions={
            regions &&
            [...regions].sort(
              (a, b) => Number(a.region_id) - Number(b.region_id)
            )
          }
          currency_symbol={currency_symbol}
          prefered_currency={prefered_currency}
          symbol_placement={symbol_placement}
        />
        <Payment
          laterBtnOnclick={hidePaymentHandler}
          currency_symbol={currency_symbol}
          total_outstanding={total_outstanding}
          total_pledged={total_pledged}
          symbol_placement={symbol_placement}
          productsCount={productsCount}
        />
      </div>
    </div>
  );
};

const mapStateToProps = (state) => ({
  isHeaderPaymentFixed: state.headerPayment.isHeaderPaymentFixed,
  isMainMenuMiniSize: state.headerMenu.mainMenu.isMiniSize,
  brand_logo: state.categorygrid.brand_logo,
  total_outstanding: state.cart.total_outstanding,
  total_pledged: state.cart.total_pledged,
  symbol_placement: state.cart.symbol_placement,
  productsCount: state.cart.productsCount,
});

export default connect(mapStateToProps, null)(CommunityWithRegions);
