import {
  loadHomePageDataStart,
  loadHomePageDataSuccess,
  loadHomePageDataError,
} from '../../actions/homepage';
import { fetchWithAuth } from '../../../http/fetchWithAuth';
import { logoutUser } from '../logout';

export const fetchHomePageData = () => async (dispatch) => {
  try {
    dispatch(loadHomePageDataStart());
    const response = await fetchWithAuth(
      'https://voxdigital.by:4000/homepage',
      {
        method: 'GET',
      }
    );
    if (!response || response.status === 500) {
      dispatch(
        loadHomePageDataError({
          error: {
            message: 'Something went wrong. Please try again',
            statusCode: 500,
          },
        })
      );
      dispatch(logoutUser());
      return;
    }
    if (response.status === 401) {
      loadHomePageDataError({
        error: {
          message: 'Not authorized. Please try again',
          statusCode: 401,
        },
      });
      dispatch(logoutUser());
      return;
    }

    const responseData = await response.json();

    const { imagespath, brand_id } = responseData; //todo response handling

    dispatch(
      loadHomePageDataSuccess({
        brand_id,
        imagespath,
      })
    );
  } catch (error) {
    dispatch(
      loadHomePageDataError({
        error: { message: error.message, statusCode: error.status || 500 },
      })
    );
    throw error;
  }
};
