import styles from './styles.module.scss';
import { connect } from 'react-redux';
import { PDFViewer } from '@react-pdf/renderer';
import dateFormat from "dateformat";
import { useHistory } from 'react-router-dom';

import React, {  useEffect, useState } from 'react';
import  exp_img  from '../../../assets/images/ui-icons/Export_icon.svg';
import arrow from '../../../assets/images/ui-icons/Arrow_icon.svg';
import drop_down from '../../../assets/images/ui-icons/mobileMenuWhiteArrow.svg';
import close from '../../../assets/images/ui-icons/close_white.svg';
import Exportdoc from '../../../components/molecules/Exportdoc';



const Summary_owner = () => {

    const { push } = useHistory();

    const [dataInit, setDataInit] = useState();
    const [data, setData] = useState();
    const [activeBlock, setActiveBlock] = useState(1);
    const [view, setView] = useState([]);
    const [selected, setSelected] = useState([]);
    const [viewExport, setViewExport] = useState(false);
    const [my_time_date, set_my_time_date] = useState([]);
   

    const setSelectedId = a => {
        let newArray = [...selected];
        let position = selected.indexOf(a);        
        position !== -1 ? newArray[position] = null :  newArray.push(a);       
        setSelected(newArray.filter(i=>i));       
    };

    const setActiveBlockClick = (a) => {
        setActiveBlock(a);      
        let new_data_all = dataInit
            .filter(i => (a === 1) ?
                i : 
                (a === 2) ?
                i.payment_status === 'Paid' : 
                i.payment_status !== 'Paid'
            );
        let new_data = Object.assign(data,{all_orders : new_data_all});               
        setData(new_data);
        setView(Array(dataInit.length).fill(false));                
        set_my_time_date([...new Set(data.all_orders.map(i=>my_time_filter(i.paymentTime)))]);
    };
   
    useEffect(() => {        
        const options = {
            method: 'get',
            headers: { 'Content-Type': 'application/json' },           
        };
        fetch('https://skillvox.herokuapp.com/summary_brand', options)
            .then(response => response.json())
            .then(data => {                
                setDataInit(data.data.all_orders);               
                setData(data.data);
                console.log(data.data.all_orders .filter(i => i.payment_status === 'Paid' ));               
                setView(Array(data.data.all_orders.length).fill(false));                
                set_my_time_date([...new Set(data.data.all_orders.map(i=>my_time_filter(i.paymentTime)))]);
            })
    },[]);

    const setSelectedIdAll = a => {
        let id = data.all_orders
        .filter(i => my_time_filter(i.paymentTime) === a )
        .map(i=>i.order_id);       
        let position = [];
        for( const i of id) {
            position.push(selected.indexOf(i));
        }             
        setSelected(position.some(a => a === -1 ) ? selected.concat(id) : selected.filter(i=>(id.indexOf(i) ===-1)));
    };   
  
    const my_time = a => {
        let now = new Date(a);
        return dateFormat(now,"dd.mm.yy");
    };
    const my_time_filter = (a) => {
        let now = new Date(a);        
        return dateFormat(now,"mmmm yyyy");
    };
    const setViewDiv = a => {       
        let newArr = [...view]; 
        newArr[parseInt(a.n)] = (newArr[parseInt(a.n)] && (newArr[parseInt(a.n)]['m'] === a['m'])) ? false : a;       
        setView(newArr);
        console.log(newArr);       
    };   
    function sum_order_subtotal (a,b) {
        let s = data.all_orders
        .filter(i => my_time_filter(i.paymentTime) === a)
        .map(i=>i.community_sponsored.map(a=>a[b]))
        .flat()
        .reduce((a,b) => a + Number(b.replace(',','').replace('.00','')),0);       
        return  new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'EUR' })
        .format(s)
        .replace('','');              
    }; 

    return ( <>
        { data ? <div className={styles.main}>
            <div className={styles.head}>
                <div className={styles.logo} 
                    style={{backgroundImage: `url(${data.brandLogoURL})`}}
                >{data.brandName}</div>
                <div className={styles.pledged}>
                    <p>Sponsorship amount pledged </p>
                    <h2>{data.total_pledged} <b></b></h2>
                </div>
                <div className={styles.receved}>
                    <h3>{data.total_paid} <b></b> <span>RECEIVED</span></h3>
                </div>
                <div className={styles.sponsors}>
                    <h2 onClick={() => push('/my-account/summary')}>Your Sponsorship by region</h2>
                    {data.sponsored_regions.map((i,index) => 
                        <p key={index}>
                            <span>{i.name} </span>
                            <br/>
                            <span>{i.communities_sponsored} communities</span>
                        </p>
                    )}
                </div>
            </div>
            {selected.length ? 
                <div className={styles.selected}>
                    <span className={styles.count_selected}>{selected.length} selected</span>
                    <p className={styles.action}
                        style={{backgroundImage: `url(${drop_down})`}}
                    >Actions                   
                        <span className={styles.actions_menu}>
                            <b>Export details</b>
                            <br/>
                            <b>Cancel order(s)</b>
                        </span>
                    </p>
                    <img src={close} alt="close" className={styles.close} onClick={()=>setSelected([])}/>
                </div>    
            :
                <div className={styles.export}>
                    <div className={activeBlock === 1 ? styles.active : styles.unactive} 
                        onClick={()=>setActiveBlockClick(1)}
                    >ALL ({dataInit.length})</div>
                    <div className={activeBlock === 2 ? styles.active : styles.unactive} 
                        onClick={()=>setActiveBlockClick(2)}
                    >
                        PAID ({dataInit.filter(i=>i.payment_status === 'Paid').length})   
                    </div>
                    <div className={activeBlock === 3 ? styles.active : styles.unactive} 
                        onClick={()=>setActiveBlockClick(3)}
                    >
                        OUTSTANDING PAYMENT ({dataInit.filter(i=>i.payment_status !== 'Paid').length})
                    </div>
                    <button onClick={()=>setViewExport(true)}>
                        <img src={exp_img} alt="export" height="25" width="25"/>
                        EXPORT
                    </button>
                </div>
            }
            <div className={styles.bigblock}>
            <div className={styles.nextblock}>
               {my_time_date.map(a => <React.Fragment key={a}>
                <p className={styles.date}>{a}</p>               
                <div className={styles.header_table}>
                    <label style={{width: '9%',padding: 0}}>
                        <input
                            type="checkbox"
                            checked={data.all_orders
                                .filter(i => my_time_filter(i.paymentTime) === a )
                                .map(i=>i.order_id)
                                .every(i=>selected.includes(i))
                            }
                            onChange={()=>setSelectedIdAll(a)}
                        />
                    {' '} Date
                    </label>
                    <span style={{width:'8%'}}>Owner</span>
                    <span style={{width:'4%'}}>No.</span>
                    <span style={{width:'13%'}}>Payer | Department</span>
                    <span style={{width:'22.3%',paddingRight: 30}}>Sponsored community</span>
                    <span>Subtutorial ()</span>
                    <span>Total (incl tax)</span>
                    <span style={{width:'12%'}} className={styles.payment_status}>Payment status</span>
                    <span style={{width:'11.5%'}}>Fufillment status</span>
                </div>
                {data.all_orders
                    .filter(i => my_time_filter(i.paymentTime) === a )                    
                    .map(i =>
                    <div className={styles.table} key={i.order_id}>
                        <label style={{paddingRight: 15}}>
                            <input
                                type="checkbox"
                                checked={selected.indexOf(i.order_id) >= 0}
                                onChange={()=>setSelectedId(i.order_id)}
                            />
                        {' '} {my_time(i.paymentTime)}
                        </label>
                        <span style={{width:'8%'}}>{data.brand_admin.split(' ').map(f=>f.charAt(0))}</span>
                        <span style={{width:'4%'}}>{i.order_id}</span>
                        <span style={{width:'13%'}}>
                            {i.payee}<br/>
                            <b style={{color:'#898989'}}>({i.payees_dept})</b>                    
                        </span>
                        <div className={styles.community_sponsored}>
                            {i.community_sponsored.map((a,index) => 
                                <p style={{marginBottom:10}} key={index}
                                    onClick={()=>setViewDiv({n:i.order_id,m:index})}
                                >                                   
                                    <img src={arrow} 
                                        alt="arrow" 
                                        className={`${(view[parseInt(i.order_id)] && (view[parseInt(i.order_id)].m === index)) ? [styles.revolve]: ''}`}                                  
                                    />                           
                                    <strong>{a.community_title.toUpperCase()}</strong><br/>
                                    <b style={{color: '#828282'}}>{a.campaign_title}</b> <br/>
                                    {(view[parseInt(i.order_id)] && (view[parseInt(i.order_id)].m === index)) &&
                                        <b className={styles.sponsored}>
                                            {a.campaign_regions
                                            .map(b=>b.region_name).join().replace(/,/g,' / ')}
                                        </b>
                                    }
                                </p>
                            )}
                        </div>
                        <span>{i.community_sponsored.map((a,index)=>
                            <b key={index} style={{marginTop: index?40 + (view[parseInt(i.order_id)] && (view[parseInt(i.order_id)].m === index-1))*60 : 0}}>{a.order_subtotal}</b>)}
                        </span>
                        <span>{i.community_sponsored.map((a,index)=>
                            <b key={index} style={{marginTop: index?40 + (view[parseInt(i.order_id)] && (view[parseInt(i.order_id)].m === index-1))*60 : 0}}>{a.total_incl_tax}</b>)}
                        </span>
                        <div style={{width:'12%',alignItems:'center'}} className={styles.payment_status}>
                            <b className={                                
                                i.payment_status === 'Pending' ?
                                styles.pending:
                                i.payment_status === 'Unpaid' ?
                                styles.unpaid:
                                i.payment_status.includes('Partially') ?
                                styles.received:
                                styles.paid}
                            >
                                {i.payment_status}
                            </b><br/>
                            {(i.payment_status === 'Paid' || i.payment_status === 'Partially received') && 
                                <span style={{cursor:'pointer'}}>View invoce </span>
                            }
                            { i.payment_status === 'Partially received' &&  <span style={{color: '#898989'}}>20,000.00 </span>}   
                            
                            {(i.payment_status === 'Unpaid' || i.payment_status === 'Partially received') &&                               
                                <button>Pay now</button>                               
                            }
                            
                        </div>
                        <div className={styles.payment_status}>
                            <b className={                                
                                i.fufillment_status === 'Pending' ?
                                styles.pending:
                                i.fufillment_status === 'Unpaid' ?
                                styles.unpaid:
                                i.fufillment_status.includes('fufilled') ?
                                styles.fufilled:
                                styles.completed}
                            >
                                {i.fufillment_status}
                            </b>                                                  
                        </div>
                    </div>
                
                )}
                <div className={styles.total}>                   
                    Total:
                    <span>{sum_order_subtotal(a,'order_subtotal')}</span>
                    <span>{sum_order_subtotal(a,'total_incl_tax')}</span>                   
                </div>                  
            </React.Fragment>
            )}
            </div>
            </div>  
            {/* { edit_transaction && 
                <Transaction edit_transaction={edit_transaction} setEdit={setEdit} data={data}/> 
            }                      */}
            { viewExport &&
                <div className={styles.pdf}>
                    <button className={styles.close_pdf} onClick={()=>setViewExport(false)}>
                        x
                    </button> 
                    <PDFViewer className={styles.pdf}>
                        <Exportdoc close={setViewExport} 
                            data={ data.all_orders
                            .filter(i => i.payment_status === 'Paid' ) }
                            header={data} 
                        />  
                    </PDFViewer>
                </div>    
            }   
            </div>       
        : 
            <div style={{height: '80vh'}}>
                <h1 style={{font: '600 40px/200px $roboto'}}>Wait</h1>
            </div>
        }
        </>
       
    )

}
export default connect()(Summary_owner);