// import styles from './styles.module.scss';
import React  from 'react';
// import  from '../../../@theme';
import LogoImg from "../../../assets/images/logoNew.png";
import { Page, Text, View, Document, StyleSheet, Image, Font, Link } from '@react-pdf/renderer';


Font.register({
    family: 'Archivo',
    src: 'https://fonts.gstatic.com/s/archivo/v9/k3k6o8UDI-1M0wlSV9XAw6lQkqWY8Q82sJaRE-NWIDdgffTT0zRZ9xds.ttf' 
   
});
Font.register({
    family: 'Roboto',
    src: 'https://fonts.gstatic.com/s/roboto/v29/KFOmCnqEu92Fr1Mu4mxP.ttf'
});
Font.register({
    family: 'Roboto_Bold',
    src: 'https://fonts.gstatic.com/s/roboto/v29/KFOlCnqEu92Fr1MmWUlfBBc9.ttf'
});

  

const ExportDoc = ({data,header}) => {

   const my_type = typeof data[0]['community_sponsored'];
   console.log(my_type);

    function sum_order_subtotal (b) {
        if (my_type === 'string') {
            let s = data           
            .map(i=>i[b])            
            .reduce((a,b)=> a + Number(b.replace(',','').replace('.00','')),0);
            return   new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'EUR' })
            .format(s)
            .replace('','');

        } else {
            let s = data           
            .map(i=>i.community_sponsored.map(a=>a[b]))
            .flat()
            .reduce((a,b)=> a + Number(b.replace(',','').replace('.00','')),0);
            return   new Intl.NumberFormat('de-DE', { style: 'currency', currency: 'EUR' })
            .format(s)
            .replace('',''); 
        }
               
       
              
    };  

    return (       
        <Document>
            <Page size="A4" wrap={true}>               
                <View style={styles.main}>
                    <View style={styles.header}>
                        <View style={styles.logo}>
                            <Image src={LogoImg} style={styles.logo_img}/>
                            <Text style={[styles.logo_text, {fontWeight: 700}]}>SKILLVOX</Text>
                        </View>
                        <Text style={styles.qut}>Quotation</Text>
                    </View>
                    <Text style={{ fontFamily: 'Roboto_Bold',fontSize: 10 }}>
                       This quote has been prepared for:
                    </Text>
                    <View style={styles.item}>
                       <View style={styles.address}>
                           <Text style={{ fontFamily: 'Roboto_Bold',marginBottom:4 }}>
                               {header.brand_representative.name}
                            </Text>
                           <Text style={{marginBottom: 6}}>Representative of
                               <Text style={{ fontFamily: 'Roboto_Bold'}}>
                                   {' '} {header.brand_representative.legally_registered_brandName}
                                </Text>
                            </Text>
                            <Text style={{fontSize:12,marginBottom: 5}}>
                                {header.brand_representative.billing_address1},
                            </Text>
                            <Text style={{fontSize:12,marginBottom: 5}}>
                                {header.brand_representative.poscode}{' '}
                                {header.brand_representative.city}{' '}
                                {header.brand_representative.country}
                            </Text>
                            <Text style={{fontSize:12}}>
                                Tel: {header.brand_representative.telephone}
                            </Text>
                        </View>
                        <View style={styles.paydata}>
                            <View style={{borderBottom: '1px solid #ddd',marginBottom: 10}}>
                                <View style={styles.paydata_row}>
                                    <Text style={{width: '33%', fontFamily: 'Roboto_Bold'}}>
                                        Order number:
                                    </Text>
                                    <Text style={{width: '60%',color: 'rgb(17, 17, 17)'}}>
                                        SOP.3435-4606-8819-0381
                                    </Text>
                                </View>
                                <View style={styles.paydata_row}>  
                                    <Text style={{width: '33%', fontFamily: 'Roboto_Bold'}}>
                                        Date of issue:
                                    </Text>
                                    <Text style={{width: '60%'}}>
                                        31.05.2021
                                    </Text>
                                </View>
                                <View style={styles.paydata_row}> 
                                    <Text style={{width: '33%', fontFamily: 'Roboto_Bold'}}>
                                        Payment method:
                                    </Text>
                                    <Text style={{width: '30%'}}>
                                        4474 **** **** 5689  
                                    </Text>
                                    <Text style={styles.visa}>VISA</Text>        
                                                                         
                                </View>
                            </View>
                            <View style={{borderBottom: '1px solid #ddd',marginBottom: 10}}>
                                <View style={styles.paydata_row}>  
                                    <Text style={{width: '33%', fontFamily: 'Roboto_Bold'}}>
                                       Supplier:
                                    </Text>
                                    <View>
                                        <Text style={{width: '60%', fontFamily: 'Roboto_Bold'}}>
                                            Skillvox BV
                                        </Text>
                                        <Text style={{ color: 'rgb(17, 17, 17)'}}>
                                            {header.skillvox_representative.business_address1},{' '}
                                            {header.skillvox_representative.post_code},{' '}
                                            {header.skillvox_representative.city},{' '}
                                            {header.skillvox_representative.country}                                           
                                        </Text>
                                    </View>
                                </View> 
                                <View style={styles.paydata_row}>  
                                    <Text style={{width: '33%', fontFamily: 'Roboto_Bold'}}>
                                        Contact person:
                                    </Text>
                                    <Text style={{width: '60%', fontFamily: 'Roboto_Bold'}}>
                                        {header.skillvox_representative.name}
                                    </Text>
                                </View>
                            </View>
                            <View>
                                <View style={styles.paydata_row}>  
                                    <Text style={{width: '33%', fontFamily: 'Roboto_Bold' }}>
                                        Deliverables:
                                    </Text>
                                    <Text style={{width: '60%'}}>
                                    {header.deliverables}
                                    </Text>
                                </View> 
                                <View style={styles.paydata_row}>  
                                    <Text style={{width: '33%', fontFamily: 'Roboto_Bold'}}>
                                        Scheduled Fufillment:
                                    </Text>
                                    <Text style={{width: '60%'}}>
                                    {header.scheduled_fufillment}
                                    </Text>
                                </View>
                            </View>
                        </View>    
                    </View>
                    <View style={styles.main_table}>
                        <View style={styles.table_header}>
                           <Text style={{width: '25%'}}>Sponsored community</Text>
                           <Text style={{width: '30%'}}>Campaign details</Text>
                           <Text style={{width: '21%'}}>Region(s)</Text>
                           <Text style={{width: '12%',textAlign:'right'}}>Subtotal ()</Text>
                           <Text style={{width: '12%',textAlign:'right'}}>Incl tax ()</Text>
                        </View>
                        { my_type === 'string' ? 
                        <View>
                            {data.map((i,index) => 
                                <View style={styles.table_main} key={index}>
                                    <Text style={{width: '25%',fontWeight: 600, color: '#000',fontSize: 10}}>{i.community_sponsored}</Text>
                                    <Text style={{width: '30%'}}>{i.campaign_title}</Text>
                                    <View style={styles.region_name}> 
                                        {i.campaign_regions.map((a,index) =>
                                        <Text key={index}>{a.region_name},</Text>)}                                   
                                    </View>
                                    <Text style={{width: '12%',textAlign:'right',color: '#000'}}>{i.order_subtotal}</Text>
                                    <Text style={{width: '12%',textAlign:'right',color: '#000'}}>{i.total_incl_tax}</Text>
                                </View>
                            )}
                        </View>:
                        <View>
                            {data[0].community_sponsored.map((i,index) => 
                                <View style={styles.table_main} key={index}>
                                 <Text style={{width: '25%',fontWeight: 600, color: '#000',fontSize: 10}}>{i.community_title}</Text>
                                 <Text style={{width: '30%'}}>{i.campaign_title}</Text>
                                 <View style={styles.region_name}> 
                                     {i.campaign_regions.map((a,index) =>
                                     <Text key={index}>{a.region_name},</Text>)}                                   
                                 </View>
                                 <Text style={{width: '12%',textAlign:'right',color: '#000'}}>{i.order_subtotal}</Text>
                                 <Text style={{width: '12%',textAlign:'right',color: '#000'}}>{i.total_incl_tax}</Text>
                               </View>
                           )}
                        </View>}        
                    </View>               
                    <View style={styles.total}>
                        <Text style={styles.total_text}>Total:</Text>
                        <Text style={styles.total_text}>
                            {sum_order_subtotal('order_subtotal')}
                        </Text>
                        <Text style={styles.total_text}>
                            {sum_order_subtotal('total_incl_tax')} 
                        </Text>
                    </View>
                    <View style={styles.footer}>
                        <View style={styles.footer_string}>
                            <Text style={{fontFamily:'Roboto_Bold'}}>Payment Terms: </Text>
                            <Text>At least 12 mounth prior to fufillment slot.</Text>
                        </View>
                        <View style={styles.footer_string}>
                            <Text style={{fontFamily:'Roboto_Bold'}}>General Conditions: </Text>                        
                            <Text>As per our Pilot Sponsorship program Terms of Service.
                                (see <Link style={{paddingLeft: 5}}> 
                                https://skillvox.herokuapp.com/sponsor/terms
                            </Link>)
                            </Text>
                        </View>
                        <View style={styles.footer_string}>
                            <Text style={{fontFamily:'Roboto_Bold'}}>Quote Valid for: </Text>
                            <Text>60 days.</Text>
                        </View>
                        <Text>Thank you for the opportunuty to provide this quitation, and we look forward to your response in due course.</Text>
                        <Text style={{marginTop: 4}}>Yours sincerely,</Text>
                        <Text style={{fontSize: 11,marginTop: 4}}>Paul O. Peterson</Text>
                        <Text style={{fontSize: 11}}>Founder and CEO</Text>
                    </View>          
                </View>    
            </Page>
        </Document>
      
    )

}
const styles = StyleSheet.create({   
    main : {
        padding: 30,
        height: '100%',
        position: 'relative'      
    },
    logo_img: {
        width: 30,
        height: 30,       
    },
    header: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        borderBottom: '1px solid #ddd',
        paddingBottom: 5,
        marginBottom: 20,
    },
    logo: {flex:1, flexDirection: 'row',justifyContent: 'flex-start',width: 200},
    logo_text: {    
      fontSize: 20, 
      fontWeight: 700,    
      margin: '5px 5px',
      fontFamily: 'Archivo',     
    },
    qut: {
        float: 'right',
        marginTop: 5,
        fontFamily: 'Roboto_Bold',   
    },
    item: { 
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'space-between',
        marginTop: 10,
        marginBottom: 20,
        fontFamily: 'Roboto',

    },
    address: {
        width: '40%',
        height: '70%',
        border: '1px solid #bbb',
        fontSize: 10,
        padding: 8,
    },
    paydata: {
        width: '57%',       
        fontSize: 8,
        border: '1px solid #bbb',
        padding: 10
    },
    paydata_row: {
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        marginBottom: 3,
        justifyContent:'flex-start',       
    },
    visa: {
        border: '1px solid #ddd',
        width: '10%',
        color: 'darkblue',
        lineHeight: 1.1,
        paddingTop: 1,
        paddingLeft: 1,       
        borderRadius: 2,
        textAlign: 'center',
        fontFamily: 'Roboto_Bold'
    },    
    main_table: {

    },
    table_header: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-start',
        fontSize: 10,
        fontFamily: 'Roboto',
        paddingBottom: 2,

        fontWeight: 600,
        fontFamily: 'Roboto',
        color: 'rgb(84, 110, 122)',
        borderBottom: '1px dotted rgb(84, 110, 122)'
            
    },
    table_main: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-start',
        fontSize: 8,
        paddingBottom: 10,
        paddingTop: 10,       
        color: 'rgb(84, 110, 122)',
        borderBottom: '1px dotted rgb(84, 110, 122)'
            
    },
    region_name: {       
        width: '21%',
        color:'blue',
        fontSize: 8,
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-start',
        flexWrap: 'wrap',       

    },
    total: {
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'flex-end',       
        paddingTop: 10,
       
    },
    total_text: {
        textAlign:'right',
        color: '#000',
        fontFamily: 'Roboto_Bold',
        fontSize: 10,
        width: '12%',
    },
    footer: {
        position: 'absolute',
        fontFamily: 'Roboto',
        bottom: 30,
        fontSize: 9,
        left: 30,
        lineHeight: 1.5
    },
    footer_string: {
        display: 'flex',
        flexDirection:'row',
        justifyContent:'flex-start',
    }
      

});
export default ExportDoc;