import styles from './styles.module.scss';

const BenefitSquare = ({title, lineColor, text}) => {
    return (
        <div className={styles.benefit}>
            <div className={styles.benefit__wrap}>
                <p className={styles.benefit__title}>{title}</p>
                <span className={styles.benefit__coloredLine} style={{background: lineColor}}></span>
                <p className={styles.benefit__text}>
                    {text}
                </p>
            </div>
        </div>
    )
}

export default BenefitSquare;