import { useState } from 'react';
import styles from './styles.module.scss';

const EditJob = ({
  job,
  setJob,
  setShowEditField,
  onCancelClick,
  // onSaveDispatch
}) => {
  const [newJob, setNewJob] = useState(job);

  const setJobHandler = (e) => {
    setNewJob(e.target.value);
  };

  const saveBtnHandler = () => {
    setJob(newJob);
    setShowEditField(false);
    // onSaveDispatch(name, secondName, middleName)
  };

  return (
    <div className={styles.editJob}>
      <div className={styles.editJob__inputsField}>
        <div className={styles.editJob__input}>
          <label className={styles.editJob__label} htmlFor="job">
            Job title <span className={styles.editJob__label__required}>*</span>
          </label>
          <input
            type="text"
            name="job"
            value={newJob}
            id="job"
            onChange={setJobHandler}
          />
        </div>
      </div>

      <div className={styles.editJob__buttons}>
        <button
          className={`${styles.editJob__buttons__btn} ${styles.editJob__buttons__btn__cancel}`}
          onClick={onCancelClick}
        >
          Cancel
        </button>
        <button
          className={`${styles.editJob__buttons__btn} ${styles.editJob__buttons__btn__save}`}
          onClick={saveBtnHandler}
        >
          Save changes
        </button>
      </div>
    </div>
  );
};

export default EditJob;
