import actionTypes from '../constants';

const initialState = {
  brand_id: '',
  imagespath: '',
  loader: false,
  error: null,
};

export default function homepageReducer(state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case actionTypes.LOAD_HOMEPAGE_IMAGES_START: {
      return {
        ...state,
        loader: true,
      };
    }
    case actionTypes.LOAD_HOMEPAGE_IMAGES_SUCCESS: {
      const { brand_id, imagespath } = payload;
      return {
        ...state,
        brand_id,
        imagespath,
        loader: false,
      };
    }

    case actionTypes.LOAD_HOMEPAGE_IMAGES_ERROR: {
      const { error } = payload;
      return {
        ...state,
        loader: true,
        error,
      };
    }
    default:
      return state;
  }
}
