import React from 'react';
import styles from './styles.module.scss';
import { Link } from 'react-router-dom';

export const footerLinks = [
  { title: 'Terms & Conditions', url: '#' },
  { title: 'Contact', url: '/contacts' },
  { title: 'Privacy Policy', url: '#' },
];

const Footer = ({ links = footerLinks, width }) => {
  return (
    <footer className={styles['footer']}>
      <div className={styles['footer__row']}>
        <div className={styles['footer__row-copyright']}>Skillvox  2021</div>
        <ul className={styles['footer__row-menu']}>
          {links &&
            links.map(({ url, title }, idx) => {
              return (
                <li className={styles['footer__row-menu-link']} key={idx}>
                  <Link to={url}>{title}</Link>
                </li>
              );
            })}
        </ul>
      </div>
    </footer>
  );
};

export default Footer;
