import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';

function useOutsideCloseModal(ref, state, setState, addClass = '') {
  useEffect(() => {
    function handleClickOutside(event) {
      if (setState && ref.current && !ref.current.contains(event.target)) {
        setState(false);
      }
    }

    document.addEventListener('click', handleClickOutside);
    return () => {
      document.removeEventListener('click', handleClickOutside);
    };
  }, [ref, setState]);
}

function OutsideCloseModal(props) {
  const wrapperRef = useRef(null);
  useOutsideCloseModal(wrapperRef, props.state, props.setState);

  return (
    <div
      className={props.addClass}
      ref={wrapperRef}
      // style={{ width: 'inherit', height: 'inherit', display: 'inherit' }}
    >
      {props.children}
    </div>
  );
}

OutsideCloseModal.propTypes = {
  children: PropTypes.element.isRequired,
  state: PropTypes.bool,
  setState: PropTypes.func,
  addClass: PropTypes.string,
};

export default OutsideCloseModal;
