import actionTypes from "../../../redux/constants";

const initialState = {
  isHeaderPaymentFixed: false,
};

export const headerPayment = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case actionTypes.SET_FIXED_HEADER_PAYMENT: {
      return {
        ...state,
        isHeaderPaymentFixed: payload,
      };
    }

    default:
      return state;
  }
};
