import { useState } from 'react';
import styles from './styles.module.scss';
import { Link } from 'react-router-dom';

const EditFullName = ({
  firstName,
  secondName,
  middleName,
  setFirstName,
  setSecondName,
  setMiddleName,
  setShowEditField,
  onCancelClick,
  // onSaveDispatch
}) => {
  const [newFirstName, setNewFirstName] = useState(firstName);
  const [newSecondName, setNewSecondName] = useState(secondName);
  const [newMiddleName, setNewMiddleName] = useState(middleName);

  const setFirstNameHandler = (e) => {
    setNewFirstName(e.target.value);
  };

  const setSecondNameHandler = (e) => {
    setNewSecondName(e.target.value);
  };

  const setMiddleNameHandler = (e) => {
    setNewMiddleName(e.target.value);
  };

  const saveBtnHandler = () => {
    setFirstName(newFirstName);
    setSecondName(newSecondName);
    setMiddleName(newMiddleName);
    setShowEditField(false);
    // onSaveDispatch(name, secondName, middleName)
  };

  return (
    <div className={styles.editFullname}>
      <div className={styles.editFullname__inputsField}>
        <div className={styles.editFullname__input}>
          <label className={styles.editFullname__label} htmlFor="firstName">
            First Name{' '}
            <span className={styles.editFullname__label__required}>*</span>
          </label>
          <input
            type="text"
            name="firstName"
            value={newFirstName}
            id="firstName"
            onChange={setFirstNameHandler}
          />
        </div>
        <div className={styles.editFullname__input}>
          <label className={styles.editFullname__label} htmlFor="secondName">
            Middle Name{' '}
            <span className={styles.editFullname__label__optional}>
              (optional)
            </span>
          </label>

          <input
            type="text"
            name="middleName"
            value={newMiddleName}
            id="middleName"
            onChange={setMiddleNameHandler}
          />
        </div>
        <div className={styles.editFullname__input}>
          <label className={styles.editFullname__label} htmlFor="secondName">
            Second Name{' '}
            <span className={styles.editFullname__label__required}>*</span>
          </label>

          <input
            type="text"
            name="secondName"
            value={newSecondName}
            id="secondName"
            onChange={setSecondNameHandler}
          />
        </div>
      </div>
      <div className={styles.editFullname__note}>
        Please also avoid adding any unusual capitalisation, punctuation or
        symbols.{' '}
        <Link to="/learn-more" className={styles.editFullname__link}>
          Learn more
        </Link>
        .
      </div>
      <div className={styles.editFullname__addMore}>
        <span className={styles.editFullname__addMore__boldText}>
          Other Names
        </span>{' '}
        <Link className={styles.editFullname__addMore__link} to="/add-more">
          Add other names
        </Link>
      </div>
      <div className={styles.editFullname__buttons}>
        <button
          className={`${styles.editFullname__buttons__btn} ${styles.editFullname__buttons__btn__cancel}`}
          onClick={onCancelClick}
        >
          Cancel
        </button>
        <button
          className={`${styles.editFullname__buttons__btn} ${styles.editFullname__buttons__btn__save}`}
          onClick={saveBtnHandler}
        >
          Save changes
        </button>
      </div>
    </div>
  );
};

export default EditFullName;
