class SessionApi {
  static register(firstName, lastName, email, password1, password2) {
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        firstName,
        lastName,
        email,
        password1,
        password2,
      }),
    };
    return fetch('https://voxdigital.by:4000/signup', requestOptions)
      .then((res) => res.json())
      .then((registationResponse) => {
        return registationResponse;
      })
      .catch((err) => {
        const error = new Error(err);
        error.status = 500;
        throw error;
      });
  }
}

export default SessionApi;
