import actionTypes from '../constants';

export const addToBucketStart = () => {
  return {
    type: actionTypes.ADD_TO_BUCKET_START,
  };
};

export const addToBucketSuccess = (payload) => {
  return {
    type: actionTypes.ADD_TO_BUCKET_SUCCESS,
    payload,
  };
};

