import actionTypes from '../constants';

export function loginStart() {
  return { type: actionTypes.LOG_IN_START };
}

export function loginSuccess(payload) {
  return { type: actionTypes.LOG_IN_SUCCESS, payload };
}

export function loginError(payload) {
  return { type: actionTypes.LOG_IN_ERROR, payload };
}

export function loginResetErrorMessage() {
  return { type: actionTypes.LOG_IN_RESET_ERROR_MESSAGE };
}

export function refreshTokenStart() {
  return { type: actionTypes.REFRESH_TOKEN_START };
}

export function refreshTokenSuccess(payload) {
  return { type: actionTypes.REFRESH_TOKEN_SUCCESS, payload };
}

export function refreshTokenError(payload) {
  return { type: actionTypes.REFRESH_TOKEN_ERROR, payload };
}
