import actionTypes from '../constants';

const initialState = {
  mainMenu: {
    isMiniSize: false,
    type: 'DEFAULT',
  },
  submenu: {
    isActive: false,
  },
};

export const headerMenu = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case actionTypes.TOGGLE_SUBMENU: {
      return {
        ...state,
        submenu: {
          ...state.submenu,
          isActive: payload.data,
        },
      };
    }
    case actionTypes.SET_HEADER_MAIN_MENU_SIZE: {
      return {
        ...state,
        mainMenu: {
          ...state.mainMenu,
          isMiniSize: payload,
        },
      };
    }

    case actionTypes.SET_HEADER_MAIN_MENU_TYPE: {
      const { type } = payload;
      return {
        ...state,
        mainMenu: {
          ...state.mainMenu,
          type: type,
        },
      };
    }
    default: {
      return state;
    }
  }
};
