import {
  loadCommunityDataStart,
  loadCommunityDataSuccess,
  loadCommunityDataError,
} from '../../actions/gridPageActions';
import { updateBucketProducts } from '../../actions/bucket';
import { logoutUser } from '../logout';
import { fetchWithAuth } from '../../../http/fetchWithAuth';

export const getCategoriesData = () => async (dispatch) => {
  dispatch(loadCommunityDataStart());
  try {
    const response = await fetchWithAuth(
      'https://voxdigital.by:4000/sponsors-grid',
      {
        method: 'GET',
      }
    );

    if (!response || response.status === 500) {
      dispatch(logoutUser());
      dispatch(
        loadCommunityDataError({
          error: {
            message: 'Something went wrong. Please try again',
            statusCode: 500,
          },
        })
      );
      return;
    }

    if (response.status === 401) {
      dispatch(logoutUser());
      dispatch(
        loadCommunityDataError({
          error: {
            message: 'You are not authorized. Please try to login again',
            statusCode: 401,
          },
        })
      );
      return;
    }
    const responseData = await response.json();
    if (responseData.error) {
      const error = new Error(responseData.error.message);
      error.status = responseData.error.status;
      throw error;
    }

    const { payload } = responseData;

    if (!responseData.isAuth) {
      dispatch(
        loadCommunityDataError({
          error: { message: 'Authenticated failed. Please try to login again' },
        })
      );
      dispatch(logoutUser());
      return;
    }
    const communitiesWithChosenRegions = payload.community.map((cmt) => {
      const chosenRegions = cmt.regions.filter((reg) => reg.chosen);
      if (chosenRegions.length) {
        return { ...cmt, regions: chosenRegions };
      }
      return null;
    });
    const addedToCartRegions = communitiesWithChosenRegions.filter(
      (el) => el !== null
    );
    const addedToCartProduct = [...addedToCartRegions].sort(
      (a, b) => Number(a.community_id) - Number(b.community_id)
    );
    let productsCount = 0;
    addedToCartProduct.map((el) =>
      el.regions.map((reg) => reg.chosen === true && productsCount++)
    );
    dispatch(
      updateBucketProducts({
        products: addedToCartProduct,
        total_paid: payload.total_paid,
        total_pledged: payload.total_pledged,
        total_outstanding: payload.total_outstanding,
        symbol_placement: payload.symbol_placement,
        productsCount,
      })
    );
    dispatch(loadCommunityDataSuccess(payload));
  } catch (err) {
    dispatch(
      loadCommunityDataError({
        error: { message: err.message, statusCode: err.status || 500 },
      })
    );
    throw err;
  }
};
