import React from 'react';
import styles from './styles.module.scss';
import plusImg from '../../../assets/images/ui-icons/plus.png';
import plusImgHover from '../../../assets/images/ui-icons/plusHover.png';
import tickSign from '../../../assets/images/tick-sign.svg';
import minus from '../../../assets/images/ui-icons/minus.png';
import ArrowImage from '../../../assets/images/ui-icons/grayArrowLeft.svg';
import ArrowWhiteImage from '../../../assets/images/ui-icons/arrow_login_white.png';
import { Link } from 'react-router-dom';

const types = {
  default: 'btn-default',
  defaultHover: 'btn-default-hover',
  clicked: 'btn-clicked',
  login: 'btn-login',
  backButton: 'back-button',
  sponsor: 'btn-sponsor',
  link: 'btn-link',
  linkWithArrow: 'btn-link-withArrow',
};

const Button = ({
  text,
  clickHandler,
  appearance = 'default',
  addCss,
  addClass = '',
  disabled = false,
  clicked,
  image,
  children,
  href = '',
  revert = false,
  arrowWhite = false,
  arrowBlack = false,
  uppercase,
  tabIndex,
}) => {
  const btnClass = types[appearance] ? types[appearance] : '';

  if (appearance === 'payment') {
    return (
      <button
        className={`${styles[btnClass]} ${styles.payment}`}
        disabled={disabled}
        style={{ addCss }}
        onClick={clickHandler}
      >
        {text || children}
      </button>
    );
  }

  if (appearance === 'pre-payment') {
    return (
      <button
        className={`${styles[btnClass]} ${styles.prePayment}`}
        disabled={disabled}
        style={{ addCss }}
        onClick={clickHandler}
      >
        {text || children}
      </button>
    );
  }

  if (appearance === 'login') {
    return (
      <button
        className={`${styles[btnClass]} ${addClass ? styles[addClass] : ''}`}
        disabled={disabled}
        style={{ addCss }}
        onClick={clickHandler}
        tabIndex={tabIndex}
      >
        {text ? text : children || 'Sign in'}
        {image && (
          <span className={`${styles[btnClass + '-image']}`}>
            <img src={image} alt="arrow right" />
          </span>
        )}
      </button>
    );
  }

  if (appearance === 'backButton') {
    return (
      <button
        className={`${styles[btnClass]} ${addClass ? styles[addClass] : ''}`}
        disabled={disabled}
        style={{ addCss }}
        onClick={clickHandler}
        tabIndex={tabIndex}
      >
        {!revert && (
          <span className={`${styles[btnClass + '-image']}`}>
            <img src={image ? image : ArrowImage} alt="arrow left" />
          </span>
        )}
        {text ? text : children || 'Back'}
        {revert && (
          <span className={`${styles[btnClass + '-image__revert']}`}>
            <img src={image ? image : ArrowWhiteImage} alt="arrow left" />
          </span>
        )}
      </button>
    );
  }

  if (appearance === 'sponsor') {
    return (
      <button
        className={`${styles[btnClass]} ${
          clicked ? styles.clicked : styles.default
        }`}
        disabled={disabled}
        style={{ addCss }}
        onClick={clickHandler}
        tabIndex={tabIndex}
      >
        {text && text}
        {clicked ? (
          <div>
            <img className={styles['svg-image']} src={tickSign} alt="buton" />
            <img
              className={`${styles['svg-image-hover']} ${styles['minus-image']}`}
              src={minus}
              alt="remove from bucket"
            />
          </div>
        ) : (
          <div>
            <img
              className={styles['svg-image']}
              src={plusImg}
              alt="add to bucket"
            />
            <img
              className={styles['svg-image-hover']}
              src={plusImgHover}
              alt="remove from bucket"
            />
          </div>
        )}
      </button>
    );
  }

  if (appearance === 'link') {
    return (
      <Link
        to={href || '#'}
        className={`${styles[btnClass]} ${styles[addClass]}`}
        disabled={disabled}
        style={{ addCss }}
        onClick={clickHandler}
        tabIndex={tabIndex}
      >
        {text || children}
      </Link>
    );
  }

  return (
    <button
      className={`${styles[btnClass]} ${styles.default} ${
        addClass ? styles[addClass] : ''
      } ${uppercase ? styles.uppercase : ''}`}
      disabled={disabled}
      style={{ addCss }}
      onClick={clickHandler}
      tabIndex={tabIndex}
    >
      {revert ? (
        <>
          {image && (
            <>
              <img className={styles['svg-image']} src={image} alt="button" />
            </>
          )}
          {text || children}
        </>
      ) : (
        <>
          {text ? text : children}
          {image && (
            <>
              <img
                className={`${styles['image']} ${revert ? styles.revert : ''}`}
                src={image}
                alt="button"
              />
            </>
          )}
        </>
      )}
    </button>
  );
};
export default Button;
