import { logoutStart, logoutError, logoutSuccess } from '../../actions/logout';
import { deleteToken, deleteUser } from '../../../http/localStorage';

export const logoutUser = () => async (dispatch, getState) => {
  try {
    dispatch(logoutStart());

    deleteToken();
    deleteUser();

    dispatch(logoutSuccess());

    return true;
  } catch (error) {
    logoutError({ error: error.message });
    throw error;
  }
};
