import styles from './styles.module.scss';
import { useLocation, Link, useRouteMatch } from 'react-router-dom';

const SidebarMenu = ({ menuData }) => {
  const { pathname } = useLocation();
  const { url } = useRouteMatch();

  const linksArr = pathname.split('/');
  const currentPage = isNaN(+[...linksArr][linksArr.length - 1])
    ? [...linksArr].pop()
    : [...linksArr][linksArr.length - 2];

  return (
    <div className={styles.sidebarMenu}>
      {menuData.map((el, id) => {
        return (
          <div key={id + '__' + el.label} className={styles.sidebarMenu__wrap}>
            <div className={styles.sidebarMenu__header}>
              <div className={styles.sidebarMenu__header__image}>
                <img src={el.image} alt={el.label + ' icon'} />
              </div>
              <div className={styles.sidebarMenu__header__title}>
                {el.label}
              </div>
            </div>
            {/* links */}
            <ul className={styles.sidebarMenu__links}>
              {el.links.map((link, idx) => {
                return (
                  <li
                    key={idx + '__' + link.title}
                    className={styles.sidebarMenu__links__linkWrap}
                  >
                    <Link
                      to={`${url}/${link.url}`}
                      className={`${styles.sidebarMenu__links__link} ${
                        currentPage === link.url ? styles.active : ''
                      }`}
                    >
                      {link.title}
                    </Link>
                  </li>
                );
              })}
            </ul>
          </div>
        );
      })}
    </div>
  );
};

export default SidebarMenu;
