import React, { useState } from 'react';
import styles from './styles.module.scss';

const SingleCategory = ({
  idx,
  categoryId,
  hoverImage,
  image,
  title,
  description,
  categoriesCount,
  clickHandler,
}) => {
  const [isImageHovered, setIsImageHovered] = useState(false);
  const onCategoryMouseOver = () => {
    setIsImageHovered(true);
  };

  const onCategoryMouseLeave = () => {
    setIsImageHovered(false);
  };

  const onCategoryClick = () => {
    setIsImageHovered(true);
    clickHandler(categoryId);
  };

  const countCategoryClass = (catNumbers) => {
    if (catNumbers <= 9 && catNumbers % 3 === 0) {
      return 3;
    }
    if (catNumbers % 4 === 0) {
      return 4;
    }
    return catNumbers;
  };

  return (
    <li
      key={idx}
      onClick={onCategoryClick}
      onMouseOver={onCategoryMouseOver}
      onMouseLeave={onCategoryMouseLeave}
      className={`${styles.category} ${
        styles['categories__count-' + countCategoryClass(categoriesCount) || '']
      }`}
    >
      <span
        className={`${styles.category__cross} ${styles['left-top']}`}
      ></span>
      <span
        className={`${styles.category__cross} ${styles['left-bottom']}`}
      ></span>
      <span
        className={`${styles.category__cross} ${styles['right-top']}`}
      ></span>
      <span
        className={`${styles.category__cross} ${styles['right-bottom']}`}
      ></span>

      <div className={styles.category__image}>
        <div className={styles.category__image__wrap}>
          <img
            src={`https://voxdigital.by${isImageHovered ? hoverImage : image}`}
            alt={title}
          />
        </div>
        {description && (
          <span className={styles.category__description}>{description}</span>
        )}

        <p className={styles.category__title}>{title}</p>
      </div>
    </li>
  );
};

export default SingleCategory;
