import { useState } from 'react';

import styles from './styles.module.scss';
import UserInfo from '../../../../components/molecules/UserInfo';
import PolandIcon from '../../../../assets/images/countriesIcons/poland.jpg';

const data = {
  address: '219 Kensington High Street',
  city: 'London',
  postCode: 'W12 9RT',
  state: '',
  country: 'Great Britain',
  email: 'ads@email.com',
  countryCode: '+375',
  operatorCode: '29',
  operatorCodeOptions: ['29', '25', '33', '44'],
  phoneNumber: '345-456-679',
  countryIcon: PolandIcon,
  countryId: 'PL',
  secondAddress: '',
};

const ContactDetails = (props) => {
  const [userAddress, setUserAddress] = useState(data.address);
  const [userSecondAddress, setUserSecondAddress] = useState(
    data.secondAddress
  );
  const [userCity, setUserCity] = useState(data.city);
  const [userPostCode, setUserPostCode] = useState(data.postCode);
  const [userState, setUserState] = useState(data.state);
  const [userCountry, setUserCountry] = useState(data.country);

  const [userCountryCode, setUserCountryCode] = useState(data.countryCode);
  const [userOperatorCode, setUserOperatorCode] = useState(data.operatorCode);
  const [userOperatorCodeOptions, setUserOperatorCodeOptions] = useState(
    data.operatorCodeOptions
  );
  const [userPhoneNumber, setUserPhoneNumber] = useState(data.phoneNumber);
  const [userCountryIcon, setUserCountryIcon] = useState(data.countryIcon);
  const [userCountryId, setUserCountryId] = useState(data.countryId);

  const [userEmail, setUserEmail] = useState(data.email);

  // controllers of edit fields
  const [showBillingAddressEditField, setShowBillingAddressEditField] =
    useState(false);
  const [showEmailEditField, setShowEmailEditField] = useState(false);
  const [showPhoneEditField, setShowPhoneEditField] = useState(false);

  const resetBillingAddress = () => {
    setUserAddress(userAddress);
    setUserCity(userCity);
    setUserPostCode(userPostCode);
    setUserState(userState);
    setUserCountry(userCountry);
    setShowBillingAddressEditField(false);
  };

  const resetEmail = () => {
    setUserEmail(userEmail);
    setShowEmailEditField(false);
  };

  const resetPhone = () => {
    setUserCountryCode(userCountryCode);
    setUserOperatorCode(userOperatorCode);
    setUserPhoneNumber(userPhoneNumber);
    setShowPhoneEditField(false);
  };

  return (
    <div className={styles.details}>
      <div className={styles.details__wrap}>
        <div className={styles.details__title}>Contact Details</div>
        <div className={styles.details__userData}>
          <div className={styles.details__userData__editFields}>
            <UserInfo
              option="billingAddress"
              address={userAddress}
              secondAddress={userSecondAddress}
              city={userCity}
              postCode={userPostCode}
              country={userCountry}
              state={userState}
              setAddress={setUserAddress}
              setSecondAddress={setUserSecondAddress}
              setCity={setUserCity}
              setPostCode={setUserPostCode}
              setCountry={setUserCountry}
              setState={setUserState}
              showEditField={showBillingAddressEditField}
              setShowEditField={setShowBillingAddressEditField}
              closeButtonHandler={resetBillingAddress}
            />
            <UserInfo
              option="email"
              email={userEmail}
              setEmail={setUserEmail}
              showEditField={showEmailEditField}
              setShowEditField={setShowEmailEditField}
              closeButtonHandler={resetEmail}
            />
            <UserInfo
              option="phone"
              phoneNumber={userPhoneNumber}
              setPhoneNumber={setUserPhoneNumber}
              countryCode={userCountryCode}
              setCountryCode={setUserCountryCode}
              operatorCode={userOperatorCode}
              setOperatorCode={setUserOperatorCode}
              operatorCodeOptions={userOperatorCodeOptions}
              setOperatorCodeOptions={setUserOperatorCodeOptions}
              countryIcon={userCountryIcon}
              setCountryIcon={setUserCountryIcon}
              countryId={userCountryId}
              setCountryId={setUserCountryId}
              showEditField={showPhoneEditField}
              setShowEditField={setShowPhoneEditField}
              closeButtonHandler={resetPhone}
            />
          </div>

          <div className={styles.details__userData__deleteField}>
            <UserInfo option="deleteAccount" />
          </div>
        </div>
      </div>
    </div>
  );
};

export default ContactDetails;
