import { useState } from 'react';
import styles from './styles.module.scss';

const EditDepartment = ({
  department,
  setDepartment,
  setShowEditField,
  onCancelClick,
  // onSaveDispatch
}) => {
  const [newDepartment, setNewDepartment] = useState(department);

  const setDepartmentHandler = (e) => {
    setNewDepartment(e.target.value);
  };

  const saveBtnHandler = () => {
    setDepartment(newDepartment);
    setShowEditField(false);
    // onSaveDispatch(name, secondName, middleName)
  };

  return (
    <div className={styles.editDepartment}>
      <div className={styles.editDepartment__inputsField}>
        <div className={styles.editDepartment__input}>
          <label className={styles.editDepartment__label} htmlFor="department">
            Department Name{' '}
            <span className={styles.editDepartment__label__required}>*</span>
          </label>
          <input
            type="text"
            name="department"
            value={newDepartment}
            id="department"
            onChange={setDepartmentHandler}
          />
        </div>
      </div>

      <div className={styles.editDepartment__buttons}>
        <button
          className={`${styles.editDepartment__buttons__btn} ${styles.editDepartment__buttons__btn__cancel}`}
          onClick={onCancelClick}
        >
          Cancel
        </button>
        <button
          className={`${styles.editDepartment__buttons__btn} ${styles.editDepartment__buttons__btn__save}`}
          onClick={saveBtnHandler}
        >
          Save changes
        </button>
      </div>
    </div>
  );
};

export default EditDepartment;
