import { useState } from 'react';
import styles from './styles.module.scss';
import Select from 'react-select';
import PolandIcon from '../../../../assets/images/countriesIcons/poland.jpg';

const customStyles = {
  control: (base) => ({
    ...base,
    height: '100%',
    border: 'none',
  }),
  menu: (provided, state) => ({
    ...provided,
    width: 'max-content',
  }),
  option: (provided, state) => ({
    ...provided,
    fontFamily: 'Roboto',
    fontSize: 14,
    width: 200,
  }),
  indicatorSeparator: (styles) => ({ display: 'none' }),
};

const EditPhone = ({
  phoneNumber,
  setPhoneNumber,
  countryCode,
  setCountryCode,
  operatorCode,
  setOperatorCode,
  operatorCodeOptions,
  setOperatorCodeOptions,
  countryIcon,
  setCountryIcon,
  countryId,
  setCountryId,
  setShowEditField,
  onCancelClick,
  // onSaveDispatch
}) => {
  const [newPhoneNumber, setNewPhoneNumber] = useState(phoneNumber);
  const [newCountryCode, setNewCountryCode] = useState(countryCode);
  const [newOperatorCode, setNewOperatorCode] = useState(operatorCode);
  const [newCountryIcon, setNewCountryIcon] = useState(countryIcon);
  const [newCountryId, setNewCountryId] = useState(countryId);
  const [newOperatorCodeOptions, setNewOperatorCodeOptions] = useState(
    operatorCodeOptions.map((el) => ({ label: el, value: el }))
  );

  const selectHandler = (e) => {
    setNewCountryCode(e.code);
    setNewPhoneNumber('');
    // setNewOperatorCode(e.operatorCodeOptions[0]);
    setNewCountryIcon(e.icon);
    setNewCountryId(e.value);
    // setNewOperatorCodeOptions(e.operatorCodeOptions);
  };

  // const selectOperatorCodeHandler = (e) => {
  //   setNewOperatorCode(e.value);
  // };

  const saveBtnHandler = () => {
    setPhoneNumber(newPhoneNumber);
    setCountryCode(newCountryCode);
    setOperatorCode(newOperatorCode);
    setCountryIcon(newCountryIcon);
    setCountryId(newCountryId);
    setOperatorCodeOptions(newOperatorCodeOptions);
    setShowEditField(false);
    // onSaveDispatch(name, secondName, middleName)
  };

  const setPhoneNumberHandler = (e) => {
    setNewPhoneNumber(e.target.value);
  };

  const options = [
    {
      value: 'PL',
      label: (
        <div className={styles.editPhone__selectView}>
          <img src={PolandIcon} alt="county icon" />
          <span className={styles.editPhone__selectView__code}>+48</span>
          <span className={styles.editPhone__selectView__country}>Poland</span>
        </div>
      ),
      code: '+48',
      // operatorCodeOptions: [
      //   { label: '7', value: '7' },
      //   { label: '8', value: '8' },
      //   { label: '9', value: '9' },
      // ],
      icon: PolandIcon,
    },
    {
      value: 'QW',
      label: (
        <div className={styles.editPhone__selectImage}>
          <img src={PolandIcon} alt="country icon" />
          <span className={styles.editPhone__selectView__code}>+52</span>
          <span className={styles.editPhone__selectView__country}>
            Great Britain
          </span>
        </div>
      ),
      code: '+376',
      // operatorCodeOptions: [
      //   { label: '1', value: '1' },
      //   { label: '2', value: '2' },
      //   { label: '3', value: '3' },
      // ],
      icon: PolandIcon,
    },
    {
      value: 'ZA',
      label: (
        <div className={styles.editPhone__selectImage}>
          <img src={PolandIcon} alt="county icon" />
          <span className={styles.editPhone__selectView__code}>+375</span>
          <span className={styles.editPhone__selectView__country}>Belarus</span>
        </div>
      ),
      code: '+375',
      // operatorCodeOptions: [
      //   { label: '4', value: '4' },
      //   { label: '5', value: '5' },
      //   { label: '6', value: '6' },
      // ],
      icon: PolandIcon,
    },
  ];

  const defaultValue = {
    value: newCountryId,
    label: (
      <div className={styles.editPhone__selectImage}>
        <img src={newCountryIcon} alt={newCountryId + ' - icon'} />
      </div>
    ),
    code: newCountryCode,
    operatorCodeOptions: [newOperatorCode[0]],
  };

  return (
    <div className={styles.editPhone}>
      <label className={styles.editPhone__label} htmlFor="email">
        Phone Number{' '}
      </label>
      <div className={styles.editPhone__inputsField}>
        <div className={styles.editPhone__selectInput}>
          <div className={styles.editPhone__selectInput__select}>
            <Select
              // defaultValue={defaultValue}
              value={defaultValue}
              options={options}
              hideSelectedOptions={false}
              className={`${styles.editPhone__select} `}
              onChange={selectHandler}
              styles={customStyles}
              isSearchable={false}
              tabIndex="1"
            />
          </div>
          {/* <div
            className={`${styles.editPhone__selectInput__countryCode} ${
              formData.phone.errors.length > 0 ? styles.error : ''
            }`}
          >
            {formData.countryCode}
          </div> */}
          {/* <div>
            {
              <Select
                defaultValue={newOperatorCodeOptions[0]}
                options={newOperatorCodeOptions}
                hideSelectedOptions={false}
                className={`${styles.editPhone__select} `}
                //   ${
                //     formData.phone.errors.length > 0 ? styles.error : ''
                //   }
                //   `}
                onChange={selectOperatorCodeHandler}
                styles={customStyles}
                isSearchable={false}
              />
            }
          </div> */}
          {/* <div>{newOperatorCode}</div> */}
          <div className={styles.editPhone__input}>
            <input
              className={`${styles.editPhone__input} `}
              //   ${
              //     styles.inputWithSelect
              //   } ${formData.phone.errors.length > 0 ? styles.error : ''}`}
              type="text"
              name="phoneNumber"
              value={newCountryCode + newPhoneNumber}
              onChange={setPhoneNumberHandler}
              id="phoneNumber"
              tabIndex="2"
            />
          </div>
        </div>
      </div>

      <div className={styles.editPhone__buttons}>
        <button
          className={`${styles.editPhone__buttons__btn} ${styles.editPhone__buttons__btn__cancel}`}
          onClick={onCancelClick}
          tabIndex="3"
        >
          Cancel
        </button>
        <button
          className={`${styles.editPhone__buttons__btn} ${styles.editPhone__buttons__btn__save}`}
          onClick={saveBtnHandler}
          tabIndex="4"
        >
          Save changes
        </button>
      </div>
    </div>
  );
};

export default EditPhone;
