import React from 'react';
import styles from './styles.module.scss';
import { connect } from 'react-redux';

const CommunityHeader = ({
  title,
  community_icon,
  brand_logo,
  isMainMenuMiniSize,
}) => {
  // const communityImagePath =
  //   title.split(' ').length === 1 ? title : title.replace(' ', '-');
  // const folderForCommunityImage =
  //   window.document.innerWidth < 768 ? 'small' : 'large';
  return (
    <div
      className={`${styles.communityHeader} ${
        isMainMenuMiniSize ? '' : styles.withoutTopBorder
      }`}
    >
      <div
        className={`${styles.communityHeader__block} ${styles.communityHeader__block__first}`}
      >
        <img
          className={styles.communityHeader__block__first__image}
          src={`https://voxdigital.by${community_icon}`}
          alt={title}
        />
        <p className={styles.communityHeader__block__first__text}>{title}</p>
      </div>
      <div
        className={`${styles.communityHeader__block} ${styles.communityHeader__block__second}`}
      >
        <p className={styles.communityHeader__block__second__text}>
          Supported by:
        </p>
        <img
          className={styles.communityHeader__block__second__image}
          src={`https://voxdigital.by${brand_logo}`}
          alt={title + ' - sponsor logo'}
        />
      </div>
    </div>
  );
};

export default connect(null, null)(CommunityHeader);
