import React, { useState, useEffect } from 'react';
import styles from './styles.module.scss';
import ScrollToTopImg from '../../../assets/images/ui-icons/scrollToTop.png';

const ScrollToTop = () => {
  const [isScrollBtnActive, setIsScrollBtnActive] = useState(false);
  useEffect(() => {
    function showScrollToTopBtn() {
      if (window.scrollY >= 400) {
        setIsScrollBtnActive(true);
      } else {
        setIsScrollBtnActive(false);
      }
    }

    document.addEventListener('scroll', showScrollToTopBtn);
    return () => document.removeEventListener('scroll', showScrollToTopBtn);
  }, []);

  const scrollHandler = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  return (
    <div
      className={`${styles.scrollToTop} ${
        isScrollBtnActive ? styles.scrollToTop__show : ''
      }`}
      onClick={scrollHandler}
    >
      <div className={styles.scrollToTop__image}>
        <img src={ScrollToTopImg} alt="Back to top arrow" />
      </div>
      <p className={styles.scrollToTop__text}>Back to top</p>
    </div>
  );
};

export default ScrollToTop;
