import React from 'react';
import styles from './styles.module.scss';

const SponsorsBenefit = ({image, title, text}) => {
    return (
        <li className={styles['sponsorsBenefit']}>
            <div className={styles['sponsorsBenefit__wrap']}>
                    <div className={styles['sponsorsBenefit__header']}>
                        <div className={styles['sponsorsBenefit__image__wrap']}>
                            <img className={styles['sponsorsBenefit__image']} src={image} alt="sponsor" />
                        </div>
                        <div className={styles['sponsorsBenefit__title']}>{title}</div>
                    </div>
                    <p className={styles['sponsorsBenefit__text']}>{text}</p>
            </div>
        </li>
    )
}

export default SponsorsBenefit;