export const isMainMenuMiniSizeSelector = (state) =>
  state.headerMenu.mainMenu.isMiniSize;

export const getMainMenuTypeSelector = (state) =>
  state.headerMenu.mainMenu.type;

export const isSubmenuActiveSelector = (state) =>
  state.headerMenu.submenu.isActive;

export const isHeaderPaymentFixedSelector = (state) => {
  return state.headerPayment.isHeaderPaymentFixed;
};

export const getInBucketProductsSelector = (state) => {
  return state.cart.products;
};

export const getDefaultCategoriesSelector = (state) => {
  return state.cart.defaultCategories;
};

export const getCurrentPaymentStepSelector = (state) => {
  return state.cart.paymentDetails.paymentStep;
};

export const getAppInfoSelector = (state) => {
  return state.appInfo;
};

export const getSignUpSelector = (state) => {
  return state.signUpData;
};

export const getIsMainMenuMiniSize = createSelector(
  isMainMenuMiniSizeSelector,
  (isMainMenuMiniSize) => isMainMenuMiniSize
);

export const getInBucketProducts = createSelector(
  getInBucketProductsSelector,
  (inBucketProducts) => inBucketProducts
);

export const getDefaultCategories = createSelector(
  getDefaultCategoriesSelector,
  (defaultCategories) => defaultCategories
);

