export const getSignUpData = createSelector(getSignUpSelector, (signUpData) => {
  const passwordValidationFailed = {
    status: false,
    message: '',
  };
  const { hasMin8Char, hasMin15Char, hasNumber, hasLowerCaseLetter } =
    signUpData.passwordErrors;

  if (
    !hasMin8Char.status ||
    // !hasMin15Char.status ||
    !hasNumber.status ||
    !hasLowerCaseLetter.status
  ) {
    passwordValidationFailed.status = true;
    if (!hasMin8Char.status) {
      passwordValidationFailed.message = hasMin8Char.message;
    } else if (!hasNumber.status) {
      passwordValidationFailed.message = hasNumber.message;
    } else if (!hasLowerCaseLetter.status) {
      passwordValidationFailed.message = hasLowerCaseLetter.message;
    } else {
      passwordValidationFailed.message =
        'Something went wrong. Please try again';
    }
  }
  const isSignUpFirstStepBtnDisabled =
    !signUpData.email ||
    signUpData.emailErrors.length > 0 ||
    !signUpData.firstName ||
    signUpData.firstNameErrors.length > 0 ||
    !signUpData.lastName ||
    signUpData.lastNameErrors.length > 0;

  return {
    firstName: signUpData.firstName,
    lastName: signUpData.lastName,
    firstNameErrors: signUpData.firstNameErrors,
    firstNameValidationFailed:
      signUpData.firstName && signUpData.firstNameErrors.length > 0,
    lastNameErrors: signUpData.lastNameErrors,
    lastNameValidationFailed:
      signUpData.lastName && signUpData.lastNameErrors.length > 0,
    email: signUpData.email,
    emailErrors: signUpData.emailErrors,
    emailValidationFailed:
      signUpData.email && signUpData.emailErrors.length > 0,
    isSignUpFirstStepBtnDisabled,
    country: {
      label: signUpData.country.label,
      value: signUpData.country.value,
    },
    passwordErrors: {
      hasMin8Char: hasMin8Char,
      hasMin15Char: hasMin15Char,
      hasNumber: hasNumber,
      hasLowerCaseLetter: hasLowerCaseLetter,
    },
    passwordValidationFailed,
    currentStep: signUpData.step,
    loader: signUpData.loader,
    error: signUpData.error,
    isAuth: signUpData.isAuth,
  };
});

export const getIsAuth = (state) => state.session.isAuth;
export const getCategorygridData = (state) => state.categorygrid;
export const getBrandData = (state) => state.brand_id;

export default createSelector(
  [
    getAppInfoSelector,
    getOfferedProductsSelector,
    getInBucketProductsCountSelector,
    getInBucketProductsTotalPriceSelector,
    getPriceCurrencySelector,
    getDefaultSponsorsSelector,
    getIsMainMenuMiniSize,
    isSubmenuActiveSelector,
    isHeaderPaymentFixedSelector,
    getInBucketProducts,
    getDefaultCategories,
    // getInBucketProductsWithCategoriesTitle,
    getInvoiceReceiveSelector,
    getCurrentPaymentStepSelector,
    getSignUpData,
    getMainMenuTypeSelector,
    getIsAuth,
    getCategorygridData,
    getBrandData,
  ],
  (
    appInfo,
    offeredProducts,
    inBucketProductsCount,
    inBucketProductsTotalPrice,
    priceCurrency,
    defaultSponsors,
    isMainMenuMiniSize,
    isSubmenuActive,
    isHeaderPaymentFixed,
    inBucketProducts,
    defaultCategories,
    // inBucketProductsWithCategoriesTitle,
    invoiceReceive,
    currentPaymentStep,
    creditCardData,
    signUpData,
    mainMenuType,
    isAuth,
    categorygridData,
    brandData
  ) => {
    return {
      appInfo,
      offeredProducts,
      inBucketProductsCount,
      inBucketProductsTotalPrice,
      priceCurrency,
      defaultSponsors,
      isMainMenuMiniSize,
      isSubmenuActive,
      isHeaderPaymentFixed,
      inBucketProducts,
      defaultCategories,
      // inBucketProductsWithCategoriesTitle,
      invoiceReceive,
      currentPaymentStep,
      creditCardData,
      signUpData,
      mainMenuType,
      isAuth,
      categorygridData,
      brandData,
    };
  }
);
