import actionTypes from '../constants';

export const openAuthWindow = () => {
  return {
    type: actionTypes.OPEN_AUTH_WINDOW,
  };
};

export const closeAuthWindow = () => {
  return {
    type: actionTypes.CLOSE_AUTH_WINDOW,
  };
};

export const loginStart = () => {
  return {
    type: actionTypes.LOGIN_START,
  };
};

export const loginInputEmail = (payload) => {
  return {
    type: actionTypes.LOGIN_INPUT_EMAIL,
    payload,
  };
};
