import styles from './styles.module.scss';
import GreenTick from '../../../../assets/images/ui-icons/green-tick.png';

const pendingIntives = [
  {
    firstName: 'Paul',
    lastName: 'Philips',
    department: 'Finance',
    invitedDate: 'Oct 16, 2017',
    reminder: { isReminded: false, date: null },
  },
  {
    firstName: 'Alex',
    lastName: 'Philips',
    department: 'Finance',
    invitedDate: 'Oct 16, 2017',
    reminder: { isReminded: false, date: null },
  },
  {
    firstName: 'John',
    lastName: 'Philips',
    department: 'Finance',
    invitedDate: 'Oct 16, 2017',
    reminder: { isReminded: false, date: null },
  },
  {
    firstName: 'Julia',
    lastName: 'Philips',
    department: 'Finance',
    invitedDate: 'Oct 16, 2017',
    reminder: { isReminded: false, date: null },
  },
  {
    firstName: 'Kate',
    lastName: 'Philips',
    department: 'Finance',
    invitedDate: 'Oct 16, 2017',
    reminder: { isReminded: false, date: null },
  },
  {
    firstName: 'Alesya',
    lastName: 'Philips',
    department: 'Finance',
    invitedDate: 'Oct 16, 2017',
    reminder: { isReminded: true, date: '2 hour' },
  },
];

const PendingInvites = () => {
  return (
    <div className={styles.manageInvites}>
      <div className={styles.manageInvites__wrap}>
        <div className={styles.manageInvites__title}>Pending Invites</div>
      </div>
      <div className={styles.manageInvites__table}>
        <div className={styles.manageInvites__table__wrap}>
          <div className={styles.manageInvites__table__header}>
            <div className={styles.manageInvites__table__header__item}>
              NAME
            </div>
            <div className={styles.manageInvites__table__header__item}>
              DATE INVITED
            </div>
          </div>
          <div className={styles.manageInvites__table__body}>
            {pendingIntives &&
              pendingIntives.map((el, idx) => {
                return (
                  <div
                    className={styles.manageInvites__table__body__item}
                    key={el.firstName + '__' + idx}
                  >
                    <div
                      className={
                        styles.manageInvites__table__body__item__personData
                      }
                    >
                      <div
                        className={
                          styles.manageInvites__table__body__item__fullname
                        }
                      >
                        {el.firstName} {el.lastName}
                      </div>
                      <div
                        className={
                          styles.manageInvites__table__body__item__department
                        }
                      >
                        {el.department}
                      </div>
                    </div>

                    <div
                      className={styles.manageInvites__table__body__item__date}
                    >
                      {el.invitedDate}
                      <span
                        className={
                          styles.manageInvites__table__body__item__date__breakLine
                        }
                      ></span>
                    </div>

                    <div
                      className={
                        styles.manageInvites__table__body__item__controls
                      }
                    >
                      {!el.reminder.isReminded && (
                        <button
                          className={`${styles.manageInvites__table__body__item__controls__button} ${styles.remind}`}
                        >
                          Remind
                        </button>
                      )}
                      {el.reminder.isReminded && (
                        <div
                          className={
                            styles.manageInvites__table__body__item__controls__lastReminded
                          }
                        >
                          <span
                            className={
                              styles.manageInvites__table__body__item__controls__lastReminded__tick
                            }
                          >
                            <img src={GreenTick} alt="tick icon" />
                          </span>
                          Reminded sent {el.reminder.date} ago
                        </div>
                      )}
                      <button
                        className={`${styles.manageInvites__table__body__item__controls__button} ${styles.rescind}`}
                      >
                        Rescind
                      </button>
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
      </div>
    </div>
  );
};

export default PendingInvites;
