import {
  refreshTokenStart,
  refreshTokenSuccess,
  refreshTokenError,
} from '../../actions/login';
import { logoutUser } from '../logout';
import { getTokenData } from '../../../http/localStorage';

export const checkAuth = () => async (dispatch) => {
  try {
    dispatch(refreshTokenStart());
    const tokenData = getTokenData();

    if (!tokenData) {
      dispatch(logoutUser());
      return;
    }

    const expiredAccessToken = tokenData.accessToken;
    const graphqlQuery = {
      query: `
        {
          getAccessToken
        }
    `,
    };
    const response = await fetch('https://voxdigital.by:4000/query', {
      method: 'POST',
      headers: {
        Authorization: expiredAccessToken ? `Bearer ${expiredAccessToken}` : '',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(graphqlQuery),
    });
    const responseData = await response.json();
    if (
      responseData.errors ||
      !responseData.data.getAccessToken ||
      responseData.data.getAccessToken === 'undefined'
    ) {
      dispatch(logoutUser());
      return;
    }
    const user = JSON.parse(localStorage.getItem('user'));
    dispatch(
      refreshTokenSuccess({
        isAuth: !!responseData.data.getAccessToken,
        ...user,
      })
    );
    return responseData.data.getAccessToken;
  } catch (error) {
    console.log(error);
    refreshTokenError({ error });
    dispatch(logoutUser());
  }
};
