import {
  loadCommunityDataStart,
  loadCommunityDataError,
  updateCommunities,
} from '../../actions/gridPageActions';
import { updateBucketProducts } from '../../actions/bucket';
import { fetchWithAuth } from '../../../http/fetchWithAuth';

export const toggleCommunityRegionToCart =
  (communityData) => async (dispatch, getState) => {
    dispatch(loadCommunityDataStart());
    const store = getState();
    const inBucketProductsCount = store.cart.productsCount;
    const inBucketProducts = store.cart.products;
    const communities = store.categorygrid.communities;

    const { region_id, community_id: communityId, chosen } = communityData;
    try {
      const graphqlQuery = {
        query: `
        {
          getVerifiedCommunity(
            regionId: ${region_id}
            communityId: ${communityId}
            chosen: ${chosen}
          ) {
            brand_name
            brand_id
            community_title
            community_id
            regions {
              name
              region_id
              payment_status
              price
              chosen
            }
            price_summary {
              total_paid
              total_pledged
              total_outstanding
              preferred_currency_symbol
              currency_symbol_placement
            }
          }
        }
      `,
      };
      const response = await fetchWithAuth('https://voxdigital.by:4000/query', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(graphqlQuery),
      });
      const responseData = await response.json();
      const {
        brand_id,
        brand_name,
        community_id,
        community_title,
        regions,
        price_summary: {
          // currency_symbol_placement,
          // preferred_currency_symbol,
          total_outstanding,
          total_paid,
          total_pledged,
        },
      } = responseData.data.getVerifiedCommunity;
      dispatch(
        updateCommunities({
          brand_id,
          brand_name,
          community_id,
          community_title,
          regions,
        })
      );
      const newInBucketProductsCount = chosen
        ? inBucketProductsCount + 1
        : inBucketProductsCount - 1;

      const chosenRegions = regions.filter((el) => el.chosen === true);

      if (!chosenRegions && !chosenRegions.length) {
        const newInBucketProducts = inBucketProducts.filter(
          (el) => Number(el.community_id) !== Number(community_id)
        );

        dispatch(
          updateBucketProducts({
            products: newInBucketProducts,
            total_paid,
            total_pledged,
            total_outstanding,
            productsCount: newInBucketProductsCount,
          })
        );
        return;
      }

      let currentCommunityProducts = inBucketProducts.find(
        (el) =>
          Number(el.community_id) === Number(community_id) && {
            ...el,
            regions: chosenRegions,
          }
      );
      if (!currentCommunityProducts) {
        currentCommunityProducts = communities.find(
          (el) =>
            Number(el.community_id) === Number(community_id) && {
              ...el,
              regions: chosenRegions,
            }
        );
      }
      const inBucketProductsWithoutCurrentCommunity = inBucketProducts.filter(
        (el) => Number(el.community_id) !== Number(community_id)
      );

      const alpaBeticalSortedInBucketProducts = [
        ...inBucketProductsWithoutCurrentCommunity,
        currentCommunityProducts,
      ].sort((a, b) => Number(a.community_id) - Number(b.community_id));

      dispatch(
        updateBucketProducts({
          products: alpaBeticalSortedInBucketProducts,
          total_paid,
          total_pledged,
          total_outstanding,
          productsCount: newInBucketProductsCount,
        })
      );
    } catch (err) {
      dispatch(
        loadCommunityDataError({
          error: { message: err, clientMessage: 'Please, try again' },
        })
      );
      throw err;
    }
  };
