import sessionApi from '../../api/SessionApi';
import {
  signUpSetStep,
  signUpStart,
  signUpSuccess,
  signUpError,
} from './sign-up';

export const userActions = {
  register,
};

function register(firstName, lastName, email, password1, password2) {
  return (dispatch) => {
    dispatch(signUpStart());
    sessionApi
      .register(firstName, lastName, email, password1, password2)
      .then((response) => {
        if (response.error) {
          throw new Error(response.error);
        }
        dispatch(signUpSuccess());
        dispatch(signUpSetStep({ step: 2 }));
      })
      .catch((error) => {
        dispatch(signUpError({ message: error.message, status: 401 }));
      });
  };
}
