import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import styles from './styles.module.scss';
import { toggleCommunityRegionToCart } from '../../redux/thunk/becomeSponsor';
import RegionsList from './RegionsList';
import Region from './Region';

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.scss';
import 'swiper/components/effect-fade/effect-fade.scss';
import Backdrop from '../molecules/Backdrop';

const CommunityRegions = ({
  community_id,
  regions,
  currency_symbol,
  symbol_placement,
  brand_id,
  loader,
  dispatchToggleCommunityRegionToCart,
}) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [countriesList, setCountriesList] = useState(null);
  const [continent, setContinent] = useState('');
  const [isMobileStylization, setIsMobileStylization] = useState(false);

  useEffect(() => {
    let body = document.querySelector('body');
    body.style.overflow = 'hidden';

    if (window.screen.width < 860) {
      setIsMobileStylization(true);
      return () => {
        body.style.overflow = 'auto';
      };
    }
    setIsMobileStylization(false);

    return () => {
      body.style.overflow = 'auto';
    };
  }, []);

  const onRegionClick = (regionData, chosen) => {
    dispatchToggleCommunityRegionToCart({
      region_id: regionData.region_id,
      community_id,
      chosen,
    });
  };

  const showCountriesHandler = (continent, countries) => {
    if (countries && countries.length === 0) return;
    setContinent(continent);
    setCountriesList(countries);
    setIsModalOpen(true);
  };

  const hideCountriesHandler = () => {
    setIsModalOpen(false);
  };

  return (
    <div className={`${styles['sponsor']}`}>
      <div className={`${styles['sponsor__header']}`}>
        Please choose all regions you wish to sponsor
      </div>

      <ul className={`${styles['sponsor__table']}`}>
        {isMobileStylization ? (
          <Swiper slidesPerView={1.6} spaceBetween={0}>
            {regions &&
              regions.map((regionData, idx) => {
                return (
                  <SwiperSlide
                    key={`slide-${idx}`}
                    className={styles['sponsor__slide']}
                  >
                    <Region
                      key={idx}
                      data={regionData}
                      currency_symbol={currency_symbol}
                      symbol_placement={symbol_placement}
                      clickHander={onRegionClick}
                      showSponsorDetails={showCountriesHandler}
                      loader={loader}
                    />
                  </SwiperSlide>
                );
              })}
          </Swiper>
        ) : (
          regions &&
          regions.map((regionData, idx) => {
            return (
              <Region
                key={idx}
                data={regionData}
                currency_symbol={currency_symbol}
                symbol_placement={symbol_placement}
                paymentStatus={regionData.payment_status}
                clickHander={onRegionClick}
                showSponsorDetails={showCountriesHandler}
                loader={loader}
              />
            );
          })
        )}
      </ul>

      <Backdrop
        show={isModalOpen}
        clickhandler={hideCountriesHandler}
        appearance="dark"
      />
      {isModalOpen && (
        <RegionsList
          title={continent}
          firstColumn={countriesList[0]}
          secondColumn={countriesList[1]}
          thirdColumn={countriesList[2]}
          fourthColumn={countriesList[3]}
        />
      )}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    brand_id: state.brand_id.brand_id,
    loader: state.categorygrid.loader,
  };
};

const mapDispatchToProps = {
  dispatchToggleCommunityRegionToCart: toggleCommunityRegionToCart,
};

export default connect(mapStateToProps, mapDispatchToProps)(CommunityRegions);
