import React, { useState, useEffect, useRef, useCallback } from 'react';
import styles from './styles.module.scss';
import { loginStart, loginInputEmail } from '../../redux/actions/auth';
import { connect, useDispatch } from 'react-redux';
import LoginBWImage from '../../assets/images/BWSingIn.jpg';
import Logo from '../../assets/images/logoNew.png';
import Button from '../atoms/Button';
import ArrowWhiteImage from '../../assets/images/ui-icons/arrow_login_white.png';
import ArrowDisabledImage from '../../assets/images/ui-icons/right.svg';
import MainAuthImage from '../../assets/images/coloredSignInNew.jpg';
import { useHistory, Link, useLocation } from 'react-router-dom';
import validator from 'validator';
import { logInUser } from '../../redux/thunk/login';
import { loginResetErrorMessage } from '../../redux/actions/login';
//images
import Aeronautics from '../../assets/images/categoriesColored/Aeronautics.png';
import ElectricalEngineering from '../../assets/images/categoriesColored/Electrical-Engineering.png';
import Astronomy from '../../assets/images/categoriesColored/Astronomy.png';
import Robotics from '../../assets/images/categoriesColored/Robotics.png';
import Physics from '../../assets/images/categoriesColored/Physics.png';
import Music from '../../assets/images/categoriesColored/Music.png';
import Geology from '../../assets/images/categoriesColored/Geology.png';
import Nutrition from '../../assets/images/categoriesColored/Nutrition.png';
import Tourism from '../../assets/images/categoriesColored/Tourism.png';
import Literature from '../../assets/images/categoriesColored/Literature.png';
import Gastronomy from '../../assets/images/categoriesColored/Gastronomy.png';
import Chemistry from '../../assets/images/categoriesColored/Chemistry.png';
// input icons
import WarningIcon from '../../assets/images/ui-icons/warningIcon.png';
//import SuccessedIcon from '../../assets/images/ui-icons/successedIcon.svg';

const animationData = [
  //x = -70, y = -400
  { x: -45, y: -340, timeOut: 2 },
  { x: -45, y: -340, timeOut: 1 },
  { x: -870, y: -425, timeOut: 2 },
  { x: -870, y: -425, timeOut: 1 },
  { x: -1425, y: -295, timeOut: 2 },
  { x: -1425, y: -295, timeOut: 1 },
  { x: -1085, y: -230, timeOut: 2 },
  { x: -1085, y: -230, timeOut: 1 },
  { x: -1425, y: -20, timeOut: 2 },
  { x: -1425, y: -20, timeOut: 1 },
  { x: -1625, y: -530, timeOut: 2 },
  { x: -1625, y: -530, timeOut: 1 },
  { x: -1405, y: -140, timeOut: 2 },
  { x: -1405, y: -140, timeOut: 1 },
  { x: -1755, y: -210, timeOut: 2 },
  { x: -1755, y: -210, timeOut: 1 },
  { x: -1555, y: -120, timeOut: 2 },
  { x: -1555, y: -120, timeOut: 1 },
  { x: -965, y: -220, timeOut: 2 },
  { x: -965, y: -220, timeOut: 1 },
  { x: -485, y: -270, timeOut: 2 },
  { x: -485, y: -270, timeOut: 1 },
  { x: -325, y: 30, timeOut: 2 },
  { x: -325, y: 30, timeOut: 1 },
];

const r = 80;
let timeout1;
let timeout2;

const Auth = ({
  isAuth,
  loginErrorMessage,
  dispatchLoginInputEmail,
  dispatchLoginUser,
}) => {
  const [authStep, setAuthStep] = useState(1);
  const [authData, setAuthData] = useState({ login: '', password: '' });
  const [isEmailValid, setIsEmailValid] = useState([]);
  const [isPasswordValid, setIsPasswordValid] = useState([]);
  const [showPasswordText, setShowPasswordText] = useState(false);

  //const [redirectToReferrer, setRedirectToReferrer] = useState(false);

  const [isMoveFinished, setIsMovedFinished] = useState(false);
  const [isCircleOpen, setIsCircleOpen] = useState(true);
  const [isCircleClosed, setIsCircleClosed] = useState(false);
  const [animationIndex, setAnimationIndex] = useState(0);

  const history = useHistory();
  const location = useLocation();
  const dispatch = useDispatch();

  const differenceBtwResolutionsInPX = Math.round(640 - window.screen.width);
  const xCircleOffset =
    182 -
    (differenceBtwResolutionsInPX === 0 ? 0 : differenceBtwResolutionsInPX / 2); //182 -     640px,    .
  const xBgOffset =
    93 -
    (differenceBtwResolutionsInPX === 0 ? 0 : differenceBtwResolutionsInPX / 2); //182 -     640px,    .

  const [coords, setCoords] = useState({
    bg: {
      x: window.screen.width <= 640 ? xBgOffset : -90,
      y: window.screen.width <= 640 ? -135 : 0,
    },
    circle: { x: -167, y: -160 },
  });

  const [count, setCount] = useState(0);
  const [images, setImages] = useState([
    { id: 1, src: Music, available: false },
    { id: 2, src: Literature, available: false },
    { id: 3, src: Robotics, available: false },
    { id: 4, src: Chemistry, available: false },
    { id: 5, src: Astronomy, available: false },
    { id: 6, src: Nutrition, available: false },
    { id: 7, src: Physics, available: false },
    { id: 8, src: ElectricalEngineering, available: false },
    { id: 9, src: Aeronautics, available: false },
    { id: 10, src: Geology, available: false },
    { id: 11, src: Gastronomy, available: false },
    { id: 12, src: Tourism, available: false },
  ]);

  const [showPhoto, setShowPhoto] = useState('none');

  let edges = useRef();
  let bodies = useRef();
  const emailInputRef = useRef();
  const passwordInputRef = useRef();

  const arc = useCallback((x, y, s) => `A${r},${r},0,0,${s},${x},${y}`, []);

  const path = useCallback(
    (i, d) => `<path transform='rotate(${(i / 6) * 360})' ${d}></path>`,
    []
  );

  const upd = useCallback(
    (val) => {
      let step = Math.PI * (0.5 + 2 / 6);
      let p1x = Math.cos(step) * r;
      let p1y = Math.sin(step) * r;
      let cos = Math.cos(-val);
      let sin = Math.sin(-val);
      let c1x = p1x - cos * p1x - sin * p1y;
      let c1y = p1y - cos * p1y + sin * p1x;
      let dx = -sin * r - c1x;
      let dy = r - cos * r - c1y;
      let dc = Math.sqrt(dx * dx + dy * dy);
      let a = Math.atan2(dy, dx) - Math.acos(dc / 2 / r);
      let x = c1x + Math.cos(a) * r;
      let y = c1y + Math.sin(a) * r;

      let edge = `M${p1x},${p1y}${arc(0, r, 0)}${arc(x, y, 1)}`;
      if (edges && edges.current && edges.current.innerHTML)
        edges.current.innerHTML = '';
      if (bodies && bodies.current && bodies.current.innerHTML)
        bodies.current.innerHTML = '';
      for (let i = 0; i < 10; i++) {
        if (edges && edges.current)
          edges.current.innerHTML += path(
            i,
            `fill=none stroke=#fff d='${edge}'`
          );
        if (bodies && bodies.current)
          bodies.current.innerHTML += path(
            i,
            `fill=#84aff8  d='${edge}${arc(p1x, p1y, 0)}'`
          );
      }
    },
    [arc, path]
  );

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const closeCircle = useCallback((itemCount = 0) => {
    setShowPhoto('block');
    if (itemCount < 100) {
      upd((itemCount / 100) * 1.04);
      clearTimeout(timeout1);
      timeout1 = setTimeout(() => {
        itemCount += 5;
        closeCircle(itemCount);
      }, 10);
    }
    if (itemCount >= 100) {
      setIsCircleClosed(true);
    }
  });

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const openCircle = useCallback((itemCount = 100) => {
    if (itemCount > 0) {
      upd((itemCount / 100) * 1.04);
      clearTimeout(timeout2);
      timeout2 = setTimeout(() => {
        itemCount -= 5;
        openCircle(itemCount);
      }, 10);
    }
    if (itemCount === 0) {
      setIsCircleOpen(true);
      setShowPhoto('none');
    }
  });

  useEffect(() => {
    if (isMoveFinished) {
      setTimeout(() => {
        closeCircle();
      }, 1300);
      setIsMovedFinished(false);
    }
  }, [isMoveFinished, closeCircle, setIsMovedFinished]);

  useEffect(() => {
    if (isCircleClosed) {
      openCircle();
      setCount(count + 1);
      setIsCircleClosed(false);
    }
  }, [isCircleClosed, setIsCircleClosed, openCircle]);

  useEffect(() => {
    if (count === images.length + 1) {
      setCount(1);
    }
    if (count > 0 && count <= images.length) {
      const newImagesArray = images.map((el) => {
        if (el.id === count) {
          return { ...el, available: true };
        }
        return el;
      });
      const hideImageId =
        count === 10 ? 1 : count === 11 ? 2 : count === 12 ? 3 : count + 3;
      const newImagesArrayWithHideOne = newImagesArray.map((el) => {
        if (el.id === hideImageId) {
          return { ...el, available: false };
        }
        return el;
      });
      setImages(newImagesArrayWithHideOne);
    }
  }, [count]);

  useEffect(() => {
    if (isCircleOpen) {
      const animationElement = animationData[animationIndex];

      const drawNorm = () => {
        setTimeout(() => {
          let newCoordinates;
          if (window.screen.width <= 640) {
            newCoordinates = {
              bg: {
                x: animationElement.x + xCircleOffset,
                y: animationElement.y - 132,
              },
              circle: {
                x: animationElement.x - 77,
                y: animationElement.y - 160,
              },
            };
          } else {
            newCoordinates = {
              bg: { x: animationElement.x, y: animationElement.y },
              circle: {
                x: animationElement.x - 77,
                y: animationElement.y - 160,
              },
            };
          }
          setCoords(newCoordinates);
          setAnimationIndex(
            animationIndex === animationData.length - 1 ? 0 : animationIndex + 1
          );
          setIsMovedFinished(animationElement.timeOut === 1 ? true : false);
        }, animationElement.timeOut * 1000);
      };
      if (animationElement.timeOut === 1) setIsCircleOpen(false);
      drawNorm();
    }
  }, [isCircleOpen, animationIndex]);

  const userLoginHandler = (e) => {
    setIsEmailValid([]);
    setAuthData({ ...authData, [e.target.name]: e.target.value });
  };

  const resetEmail = (e) => {
    e.stopPropagation();
    setAuthData({ ...authData, login: '' });
  };

  const showPasswordTextHandler = (e) => {
    e.stopPropagation();
    setShowPasswordText(!showPasswordText);
  };

  // const nextStepHandler = () => {
  //   validEmail();
  // };

  const previousStepHandler = () => {
    dispatch(loginResetErrorMessage());
    setAuthStep(authStep - 1);
    setShowPasswordText(false);
  };

  const nextStepHandler = () => {
    setIsEmailValid([]);
    dispatchLoginInputEmail({ email: authData.login });
    if (
      validator.isEmpty(authData.login) ||
      !validator.isEmail(authData.login)
    ) {
      return setIsEmailValid([
        {
          message: 'Incorrect email. Please enter valid email address',
        },
      ]);
    }
    return setAuthStep(authStep + 1);
        
  };

  

  // useEffect(() => {
  //   if (authData.password === '') {
  //     setShowPasswordText(false);
  //   }
  //   if (!validator.isLength(authData.password, { min: 6 })) {
  //     return setIsPasswordValid([
  //       {
  //         message: 'Password should contains minimum 6 symbols',
  //       },
  //     ]);
  //   }

  //   return setIsPasswordValid([]);
  // }, [authData.password]);

  const handleSubmit = async (e) => {
    setAuthData({ ...authData, [e.target.login]: e.target.value });
    setAuthData({ ...authData, [e.target.password]: e.target.value });
    e.preventDefault();

    if (authData.login && authData.password) {
      dispatchLoginUser(authData.login, authData.password);
    }
  };

  const handleKeyDown = (e) => {
    if (
      e.target.name === 'login' &&
      e.key === 'Enter' &&
      authData.login.length > 0 &&
      isEmailValid.length === 0
    ) {
      setAuthStep(authStep + 1);
      return;
    }
    if (
      e.target.name === 'password' &&
      e.key === 'Enter' &&
      authData.password &&
      isPasswordValid.length === 0
    ) {
      handleSubmit(e);
      return;
    }
  };
  
  
  useEffect(() => {
    let { from }  = location.state || { from: { pathname: '/' } };
    if (isAuth) {
      history.push(from);
    }
  }, [history, isAuth, location.state]);

  useEffect(() => {
    if (authStep === 1) {
      emailInputRef.current.focus();
    }
    if (authStep === 2) {
      passwordInputRef.current.focus();
    }
  }, [authStep]);

  return (
    <div className={styles.authLayout}>
      <div className={styles['auth']}>
        <div className={styles['auth__search-image-wrapper']}>
          <div
            className={styles['auth__search-image']}
            style={{
              backgroundImage: `url(${LoginBWImage})`,
              backgroundPosition: `${coords.bg.x}px ${coords.bg.y}px`,
              backgroundRepeat: 'no-repeat',
            }}
          >
            <div
              className={styles['auth__search-image-circle']}
              style={{
                background: `url(${MainAuthImage}) no-repeat`,
                backgroundPosition: `${coords.circle.x}px ${coords.circle.y}px`,
              }}
            >
              <svg
                viewBox="-80,-80, 160,160"
                width="100%"
                height="100%"
                style={{
                  position: 'absolute',
                  display: showPhoto,
                }}
                id="svg"
              >
                <g ref={bodies}></g>
                <g ref={edges}></g>
              </svg>
            </div>
          </div>
        </div>
        <div className={styles['auth__sign-up']}>
          <div className={styles['auth__sign-up-wrapper']}>
            <div className={styles['auth__sign-up-logo-wrapper']}>
              <img
                className={styles['auth__sign-up-logo']}
                src={Logo}
                alt="SkillVox logo"
              />
            </div>            
            <div className={styles['auth__sign-up-title']}>
              <p>Sign in to your Skillvox account</p>
            </div>
            {authStep === 1 ? (
              <>
                <div className={styles['auth__sign-up-label']}>
                  <label htmlFor="login">Email</label>
                  <span
                    className={`${
                      styles['auth__sign-up-label-validationIcons']
                    } ${
                      authData.login.length > 0 &&
                      (isEmailValid.length > 0 || isEmailValid.length === 0) &&
                      styles['auth__sign-up-label-validationIcons__show']
                    }`}
                  >
                    {authData.login.length > 0 && isEmailValid.length > 0 && (
                      <img src={WarningIcon} alt="warning icon" />
                    )}
                    {/* { isEmailValid.length === 0 && (
                      <img src={SuccessedIcon} alt="successed icon" />
                    )} */}
                  </span>
                </div>
                <div className={styles['auth__sign-up-input-wrapper']}>
                  <input
                    className={`${styles['auth__sign-up-input']}` 
                      // ${ authData.login.length > 0 && isEmailValid.length > 0
                      //     ? styles.error
                      //     : authData.login.length > 0 && isEmailValid.length === 0 
                      //     ? styles.success : ''
                         
                      // }`                   
                    }
                    type="text"
                    placeholder="Email"
                    value={authData.login}
                    onChange={userLoginHandler}
                    name="login"
                    id="login"
                    ref={emailInputRef}
                    tabIndex={1}
                    onKeyDown={handleKeyDown}
                  />
                  {authData.login && (
                    <div
                      className={`${styles['auth__sign-up-input-clearEmail']}`}
                      onClick={resetEmail}
                    ></div>
                  )}
                  {authData.login.length > 0 && isEmailValid.length > 0 && (
                    <p className={styles.errorText}>
                      {isEmailValid[0].message}
                    </p>
                  )}
                </div>

                <div
                  className={`${styles['auth__sign-up-button-wrapper']} ${styles.email}`}
                >
                  <Button
                    key={1}
                    text="Next"
                    image={
                      authData.login && isEmailValid.length === 0
                        ? ArrowWhiteImage
                        : ArrowDisabledImage
                    }
                    disabled={
                      authData.login.length !== 0 ? false : true
                    }
                    appearance="login"
                    clickHandler={nextStepHandler}
                    addClass="centered"
                    tabIndex={2}
                  />
                </div>
              </>
            ) : authStep === 2 ? (
              <>
                <div className={styles['auth__sign-up-input-wrapper']}>
                  <input
                    className={`${styles['auth__sign-up-input']} ${
                      styles['password']
                    } ${
                      authData.password.length > 0 && isPasswordValid.length > 0
                        ? styles.error
                        : ''
                    } ${
                      authData.password.length > 0 &&
                      isPasswordValid.length === 0
                        ? styles.success
                        : ''
                    }`}
                    type={showPasswordText ? 'text' : 'password'}
                    placeholder="Password"
                    value={authData.password}
                    onChange={userLoginHandler}
                    name="password"
                    ref={passwordInputRef}
                    tabIndex={1}
                    onKeyDown={handleKeyDown}
                  />
                  {authData.password !== '' && (
                    <div
                      className={
                        styles[
                          `auth__sign-up-input-${
                            showPasswordText ? 'show-password' : 'hide-password'
                          }`
                        ]
                      }
                      onClick={showPasswordTextHandler}
                    ></div>
                  )}
                  <Link
                    to="/recovery"
                    className={styles['auth__sign-up-forgot-password']}
                    tabIndex={2}
                  >
                    Forgot password?
                  </Link>
                  {authData.password.length > 0 &&
                    isPasswordValid.length > 0 && (
                      <p className={styles.errorText}>
                        {isPasswordValid[0].message}
                      </p>
                    )}
                  {loginErrorMessage && (
                    <p className={styles.errorText}>{loginErrorMessage}</p>
                  )}
                </div>
                <div
                  className={`${styles['auth__sign-up-button-wrapper']} ${styles.password}`}
                >
                  <Button
                    key={'backButton__login'}
                    text="Back"
                    appearance="backButton"
                    clickHandler={previousStepHandler}
                    tabIndex={3}
                  />
                  <Button
                    key={'signInButton__login'}
                    text="Sign In"
                    disabled={
                      authData.password && isPasswordValid.length === 0
                        ? false
                        : true
                    }
                    appearance="login"
                    clickHandler={handleSubmit}
                    tabIndex={4}
                  />
                </div>
              </>
            ) : (
              ''
            )}
          </div>
        </div>

        <img
          className={`
          ${styles['single__image']} 
          ${styles[images[0].available && 'first-image']}
          ${styles[images[0].available && 'show-image']}
          `}
          src={Music}
          alt="Music"
        />

       <img
          className={`
          ${styles['single__image']}
          ${styles[images[1].available && 'second-image']}
          ${styles[images[1].available && 'show-image']}
          `}
          src={Literature}
          alt="Literature"
        /> 
       
        <img
          className={`${styles['single__image']}
          ${styles[images[2].available && 'third-image']}
          ${styles[images[2].available && 'show-image']}
          `}
          src={Robotics}
          alt="Robotics"
        /> 
        <img
          className={`
          ${styles['single__image']}
          ${styles[images[3].available && 'fourth-image']}
          ${styles[images[3].available && 'show-image']}
          `}
          src={Chemistry}
          alt="Chemistry"
        />

        <img
          className={`
          ${styles['single__image']}
          ${styles[images[4].available && 'fifth-image']}
          ${styles[images[4].available && 'show-image']}
          `}
          src={Astronomy}
          alt="Astronomy"
        />

        <img
          className={`
          ${styles['single__image']}
          ${styles[images[5].available && 'six-image']}
          ${styles[images[5].available && 'show-image']}
          `}
          src={Nutrition}
          alt="Nutrition"
        /> 
       <img
          className={`
          ${styles['single__image']}
          ${styles[images[6].available && 'seventh-image']}
          ${styles[images[6].available && 'show-image']}
          `}
          src={Physics}
          alt="Physics"
        />

        <img
          className={`
          ${styles['single__image']}
          ${styles[images[7].available && 'eight-image']}
          ${styles[images[7].available && 'show-image']}
          `}
          src={ElectricalEngineering}
          alt="Electrical Engineering"
        />

        <img
          className={`
          ${styles['single__image']}
          ${styles[images[8].available && 'nineth-image']}
          ${styles[images[8].available && 'show-image']}
          `}
          src={Aeronautics}
          alt="Aeronautics"
        />

        <img
          className={`
          ${styles['single__image']}
          ${styles[images[9].available && 'ten-image']}
          ${styles[images[9].available && 'show-image']}
          `}
          src={Geology}
          alt="Geology"
        />

        <img
          className={`
          ${styles['single__image']}
          ${styles[images[10].available && 'eleven-image']}
          ${styles[images[10].available && 'show-image']}
          `}
          src={Gastronomy}
          alt="Gastronomy"
        />

        <img
          className={`
          ${styles['single__image']}
          ${styles[images[11].available && 'second-image']}
          ${styles[images[11].available && 'show-image']}
          `}
          src={Tourism}
          alt="Tourism"
        />
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    isAuth: state.session.isAuth,
    loginErrorMessage: state.session.error,
  };
};

const mapDispatchToProps = {
  dispatchLoginUser: logInUser,
  dispatchLoginResetErrorMessage: loginResetErrorMessage,
  dispatchLoginStart: loginStart,
  dispatchLoginInputEmail: loginInputEmail,
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);
