import { useState, useEffect } from 'react';
import styles from './styles.module.scss';
import { Link } from 'react-router-dom';
import WarningIcon from '../../../../assets/images/ui-icons/warningIcon.png';

const userPass = '12345';

const EditPassword = ({
  setPassword,
  setShowEditField,
  onCancelClick,
  // onSaveDispatch
}) => {
  const [currentPassword, setCurrentPassword] = useState('');
  const [currentPassError, setCurrentPassError] = useState([]);
  const [newPassword, setNewPassword] = useState('');
  const [newPassError, setNewPassError] = useState([]);
  const [showNewPassword, setShowNewPassword] = useState(false);
  const [newPasswordRepeat, setNewPasswordRepeat] = useState('');
  const [showNewPasswordRepeat, setShowNewPasswordRepeat] = useState(false);

  const [isSaveBtnDisabled, setIsSaveBtnDisabled] = useState(true);

  useEffect(() => {
    if (currentPassword && newPassword && newPasswordRepeat) {
      if (newPassword === newPasswordRepeat) {
        setIsSaveBtnDisabled(false);
        setNewPassError([]);
      } else {
        setIsSaveBtnDisabled(true);
        setNewPassError([
          ...newPassError,
          { message: "Passwords isn't match" },
        ]);
      }
    }
  }, [currentPassword, newPassword, newPasswordRepeat]);

  useEffect(() => {
    if (newPassword.length > 0 && newPasswordRepeat.length > 0) {
      if (newPassword === newPasswordRepeat) {
        setNewPassError([]);
      } else {
        setNewPassError([...newPassError, { message: 'Passwords isnt match' }]);
      }
    }
  }, [newPassword, newPasswordRepeat]);

  const setCurrentPasswordHandler = (e) => {
    setCurrentPassword(e.target.value);
  };

  const setNewPasswordHandler = (e) => {
    setNewPassword(e.target.value);
  };

  const repeatNewPasswordHandler = (e) => {
    setNewPasswordRepeat(e.target.value);
  };

  const saveBtnHandler = () => {
    if (currentPassword === userPass) {
      setCurrentPassError([]);
      setPassword(newPassword);
      setShowEditField(false);
      // onSaveDispatch(password)
    } else {
      setCurrentPassError([
        ...currentPassError,
        { message: 'Incorrect current password. Try again' },
      ]);
      setIsSaveBtnDisabled(true);
    }
  };

  const showNewPasswordHandler = () => {
    setShowNewPassword(!showNewPassword);
  };

  const showNewPasswordRepeatHandler = () => {
    setShowNewPasswordRepeat(!showNewPasswordRepeat);
  };

  return (
    <div className={styles.editPassword}>
      <div className={styles.editPassword__fields}>
        <div className={styles.editPassword__inputField}>
          <label
            className={styles.editPassword__label}
            htmlFor="currentPassword"
          >
            Current{' '}
            <span className={styles.editPassword__label__required}>*</span>
            {currentPassword > 0 && currentPassError.length > 0 && (
              <span className={styles.editPassword__label__warningIcon}>
                <img src={WarningIcon} alt="warning icon" />
              </span>
            )}
          </label>
          <div className={styles.editPassword__inputField__input}>
            <input
              type="password"
              name="currentPassword"
              value={currentPassword}
              id="currentPassword"
              onChange={setCurrentPasswordHandler}
              tabIndex="1"
            />

            <div
              className={`${styles.editPassword__inputField__errorMessage} ${
                currentPassword > 0 && currentPassError.length > 0
                  ? styles.show
                  : ''
              }`}
            >
              {currentPassword > 0 &&
                currentPassError.length > 0 &&
                currentPassError[0].message}
            </div>
          </div>
        </div>
        <div className={styles.editPassword__inputField}>
          <label className={styles.editPassword__label} htmlFor="newPassword">
            New <span className={styles.editPassword__label__required}>*</span>
          </label>
          <div className={styles.editPassword__inputField__input}>
            <input
              type={showNewPassword ? 'text' : 'password'}
              name="newPassword"
              value={newPassword}
              id="newPassword"
              tabIndex="2"
              onChange={setNewPasswordHandler}
              className={`${
                newPassError.length > 0 && newPassError[0].message
                  ? styles.error
                  : ''
              }
              ${
                newPassword.length > 0 &&
                newPasswordRepeat > 0 &&
                newPassword === newPasswordRepeat &&
                styles.success
              }
              `}
            />
            {newPassword.length > 0 && (
              <div
                className={`${styles.editPassword__inputField__input__hint} ${
                  showNewPassword ? styles.showPassword : styles.hidePassword
                }`}
                onClick={showNewPasswordHandler}
              ></div>
            )}
          </div>
        </div>
        <div className={styles.editPassword__inputField}>
          <label
            className={styles.editPassword__label}
            htmlFor="repeatNewPassword"
          >
            Retype new{' '}
            <span className={styles.editPassword__label__required}>*</span>
          </label>
          <div className={styles.editPassword__inputField__input}>
            <input
              type={showNewPasswordRepeat ? 'text' : 'password'}
              name="repeatNewPassword"
              value={newPasswordRepeat}
              id="repeatNewPassword"
              onChange={repeatNewPasswordHandler}
              tabIndex="3"
              className={`${
                newPassError.length > 0 && newPassError[0].message
                  ? styles.error
                  : ''
              }
              ${
                newPassword.length > 0 &&
                newPasswordRepeat > 0 &&
                newPassword === newPasswordRepeat &&
                styles.success
              }
              `}
            />
            {newPasswordRepeat.length > 0 && (
              <div
                className={`${styles.editPassword__inputField__input__hint} ${
                  showNewPasswordRepeat
                    ? styles.showPassword
                    : styles.hidePassword
                }`}
                onClick={showNewPasswordRepeatHandler}
              ></div>
            )}
            <div
              className={`${styles.editPassword__inputField__errorMessage} ${
                newPasswordRepeat.length > 0 &&
                newPassword.length > 0 &&
                newPassError.length > 0 &&
                newPassError[0].message
                  ? styles.show
                  : ''
              }`}
            >
              {newPasswordRepeat.length > 0 &&
                newPassword.length > 0 &&
                newPassError.length > 0 &&
                newPassError[0].message}
            </div>
          </div>
        </div>
      </div>
      <div className={styles.editPassword__forgotPassword}>
        <Link to="/password-recovery" className={styles.editPassword__link}>
          Forgot your password?
        </Link>
      </div>

      <div className={styles.editPassword__buttons}>
        <button
          className={`${styles.editPassword__buttons__btn} ${styles.editPassword__buttons__btn__cancel}`}
          onClick={onCancelClick}
          tabIndex="4"
        >
          Cancel
        </button>
        <button
          className={`${styles.editPassword__buttons__btn} ${styles.editPassword__buttons__btn__save}`}
          onClick={saveBtnHandler}
          disabled={isSaveBtnDisabled}
          tabIndex="5"
        >
          Save changes
        </button>
      </div>
    </div>
  );
};

export default EditPassword;
