import React, { useEffect, useState } from 'react';
import styles from './styles.module.scss';
import { NavLink, Link } from 'react-router-dom';
import BurgerMenuImg from '../../../assets/images/ui-icons/burgerMenu.svg';
import CloseBurgerMenuImg from '../../../assets/images/ui-icons/closeBurgerMenu.svg';
import OutsideCloseModal from '../../OutsideCloseModal';

const Nav = ({ menuType, links, clickHandler }) => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [showSubmenu, setShowSubmenu] = useState(false);

  useEffect(() => {
    if (isMobileMenuOpen === false) {
      setShowSubmenu(false);
    }
  }, [isMobileMenuOpen]);

  const openMobileMenu = () => {
    setIsMobileMenuOpen(true);
  };

  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  const onMenuClick = () => {
    setIsMobileMenuOpen(false);
  };

  const toggleSubmenuHandler = () => {
    setShowSubmenu(!showSubmenu);
  };

  return (
    <nav className={styles['nav']}>
      <ul
        className={`${styles['nav__list']} ${
          menuType === 'MY_ACCOUNT' ? styles.hide : ''
        }`}
      >
        {links &&
          links.map(({ title, url, image, submenu }, idx) => {
            if (submenu) {
              return (
                <li
                  key={idx}
                  className={styles['nav__link']}
                  // ${
                  //   styles[activeMenuPoint === idx ? "active" : ""]
                  // }

                  onClick={() => clickHandler(submenu, idx)}
                >
                  <Link to="#">
                    {title}
                    {image && (
                      <img
                        className={styles['nav__menu-arrow-down']}
                        src={image}
                        alt={title}
                      />
                    )}
                  </Link>
                </li>
              );
            }

            return (
              <li
                key={idx}
                style={{marginLeft: title === 'Roadmap'? '25px' : 0}}
                className={styles['nav__link']}
                onClick={() => clickHandler(null, idx)}
              >
                <NavLink to={url} activeClassName={styles.active}>               
                  {title}
                  {image && (
                    <img
                      className={styles['nav__menu-arrow-down']}
                      src={image}
                      alt={title}
                    />
                  )}
                </NavLink>
              </li>
            );
          })}
      </ul>

      <OutsideCloseModal
        state={isMobileMenuOpen}
        setState={setIsMobileMenuOpen}
      >
        <div
          className={`${styles['nav__mobile']} ${
            menuType === 'MY_ACCOUNT' ? styles.show : ''
          }`}
        >
          <div
            className={styles['nav__mobile-imageWrap']}
            onClick={openMobileMenu}
          >
            <img src={BurgerMenuImg} alt="Mobile menu" />
          </div>

          <div
            className={`${styles['nav__mobile-menu']} ${
              styles[isMobileMenuOpen ? 'show' : 'hide']
            } ${menuType === 'MY_ACCOUNT' ? styles.myAccount : ''}`}
          >
            <div
              className={styles['nav__mobile-closeMenu']}
              onClick={closeMobileMenu}
            >
              <img src={CloseBurgerMenuImg} alt="Close burger menu" />
            </div>
            <div className={styles['nav__mobile-menu-wrapper']}>
              <ul className={styles['nav__mobile-menu-mainMenu']}>
                {links &&
                  links.map(({ title, url, image, submenu }, idx) => {
                    if (submenu) {
                      return (
                        <li key={idx}>
                          <div
                            className={styles['nav__mobile-menu-link']}
                            onClick={toggleSubmenuHandler}
                          >
                            {title}
                            {image && (
                              <img
                                src={image}
                                alt={title}
                                className={`${
                                  showSubmenu ? styles.imageRotate : ''
                                }`}
                              />
                            )}
                          </div>
                          <ul
                            className={`${styles['nav__mobile-menu-submenu']} ${
                              showSubmenu ? styles.show : ''
                            }`}
                          >
                            {submenu.map((subItem, idx) => {
                              return (
                                <li key={idx} onClick={onMenuClick}>
                                  <NavLink to={subItem.url}>
                                    {subItem.title}
                                    <span>{subItem.description}</span>
                                  </NavLink>
                                </li>
                              );
                            })}
                          </ul>
                        </li>
                      );
                    }
                    return (
                      <li key={idx} onClick={onMenuClick}>
                        <NavLink
                          to={url}
                          className={styles['nav__mobile-menu-link']}
                        >
                          {title}
                        </NavLink>
                      </li>
                    );
                  })}
              </ul>
            </div>
          </div>
        </div>
      </OutsideCloseModal>
    </nav>
  );
};

export default Nav;
