import styles from './styles.module.scss';
import { useEffect, useState } from 'react';
// import Select from 'react-select';
import { Link } from 'react-router-dom';
// import BlueTick from '../../../../assets/images/ui-icons/blueTick.png';
import Button from '../../../components/atoms/Button';

const FirstStep = ({
  signUpData,
  options,
  currentStep,
  signUpEmailHandler,
  setSignUpStep,
  signUpFirstAndLastNameHandler,
  password,
  loader,
  setPassword,
  showPassword,
  setShowPassword,
  confirmedPassword,
  setConfirmedPassword,
  showConfirmedPassword,
  setShowConfirmedPassword,
  isPasswordsMatches,
  // signUpCountryHandler,
}) => {
  let successHintTimeout;
  const [showSuccessHint, setShowSuccessHint] = useState(false);

  useEffect(() => {
    return () => {
      clearTimeout(successHintTimeout);
    };
  }, []);

  useEffect(() => {
    if (
      confirmedPassword.length > 0 &&
      isPasswordsMatches &&
      !signUpData.passwordValidationFailed.status
    ) {
      setShowSuccessHint(true);
    }
  }, [
    confirmedPassword.length,
    isPasswordsMatches,
    signUpData.passwordValidationFailed.status,
  ]);

  useEffect(() => {
    if (showSuccessHint) {
      successHintTimeout = setTimeout(() => {
        setShowSuccessHint(false);
      }, 3000);
    }
  }, [showSuccessHint]);
  // const customStyles = {
  //   control: (base) => ({
  //     ...base,
  //     height: '100%',
  //     border: '1px solid #d7d7d7',
  //     borderRadius: '2px',
  //     paddingLeft: '10px',
  //   }),
  //   option: (styles, { data, isDisabled, isFocused, isSelected }) => {
  //     return {
  //       ...styles,
  //       cursor: isDisabled ? 'not-allowed' : 'pointer',
  //       color: isSelected ? '#000000' : '#707070',
  //       backgroundColor: '#fff',
  //       backgroundImage: isSelected ? `url(${BlueTick})` : '',
  //       backgroundRepeat: 'no-repeat',
  //       '&:hover': {
  //         backgroundColor: '#f5f5f5',
  //       },
  //       backgroundPosition: '97% 50%',
  //       backgroundSize: '10px',
  //       paddingLeft: '20px',
  //     };
  //   },
  //   indicatorSeparator: (styles) => ({ display: 'none' }),
  // };
  const signUpStepHandler = () => {
    setSignUpStep(currentStep + 1);
  };

  return (
    <div className={styles.firstStep}>
      {/* <div className={styles.firstStep__countrySelect}>
        <label
          className={styles.firstStep__countrySelect__label}
          htmlFor="chooseCountry"
        >
          Country/Region
        </label>
        <Select
          defaultValue={signUpData.country}
          options={options}
          hideSelectedOptions={false}
          className={styles.firstStep__countrySelect__select}
          onChange={signUpCountryHandler}
          styles={customStyles}
          isSearchable={false}
          id="chooseCountry"
        />
      </div> */}
      <div className={styles.firstStep__fullName}>
        <div className={styles.firstStep__fullName__inputField}>
          <label
            className={styles.firstStep__inputField__label}
            htmlFor="firstName"
          >
            First Name
          </label>
          <div className={styles.firstStep__inputField__input}>
            <input
              type="text"
              name="firstName"
              value={signUpData.firstName}
              placeholder=""
              onChange={signUpFirstAndLastNameHandler}
              className={
                signUpData.firstNameValidationFailed ? styles.error : ''
              }
              id="firstName"
            />
            <div
              className={`${styles.firstStep__inputField__error} ${
                signUpData.firstNameValidationFailed && styles.show
              }`}
            >
              {signUpData.firstNameValidationFailed &&
                signUpData.firstNameErrors[0].message}
            </div>
          </div>
        </div>
        <div className={styles.firstStep__fullName__inputField}>
          <label
            className={styles.firstStep__inputField__label}
            htmlFor="lastName"
          >
            Last Name
          </label>
          <div className={styles.firstStep__inputField__input}>
            <input
              type="text"
              name="lastName"
              value={signUpData.lastName}
              placeholder=""
              onChange={signUpFirstAndLastNameHandler}
              className={
                signUpData.lastNameValidationFailed ? styles.error : ''
              }
              id="lastName"
            />
            <div
              className={`${styles.firstStep__inputField__error} ${
                signUpData.lastNameValidationFailed && styles.show
              }`}
            >
              {signUpData.lastNameValidationFailed &&
                signUpData.lastNameErrors[0].message}
            </div>
          </div>
        </div>
      </div>

      <div className={styles.firstStep__inputField}>
        <label className={styles.firstStep__inputField__label} htmlFor="email">
          Email
        </label>
        <div className={styles.firstStep__inputField__input}>
          <input
            type="text"
            name="email"
            value={signUpData.email}
            placeholder="company@gmail.com"
            onChange={signUpEmailHandler}
            className={signUpData.emailValidationFailed ? styles.error : ''}
            id="email"
          />
          <div
            className={`${styles.firstStep__inputField__error} ${
              signUpData.emailValidationFailed && styles.show
            }`}
          >
            {signUpData.emailValidationFailed &&
              signUpData.emailErrors[0].message}
          </div>
        </div>
      </div>

      {/* password */}

      <div className={styles.firstStep__inputField}>
        <label className={styles.firstStep__inputField__label} htmlFor="email">
          Enter Password
        </label>
        <div className={styles.firstStep__inputField__input}>
          <input
            type={showPassword ? 'text' : 'password'}
            name="password"
            value={password}
            placeholder="Enter password"
            onChange={setPassword}
            className={
              (password.length > 0 &&
                signUpData.passwordValidationFailed.status) ||
              (confirmedPassword.length > 0 && !isPasswordsMatches)
                ? styles.error
                : ''
            }
          />
          {password.length > 0 && (
            <div
              className={
                showPassword
                  ? styles.firstStep__showPassword
                  : styles.firstStep__hidePassword
              }
              onClick={setShowPassword}
            ></div>
          )}
          <div
            className={`${styles.firstStep__inputField__error} ${
              password.length > 0 &&
              signUpData.passwordValidationFailed.status &&
              styles.show
            }`}
          >
            {signUpData.passwordValidationFailed.status &&
              signUpData.passwordValidationFailed.message}
          </div>
        </div>
        <div className={styles.firstStep__checkingPrompt}>
          Make sure its{' '}
          <span
            className={
              signUpData.passwordErrors.hasMin15Char.status
                ? styles.firstStep__validation__success
                : signUpData.passwordErrors.hasMin8Char.status
                ? ''
                : styles.firstStep__validation__error
            }
          >
            at least 15 characters
          </span>{' '}
          OR{' '}
          <span
            className={
              signUpData.passwordErrors.hasMin8Char.status
                ? styles.firstStep__validation__success
                : styles.firstStep__validation__error
            }
          >
            at least 8 characters
          </span>{' '}
          <span
            className={
              signUpData.passwordErrors.hasNumber.status
                ? styles.firstStep__validation__success
                : styles.firstStep__validation__error
            }
          >
            including a number
          </span>{' '}
          <span
            className={
              signUpData.passwordErrors.hasLowerCaseLetter.status
                ? styles.firstStep__validation__success
                : styles.firstStep__validation__error
            }
          >
            and a lowercase letter
          </span>
          . <Link to="/learn-more">Learn more</Link>
        </div>
      </div>

      <div className={styles.firstStep__inputField}>
        <label className={styles.firstStep__inputField__label} htmlFor="email">
          Re-enter Password
        </label>
        <div className={styles.firstStep__inputField__input}>
          <input
            type={showConfirmedPassword ? 'text' : 'password'}
            name="confirmedPassword"
            value={confirmedPassword}
            placeholder="Re-enter password"
            onChange={setConfirmedPassword}
            className={
              confirmedPassword.length > 0 && confirmedPassword !== password
                ? styles.error
                : ''
            }
          />
          <div>
            {confirmedPassword.length > 0 && (
              <div
                className={
                  showConfirmedPassword
                    ? styles.firstStep__showPassword
                    : styles.firstStep__hidePassword
                }
                onClick={setShowConfirmedPassword}
              ></div>
            )}
          </div>
          <div
            className={`${styles.firstStep__confirmedPasswordError} ${
              confirmedPassword.length > 0 &&
              password !== confirmedPassword &&
              styles.show
            }`}
          >
            Passwords do not match
          </div>

          <div
            className={`${styles.firstStep__confirmedPasswordSuccess} ${
              showSuccessHint && styles.show
            }`}
          >
            Passwords match
          </div>
        </div>
      </div>

      <div className={styles.firstStep__firstStepBtn}>
        <Button
          text={loader ? 'Loading' : 'Create Skillvox Account'}
          addClass="blueBG"
          disabled={
            signUpData.isSignUpFirstStepBtnDisabled || !isPasswordsMatches
          }
          clickHandler={signUpStepHandler}
        />
      </div>
      <div className={styles.firstStep__agreementText}>
        I've read and agreed to the Skillvox{' '}
        <Link to="/user-agreement">User Agreement</Link> and{' '}
        <Link to="/privacy-policy">Privacy Policy</Link>.
      </div>
    </div>
  );
};

export default FirstStep;
