import React from 'react';
import styles from './styles.module.scss';
import SingleCategory from './SingleCategorie';

const Categories = ({ categories, onClick }) => {
  return (
    <ul className={`${styles.categories}`}>
      {categories &&
        categories.map((category, idx) => {
          const {
            community_title,
            description,
            community_id,
            basic_community_icon,
            basic_community_icon_hover,
          } = category;
          return (
            <SingleCategory
              key={idx}
              categoryId={community_id}
              title={community_title}
              image={basic_community_icon}
              hoverImage={basic_community_icon_hover}
              description={description}
              categoriesCount={categories.length}
              clickHandler={onClick}
            />
          );
        })}
    </ul>
  );
};

export default Categories;
