import styles from './styles.module.scss';
import selectors from '../../../redux/selectors';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import BucketImg from '../../../assets/images/bucket.png';

const Bucket = ({ inBucketProductsCount }) => {
  return (
    <Link to={inBucketProductsCount ? '/checkout' : '#'}>
      <div className={styles['bucket']}>
        <div className={styles['bucket__image']}>
          <img src={BucketImg} alt="bucket" />
          {inBucketProductsCount > 0 && (
            <div className={styles['bucket__counter']}>
              {inBucketProductsCount}
            </div>
          )}
        </div>
      </div>
    </Link>
  );
};

export default connect(selectors, null)(Bucket);
