import {
  getTokenData,
  saveToken,
  deleteUser,
  deleteToken,
} from '../localStorage';

export async function fetchWithAuth(url, options) {
  const loginUrl = '/login'; // url   
  let tokenData = getTokenData() || null; //    tokenData

  if (!tokenData) {
    deleteUser();
    deleteToken();
    return window.location.replace(loginUrl);
  }

  if (!options.headers) {
    options.headers = {};
  }
  if (Date.now() >= tokenData.expiresIn) {
    try {
      const graphqlQuery = {
        query: `
        {
            getAccessToken()
        }
      `,
      };
      const response = await fetch('https://voxdigital.by:4000/query', {
        method: 'POST',
        headers: {
          Authorization: tokenData.accessToken
            ? `Bearer ${tokenData.accessToken}`
            : '',
          'Content-Type': 'application/json',
        },
        credentials: 'include',
        body: JSON.stringify(graphqlQuery),
      });

      const responseData = await response.json();

      const newAccessToken = responseData.data.getAccessToken;

      const currentDate = new Date();
      const expiresIn = new Date(currentDate.getTime() + 35000).getTime();
      saveToken(newAccessToken, expiresIn);
      options.headers.Authorization = `Bearer ${newAccessToken}`;
    } catch (err) {
      deleteUser();
      deleteToken();
      return window.location.replace(loginUrl);
    }
  } else {
    options.headers.Authorization = `Bearer ${tokenData.accessToken}`;
  }

  return fetch(url, options);
}
